// ----------------------------------------------------------------------------
// Collection Menu
// ----------------------------------------------------------------------------
//
// This is a section that displays collection items
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$carousel-arrow-background: transparent;
$carousel-arrow-border-radius: 0px !default;
$carousel-arrow-color: $primary-text-color;
$carousel-arrow-padding: 0;


// -------------------------------------
// Collection List - Section
// -------------------------------------
.page-section--category-driver {
    grid-column: main-start / full-end;
}

.collection-list__title {
    margin: 0 0 gap(1);
}

// -------------------------------------
// Collection List - Carousel
// -------------------------------------
.collection-list {
    position: relative;

    .carousel--collection-list {
        .swiper-wrapper {
            position: relative;
            padding: 0;
            margin: 0;
            list-style-type: none;

            @include bp(laptop) {
                padding: 0;
                margin: 0;
            }
        }
    }

    .swiper-navigation {
        display: none;

        @include bp(laptop) {
            display: flex;
            gap: 25px;
        }
    }

    .swiper-arrow {
        background: $carousel-arrow-background;
        border: 0;
        border-radius: $carousel-arrow-border-radius;
        box-shadow: 0;
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 19px;
        width: 19px;

        display: flex;
        align-items: center;
        justify-content: center;

        left: initial;
        right: initial;
        position: initial;
        display: block;
        margin: 0;

        .icon {
            position: relative;
            left: 1px;
            font-size: 20px;
        }

        &:after {
            display: none;
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;

            .icon {
                position: relative;
                left: 1px;
            }
        }

        &.swiper-button-lock {
            display: none;
        }
    }



    .swiper-scrollbar {
        background: $grey;

        .swiper-scrollbar-drag {
            background-color: $black;
        }
    }

    .swiper-controllers {
        padding-top: 30px;

        @include bp(laptop) {
            padding-top: 50px;
        }
    }

    .swiper-scrollbar.swiper-scrollbar-horizontal {
        height: 2px;
        left: 0;
        width: calc(100% - 15px);

        @include bp(laptop) {
            left: 0;
            width: calc(100% - 50px);
        }

        @include bp(desktopXL) {
            width: 100%;
            left: 0;
        }
    }
}

.collection-list__section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: gap(1);
    margin-right: $grid-gutter--mobile;

    @include bp(laptop) {
        margin-bottom: gap(3);
        margin-right: $grid-gutter;
    }

    @include bp(desktopXL) {
        width: 100%;
    }
}

.carousel--collection-list {
    // margin-left: $grid-gutter--mobile;

    // @include bp(laptop) {
    //     margin-left: $grid-gutter;
    // }

    // @include bp(desktopXL) {
    //     margin: 0;
    // }

    .card__title {
        @include font-size(16);
        font-weight: 400;
    }

    .card__description {
        color: $primary-text-color;
    }

    .card__button {
        display: none;
    }
}


