// ----------------------------------------------------------------------------
// Featured Product
// ----------------------------------------------------------------------------
//
// A section that displays a featured product image, price, description and
// link to the product page.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';


// -------------------------------------
// Default Styles
// -------------------------------------
.featured-product {
    position: relative;

    .featured-product__content {
        padding: 0 gap(1.5);
        text-align: center;
    }

    .card__tag {
        @include font-tags;
        padding: 0 gap(1);
        border-radius: 17px;
        position: absolute;
        top: -12px;
        left: 10px;
        z-index: 3;
        color: $white;
        background-color: $secondary-color;
        transition: transform 400ms ease;
    }


    .featured-product__image {
        display: flex;
        margin-bottom: gap(2);

        .background-image {
            object-fit: contain;
        }

        .background-image--2 {
            display: none;

            @include bp(tablet) {
                display: block;
            }
        }
    }

    .featured-product__title {
        margin: 0 0 5px;
        border-bottom: 0;
    }

    .price__wrapper {
        justify-content: center;
    }

    .button__featured-product {
        margin-top: gap(2);
    }

    .product__price {
        font-weight: 300;
        @include font-size(16);
        margin:  0;

        @include bp(laptop) {
            @include font-size(16);
        }
    }
}
