// ----------------------------------------------------------------------------
// Press Block - Custom Styles
// ----------------------------------------------------------------------------
//
// A section with a Header and a repeating logo item (image, title and content, link).
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------


// ------------------------------
// Press Block -- Default Styles
// ------------------------------

// Styles for the logo card grid
.press-block {
    margin-top: gap(4);

    @include bp(laptop) {
        margin-top: gap(5);
    }
}

.flex-grid__press-block {
    display: flex;
    flex-flow: row wrap;
    list-style-type: none;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0;

    a {
        text-decoration: none;
        cursor: pointer;
    }

    @include bp(tablet) {
        justify-content: center;
    }

    @include bp(desktop) {
        justify-content: center;

        &.flex-grid__press-block--full {
            justify-content: space-between;

            .flex-grid__item {
                margin: 0;
            }
        }
    }

    .flex-grid__item {
        flex: 0 1 50%;
        padding: 0 10px 30px;

         @include bp(tablet) {
            flex: 0 1 33%;
            padding: 20px 20px 30px;
        }

        @include bp(laptop) {
            flex: 0 1 20%;
        }

        .press-block__logo {
            text-align: center;
            width: 100%;
            height: 100%;
        }

        .logo-block__image-wrapper {
            margin: 0 auto;
            max-width: 125px;
            height: auto;
        }

        .press-block__quote {
            @include font-primary;
            text-align: center;
            margin-top: gap(2);
        }
    }
}

.logo-block__logo {
    a:focus-visible & {
        outline: $focus-state;
    }
}

// ---------------------------------
// logo Block -- Page Section Styles
// ---------------------------------

.press-block__title {
    margin-top: 0;
    text-align: center;
}

