// ----------------------------------------------------------------------------
// Rich Text
// ----------------------------------------------------------------------------
//
// A section with a rich text editor and section title
//
// ----------------------------------------------------------------------------



// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// ----------------------------------------
// Rich Text Editor Styles
// ----------------------------------------
.rte {
    margin: 0;

    img {
        height: auto;
    }

    table {
        table-layout: fixed;
    }

    ul,
    ol {
        margin: 0 0 gap(2);
    }

    strong {
        font-weight: bold;
    }

    h2,
    h3,
    h4 {
        margin: 40px 0 24px;
    }
}

// Allow table to scroll for tables in the RTE since we don't know
// how many columns they will contain. Class added by JS.
.rte-table {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

// This class is wrapped around YouTube/Vimeo embeds in the RTE
// to make them responsive and prevent layout breaking
.rte__video-wrapper {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    height: 0;
    height: auto;
    padding-bottom: 56.25%;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// This class is wrapped around tables in the RTE
// to prevent layout breaking with a scrollable parent
.rte__table-wrapper {
    overflow: auto;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
}

.page-section--rich-text {
    
    @include bp(laptop) {
        display: grid;
        grid-template-columns: repeat(24, minmax(0, 1fr));
        padding: 0 $grid-gutter;
    }

    .container--wysiwyg {
        padding: 0 $grid-gutter--mobile;

        @include bp(laptop) {
            grid-column: 1 / span 17;
            padding: 0;
            width: 100%;
        }
    }

    .rich-text__wrapper {

        .page-section__title {
            margin-top: 0;
        }
    }

    .container--wysiwyg--center {

        @include bp(laptop) {
            grid-column: 5 / span 16;
        }

        .rich-text__wrapper {
            text-align: center;
            margin: 0 auto;
        }
    }
}
