// ----------------------------------------------------------------------------
// Accordion
// ----------------------------------------------------------------------------
//
// Collapsible accordion block used to reveal extra product information when the
// user clicks an item's header or expand button.
//
// Related JS file: accordion/accordion.js
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$accordion-transition:                $anim !default;
$accordion-active-max-height:         5000px !default;
$accordion-title-color:               $primary-text-color !default;
$accordion-item-margin-bottom:        gap(1.5) !default;
$accordion-box-shadow:                0 !default;
$accordion-question-background-color: transparent !default;
$accordion-question-mobile-padding:   gap(1.5) 0 !default;
$accordion-question-padding:          gap(2.5) 0 !default;
$accordion-answer-background-color:   transparent !default;
$accordion-content-margin:           0 !default;
$accordion-icon-color:                $white !default;
$accordion-active-content-margin:    0 0 gap(2.5) !default;


// -------------------------------------
// accordion
// -------------------------------------
.accordion {
    width: 100%;
    border-top: $border--light;
}

.accordion__item {
    border-bottom: $border--light;

    &.is-open {
        box-shadow: $accordion-box-shadow;
    }

    @include bp(laptop) {
        &:hover {
            border-color: $secondary-color;

            .accordion__button {
                color: $secondary-color;
            }
        }
    }
}

.accordion__title {
    margin: 0;

    .accordion__button {
        @include font-header;
        font-size: 1em;
        background-color: $accordion-question-background-color;
        box-shadow: $accordion-box-shadow;
        color: $accordion-title-color;
        cursor: pointer;
        overflow: hidden;
        position: relative;
        text-align: left;
        text-transform: none;
        transition: $accordion-transition;
        padding: $accordion-question-mobile-padding;
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        white-space: normal;

        &.is-open {
            box-shadow: none;

            .accordion__icon--close {
                display: block;
            }

            .accordion__icon--open {
                display: none;
            }
        }
    }




    @include bp(laptop) {
        .accordion__button {
            padding: $accordion-question-padding;
        }
    }
}

.accordion__icon {
    display: inline-block;
    pointer-events: none;
    transition: $accordion-transition;
    margin-left: gap(2);

    .icon {
        width: 27px;
        height: 27px;
    }

    .accordion__icon--close {
        display: none;

    }

    .accordion__icon--open {
        display: block;
    }
}

.accordion__content {
    @include clearfix;
    background-color: $accordion-answer-background-color;
    height: 0;
    display: block;
    overflow: hidden;
    margin: $accordion-content-margin;
    text-align: left;
    transition: $accordion-transition;

    &.is-open {
        margin: $accordion-active-content-margin;
        transition: $accordion-transition;
    }

    p:last-child {
        margin-bottom: 0;
    }
}


