// ----------------------------------------------------------------------------
// Card Label
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$card-tag-background-color: $primary-color;

.card__tags-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: gap(1);
}

.card__tag {
    @include font-tags;
    @include line-height(20);
    text-align: center;
    display: inline-block;
    padding: gap(.2) gap(.5);
    border-radius: 0;
    color: $white;
    background-color: $card-tag-background-color;
    transition: transform 400ms ease;
}
