// -------------------------------------------
// Country Switcher
// Dropdown toggle navigation that switches
// between countries sites
// -------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';

.localization-forms {
    display: flex;
    flex-direction: column;
    gap: 0;

    @include bp(tablet) {
        flex-direction: row;
        gap: gap(2);
    }
}

.disclosure {
    position: relative;

    .disclosure__button {
        background: transparent;
        padding: 0;
        color: $primary-text-color;
        @include font-default;
        @include font-size(14);
        @include line-height(22);
        display: flex;
        align-items: center;
        margin-bottom: gap(1.5);
        text-transform: capitalize;
        letter-spacing: 0;

        @include bp(tablet) {
            margin-bottom: 0;
        }

        .country-switcher__flag {
            margin-right: 8px;


            @include bp(laptop) {
                display: none;
            }
        }
    }

    .country-toggle__icon {
        vertical-align: middle;
    }

    .country-switcher__flag {
        width: 24px;
        height: 18px;
        vertical-align: middle;
        filter: grayscale(1);
    }

    .disclosure__list {
        background-color: $background-color;
        padding: 0;
        list-style: none;
        text-align: left;
        margin-left: gap(1.5);
        margin-bottom: gap(1.5);

        @include bp(laptop) {
            border: 1px solid #E7E7E7;
            border-top: 0;
            margin: 0;
            left: -18px;
            margin-bottom: 0;
            position: absolute;
            padding: gap(1.8);
        }
    }

    .country__list {
        min-width: 248px;
    }

    .language__list {
        min-width: 125px;
    }

    .disclosure__item {
        margin-bottom: gap(1);
        display: flex;
        align-items: center;
        gap: 8px;
        letter-spacing: 0;

        @include bp(laptop) {
            margin-bottom: 0;
        }

        a {
            color: black;
            text-decoration: none;
            @include font-size(14);
            @include line-height(28);
            font-weight: 400;

            &:hover {
                text-decoration: underline;
            }
        }
    }

}

.header {
    .country-switcher__name {
        margin-right: 5px;

        @include bp(laptop) {
            display: none;
        }
    }

    .country-switcher__list {

        @include bp(laptop) {
            left: -80px;
        }
    }

    .country-switcher__bracket {
        display: none;
    }

    .disclosure {
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: 0;
            padding: 10px 10px;
        }
    }

    .disclosure__list {
        @include bp(laptop) {
            top: 100%;
        }
    }

    .disclosure__button {
        color: var(--header-text-color);
        transition: none;
    }
}

.footer {

    .localization-forms {
        width: 100%;

        .disclosure__button {
            margin-bottom: 8px;
            @include bp(laptop) {
                margin-bottom: 0;
            }
        }

        .disclosure__button[aria-expanded="true"] .country-toggle__icon {
            transform: rotate(180deg);
        }

        .disclosure__list {
            padding: 0 15px;
            margin: 0;
            bottom: 40px;
            left: -11px;

            @include bp(tablet) {
                padding: gap(1.5);
            }

            li {
                display: flex;
            }

            a {
                text-decoration: none;

                @include bp(laptop) {
                    color: $black;
                }
            }

            &::before {
                position: relative;

                @include bp(laptop) {
                    content: "";
                    position: absolute;
                    top: 100%;
                    left: 10%;
                    margin-left: -10px;
                    border-width: 10px;
                    border-style: solid;
                    border-color: $background-color transparent transparent transparent;
                }
            }
        }

        .country-switcher__flag {
            display: block;
        }



        .country-switcher__bracket.left-bracket {
            margin-left: 4px;
        }
    }
}
