// -------------------------------------
// Dependencies
// -------------------------------------
@import '../../assets/styles/init/init';


// -------------------------------------
// Variables
// -------------------------------------
$video-block-background-color: $background-color !default;
$video-block-min-height: 600px;
//$video-block-text-shadow: $text-shadow !default;
$video-block-control-color: $white !default;
$video-block-control-poster-icon-size: 80px !default;
$video-block-control-poster-icon-size-desktop: 120px !default;
$video-block-control-hover-scale-ratio: 1 !default;


// -------------------------------------
// Video Block
// -------------------------------------
.video-block {
    @include aspect-ratio(16, 9);
    background-position: 50% 50%;
    background-size: cover;
    position: relative;
    width: 100%;
}

.video-block__video {
    height: 100%;
    object-fit: cover;
    object-position: center center;
    vertical-align: bottom;
    width: 100%;
    position: absolute;

    iframe {
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
}


// Push Video / Controls behind the Poster
.video-block--is-postered {
    .video-block__video,
    .video-block__video iframe {
        z-index: -1;
    }
}

.video-block--is-postered {
    video::-webkit-media-controls {
        position: relative;
        z-index: -1;
    }
}


// Push the non-interactive video behind the "invisible cover"
.video-block--is-not-interactive {
    .video-block__video,
    .video-block__video iframe,
    video::-webkit-media-controls {
        z-index: -1;
    }
}



// Add a transparent layer over non-interactive videos (to block clicks)
.video-block--is-not-interactive {
    background-color: rgba($black, 0);
}


// -------------------------------------
// Video Block Controls
// -------------------------------------
.video-block__control {
    cursor: pointer;
    display: inline-block;
    color: $video-block-control-color;

    svg.icon {
        transition: $anim;
    }

    &:hover svg.icon {
        transform: scale($video-block-control-hover-scale-ratio);
        transition: $anim;
    }

    &:focus-visible {
        outline: $primary-color solid gap(.2);
        outline-offset: gap(.5);

        svg {
            //outline: $primary-color solid gap(.2);
            //outline-offset: gap(.5);
        }
    }
}

.video-block__controls-bar {
    position: absolute;
    bottom: $grid-gutter--mobile;
    right: $grid-gutter--mobile;
    //text-shadow: $video-block-text-shadow;
    z-index: $z-index--base-controls;

    .video-block__control {
        margin-left: 1em;
    }

    @include bp(laptop) {
        bottom: $grid-gutter;
        right: $grid-gutter;
    }


    // Move the control bar to the left
    &.video-block__controls-bar--left {
        right: auto;
        left: 20px;

        .video-block__control {
            margin-left: 0;
            margin-right: 1em;
        }

        @include bp(laptop) {
            right: auto;
            left: $grid-gutter;
        }
    }
}

.video-block__controls-poster {
    display: none; // Hidden unless the poster is visible

    .video-block__control {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        border-radius: 0;
        padding: 0;
    }

    svg.icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: $video-block-control-poster-icon-size;
        width: $video-block-control-poster-icon-size;

        @include bp(laptop) {
            height: $video-block-control-poster-icon-size-desktop;
            width: $video-block-control-poster-icon-size-desktop;
        }
    }

    &:hover svg.icon {
        transform: translate(-50%, -50%);

        width: $video-block-control-poster-icon-size * $video-block-control-hover-scale-ratio;
        height: $video-block-control-poster-icon-size * $video-block-control-hover-scale-ratio;

        @include bp(laptop) {
            width: $video-block-control-poster-icon-size-desktop * $video-block-control-hover-scale-ratio;
            height: $video-block-control-poster-icon-size-desktop * $video-block-control-hover-scale-ratio;
        }
    }

    // Display poster controls when poster is visible
    .video-block--is-postered & {
        display: block;
    }
}

.video-block--is-postered,
.video-block--is-paused {
    background-repeat: no-repeat;
    // Important needed to override inline styles
    background-size: cover !important;

    .video-block__video {
        z-index: -1;
        pointer-events: none;
    }

    .video-block__controls-poster {
        display: block;
    }
}

.video-block__control--pause {
    display: inline-block;

    .video-block--is-paused & {
        display: none;
    }
}

.video-block__control--play {
    display: none;

    .video-block--is-paused & {
        display: inline-block;
    }
}

.video-block__control--unmute {
    display: none;

    .video-block--is-muted & {
        display: inline-block;
    }
}

.video-block__control--mute {
    display: inline-block;

    .video-block--is-muted & {
        display: none;
    }
}
