// ----------------------------------------------------------------------------
// CSS Grid
// ----------------------------------------------------------------------------
//
// Container component that allows for items to be wrapped in a CSS grid.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$css-grid-gutter--mobile: $grid-gap;
$css-grid-gutter: $grid-gap;
$css-grid-columns-large: 6 !default;
$css-grid-columns-medium: 4 !default;
$css-grid-columns-small: 2 !default;


// -------------------------------------
// Base grid
// -------------------------------------
.css-grid {
    display: block;
    width: 100%;

    @supports (display: grid) {
        display: grid;
        grid-gap: $css-grid-gutter--mobile;

        @include bp(laptop) {
            grid-gap: $css-grid-gutter;
        }
    }

    &:is(ul) {
        padding: 0;
    }
}

.css-grid__item {
    display: inline-block;
    vertical-align: top;
    margin: 0 calc(#{$css-grid-gutter--mobile} / 2) $css-grid-gutter--mobile;

    @include bp(tablet) {
        margin: 0 calc(#{$css-grid-gutter} / 2) $css-grid-gutter;
    }

    @supports (display: grid) {
        margin: 0;
    }
}


// Support .css-grid--[small,medium,large]-[1-12]
@for $columns from 1 through $css-grid-columns-small {
    .css-grid--small-#{$columns} {
        grid-template-columns: repeat($columns, 1fr);
    }
}

@include bp(tablet) {
    @for $columns from 1 through $css-grid-columns-medium {
        .css-grid--medium-#{$columns} {
            grid-template-columns: repeat($columns, 1fr);

            .css-grid__item {
                width: calc(#{percentage(math.div(1, $columns))} - (#{$css-grid-gutter} * 2));

                @supports (display: grid) {
                    width: 100%;
                }
            }
        }
    }
}

@include bp(laptop) {
    @for $columns from 1 through $css-grid-columns-large {
        .css-grid--large-#{$columns} {
            grid-template-columns: repeat($columns, 1fr);

            .css-grid__item {
                width: calc(#{percentage(math.div(1, $columns))} - (#{$css-grid-gutter} * 2));

                @supports (display: grid) {
                    width: 100%;
                }
            }
        }
    }
}


// No Gutter
.css-grid--no-gutter {
    grid-gap: 0;
}


// -------------------------------------
// Grid overrides for blog posts
// -------------------------------------
.blog__posts,
.css-grid--blog-post {
    grid-row-gap: 50px;

    @include bp(tablet) {
        grid-row-gap: 60px;
    }
}
