// ----------------------------------------------------------------------------
// Layout & Site Grid
// ----------------------------------------------------------------------------
//
// Global grid modifications and base container maximums.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

// One Column Layout
$page-grid--mobile:     '[max-start] minmax(0, 1fr) [full-start] #{$grid-gutter--mobile} [main-start] minmax(0, #{$max-width}) [main-end] #{$grid-gutter--mobile} [full-end] minmax(0, 1fr) [max-end]';
$page-grid:             '[max-start] minmax(0, 1fr)[full-start] #{$grid-gutter} [main-start] minmax(0, 1820px) [main-end] #{$grid-gutter} [full-end] minmax(0, 1fr) [max-end]';

// Article Grid
$article-grid:          '[full-article-start] 12.5% [main-article-start] 75% [main-article-end] 12.5% [full-article-end]';

// Two Column Layout
$page-grid--two-column: '[full-start] minmax(#{$grid-gutter}, 1fr) [main-start] minmax(0, #{$max-width__content}) [main-end] #{$grid-gutter} [aside-start] #{$max-width__aside} [aside-end] minmax(#{$grid-gutter}, 1fr) [full-end]';


// -------------------------------------
// Base Layout declarations
// -------------------------------------

@-ms-viewport{
    width: device-width;
}

html {
  background-color: $color-body;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}


// body,
// html {
//   height: 100%;
// }

// Sticky Footer
body {
    background-color: $background-color--base;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
    overflow-x: hidden;

    .main {
        flex: 1 1 auto;
        width: 100%;
    }

    // No scroll body
    &.noscroll {
        overflow: hidden;
    }
}

figure {
  margin: 0;
}

img {
  margin: 0;
  max-width: 100%;
  height: auto;
  display: block;
}


// -------------------------------------
// Containers
// -------------------------------------

// Base Container
// Even if we don't have a grid in play, we hold content to a max width.
.container {
    @include container;
}

.container--wysiwyg {
    @include container--wysiwyg;
}


// -------------------------------------
// Grid Layouts (One & Two Column)
// -------------------------------------
// Single Column

.layout--one-column {
    display: block;
    max-width: 100%; // for IE 11
    width: 100%; // for IE 11

    @supports (display:grid) {
        display: grid;
        grid-template-columns: #{$page-grid--mobile};
        max-width: initial;
        width: 100%;
    }

    @include bp(laptop) {
        grid-template-columns: #{$page-grid};
    }

    // All content defaults to one column
    > * {
        grid-column: main;
    }
}


// Two Columns
.layout--two-column {
    display: block;
    max-width: 100%; // for IE 11
    width: 100%; // for IE 11

    @supports (display: grid) {
        display: grid;
        grid-template-columns: #{$page-grid--two-column};
        max-width: initial;
        width: 100%;
    }

    // All content defaults to one column
    > * {
        grid-column: main / aside;
    }

    // Column one, the content column
    .container--content {
        // Legacy Grid
        display: inline-block;
        width: 100%;

        @include bp(laptop) {
            max-width: $max-width__content--legacy;
        }

        @include bp(desktop) {
            max-width: $max-width__content;
        }

        // CSS Grid
        @include bp(laptop) {
            grid-column: main;

            @supports (display: grid) {
                max-width: initial;
                width: auto;
            }
        }
    }

    // Column two, the aside column
    .container--aside,
    .aside {
        // Legacy Grid
        display: inline-block;
        width: 100%;

        @include bp(laptop) {
            float: right;
            max-width: $max-width__aside--legacy;
        }

        @include bp(desktop) {
            max-width: $max-width__aside;
        }

        // CSS Grid
        @include bp(laptop) {
            grid-column: aside;

            @supports (display: grid) {
                max-width: initial;
                width: auto;
            }
        }
    }

    // Full-bleed content stretches to the edge
    .page-builder__section--full-bleed,
    .container--full-bleed {
        grid-column: full;
    }
}

// Full-bleed content stretches to the edge
.page-section--full-bleed,
.container--full-bleed {
    grid-column: full;
}

.page-section {
    margin: gap(4) 0;

    @include bp(tablet) {
        margin: gap(6) 0;
    }
}

.page-section--gap-bottom {
    margin: 0 0 gap(3);

    @include bp(tablet) {
        margin: 0 0 gap(6);
    }
}

.page-section--no-gap {
    margin: 0;
}

.page-section--wysiwyg {
    .container {
        width: 100%;

        @include bp(laptop) {
            @include container--wysiwyg;
        }
    }
}

.page-secion--main-collection {
    .css-grid--large-4 .css-grid__item {
        width: 100%;
    }
}

.page-section--collection-header + .page-section--main-collection {
    margin-top: gap(3);
}
