// ----------------------------------------------------------------------------
// Page Header
// ----------------------------------------------------------------------------
//
// Primary header for the site. Contains title and description, with image
// variant.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$page-header-image-height           : 300px;
$page-header-image-height--desktop  : 400px;
$page-header-header-height-spacing  : 80px;

$page-header-content-width: 600px;
$page-header-padding: 50px;
$page-header-mobile-padding: 15px;
$page-header-height: 430px;
$page-header-mobile-height: 374px;


// -------------------------------------
// Page Header -- Default
// -------------------------------------
// generic page header styles with no background image
.page-header {
    grid-template-columns: [gutter-start] $page-header-mobile-padding [content-start] minmax(0, $page-header-content-width) [center] 1fr [content-end] minmax(0, 600px) $page-header-mobile-padding [gutter-end];
    grid-template-rows: [image-start] $page-header-mobile-padding minmax($page-header-height, 1fr) $page-header-mobile-padding [image-end];

    @include bp(tablet) {
        grid-template-columns: [gutter-start] $page-header-padding [content-start] minmax(0, 710px) [center] auto [content-end] minmax(0, auto) $page-header-padding [gutter-end];
        grid-template-rows: [image-start] $page-header-padding minmax($page-header-height, 1fr) $page-header-padding [image-end];
        margin-bottom: gap(6);
    }

    .page-header__content {
        @include font-primary;
        padding: $grid-gutter 0;

        @include bp(laptop) {
            padding: ($grid-gutter * 2) 0;
        }
    }

    .page-header__title {
        margin: 0;
        margin-bottom: 5px;

        @include bp(laptop) {
            margin-bottom: 10px;
        }
    }

    .page-header__text {
        text-align: left;
        margin-left: 0;
    }

    .page-header__button {
        margin-top: 20px;

        @include bp(laptop) {
            margin-top: 30px;
        }
    }
}

.page-header--left {
    .page-header__title {

        @include bp(laptop) {
            text-align: left;
        }
    }
}

.page-header--text {
    padding-top: $page-header-header-height-spacing;
    min-height: 0;
    display: grid;
    grid-template-columns: [gutter-start] $page-header-mobile-padding [main-start] 1fr [main-end] $page-header-mobile-padding [gutter-end];
    grid-template-rows: [image-start] 50px auto 0px [image-end];
    margin-bottom: gap(4);

    @include bp(laptop) {
        margin-bottom: gap(6);
    }
    .page-header__content {
        padding: 0;
        text-align: left;
        grid-column: 1 / -1;
        grid-row: 2;

        @include bp(laptop) {

            grid-column: 1 / span 16;
        }

        .page-header__text {
            text-align: left;
            @include bp(tablet) {
                max-width: 55%;
            }
        }
    }
}


// -------------------------------------
// Page Header -- Image
// -------------------------------------
.page-header--image {
    max-width: 1920px;
    margin: 0 auto;
    margin-bottom: 0;

    @include bp(tablet) {
        margin-bottom: gap(6);
    }

    .background-media--image {
        min-height: $page-header-image-height;
        grid-template-columns: [gutter-start] $page-header-mobile-padding [content-start] minmax(0, $page-header-content-width) [center] 1fr [content-end] minmax(0, 600px) $page-header-mobile-padding [gutter-end];
        grid-template-rows: [image-start] $page-header-mobile-padding $page-header-mobile-height $page-header-mobile-padding [image-end];

        @include bp(tablet) {
            grid-template-columns: [gutter-start] $page-header-padding [main-start] repeat(21, 1fr) [main-end] $page-header-padding [gutter-end];
            grid-template-rows: [image-start] $page-header-padding [content] auto $page-header-padding [image-end];
        }
    }

    .background-media__content {
        text-align: left;
        grid-column: 2 / 5;
        padding: 0;
        align-self: end;
        padding: gap(3) gap(0);

        @include bp(tablet) {
            padding: 0;
            grid-column: 2 / 10;
        }

        .page-header__button-collection {
            display: flex;
            flex-wrap: wrap;
            column-gap: 10px;

            .button {
                margin-top: gap(2);
            }
        }
    }

    .page-header__text {
        width: 100%;
        margin: 0;
        margin-top: 13px;

        @include bp(laptop) {
            margin-top: 20px;
        }
    }

    .page-header__button {
        margin: 0;
    }

    .page-header__title {
        margin: 0;
    }
}

// -------------------------------------
// Page Header -- Video
// -------------------------------------
.page-header--video {
    .video-block {
        height: $page-header-image-height;

        @include bp(laptop) {
            height: $page-header-image-height--desktop;
        }
    }

    .page-header__title {
        color: $white;
    }

    .background-media__content {
        text-align: center;
        align-self: end;
    }

    .page-header__text {
        @include font-size(18);
        @include line-height(28);
        font-weight: 600;
        color: $white;
        margin: 0 auto;
        text-align: center;
    }

    .video-block__control {
        color: $primary-color;
        background-color: $white;
        padding: 4px;
        position: absolute;
        top: gap(2);
        left: gap(2);
        z-index: 1;
        border-radius: 30px;
        align-items: center;
        justify-content: center;

        @include bp(laptop) {
            top: initial;
            left: initial;
            bottom: gap(2);
            right: gap(2);
        }

        &:focus-visible {
            outline: none;
            box-shadow: 0 0 0 5px rgba($primary-color, 0.5);
        }
    }

    .video-block__control--pause {
        @include font-size(32);
        display: flex;
    }

    .video-block__control--play {
        @include font-size(32);
        display: none;
    }

    .video-block--is-paused {
        & .video-block__control--play {
            display: flex;
        }

        & .video-block__control--pause {
            display: none;
        }
    }

    .page-header__button {
        &:hover {
            color: $white;
        }
    }
}

// -------------------------------------
// Page Header -- Collection
// -------------------------------------
.page-header--collection {
    border-bottom: $border--light;
    grid-template-columns: [gutter-start] $page-header-padding [main-start] repeat(21, 1fr) [main-end] $page-header-padding [gutter-end];
    grid-template-rows: minmax(100px, 1fr) auto;
    display: grid;
    max-width: 1920px;
    margin: 0 auto;

    @include bp(laptop) {
        grid-template-rows: minmax(200px, 1fr) auto;
    }

    .background-image {
        object-fit: cover;
        height: 100%;
        width: 100%;
    }

    .background-media__background {
        grid-column: 1 / 24;
        grid-row: 1;
        max-height: 500px;

        @include bp(laptop) {
            max-height: 600px;
        }
    }

    .page-header__content:only-child {
        padding-top: 100px;

        @include bp(laptop) {
            padding-top: 130.5px;
        }
    }

    .page-header__content {
        padding: gap(3) $page-header-mobile-padding;
        grid-column: 1 / span 24;

            @include bp(laptop) {
            padding: $page-header-padding 0;
            grid-template-columns: [gutter-start] $page-header-padding [main-start] repeat(21, 1fr) [main-end] $page-header-padding [gutter-end];
            display: grid;
        }

        .breadcrumbs {
            grid-column: 2/13;
            margin-bottom: gap(2.5);

            @include bp(laptop) {
                margin-bottom: gap(1);
            }
        }

        .page-header__title {
            grid-column: 2 / 13;
            align-self: center;

            .collection_count {
                font-size: 12px;
                position: relative;
                top: -9px;
                font-weight: 400;
                vertical-align: super;

                @include bp(laptop) {
                    top: -21px;
                }
            }
        }

        .page-header__text {
            grid-column: 14 / 23;
            align-self: center;
            padding-top: gap(1.5);

            @include bp(laptop) {
                padding: 0;
            }
        }
    }
}

// -------------------------------------
// Page Header -- Search
// -------------------------------------
.page-header--search {
    padding-top: $page-header-header-height-spacing;

    .page-header__title {
        margin-bottom: gap(3);

        @include bp(laptop) {
            margin-bottom: gap(6);
        }
    }

    .page-header__text {
        @include font-caption;
    }
}

.page-header--password {

    .page-header__content {
        margin-bottom: 0;
    }

    .password-page__image-wrapper {
        margin-bottom: gap(2);
    }

    .page-header__description {
        margin-top: gap(3);
    }
}
