// ----------------------------------------------------------------------------
// Hero Banner
// ----------------------------------------------------------------------------
//
// Jumbo banner typically used for the Homepage Hero. Showcases large image
// and title along a description and CTA.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$hero-banner-content-width: 600px;
$hero-banner-padding: 50px;
$hero-banner-mobile-padding: 15px;
$hero-banner-height: 430px;



// -------------------------------------
// Hero Banner -- Default
// -------------------------------------

.hero-banner {
    overflow: hidden;
    position: relative;
    grid-template-columns: [gutter-start] $hero-banner-mobile-padding [content-start] minmax(0, $hero-banner-content-width) [center] 1fr [content-end] minmax(0, 600px) $hero-banner-mobile-padding [gutter-end];
    grid-template-rows: [image-start] 50px minmax(430px, 1fr) 0px [image-end];

    @include bp(tablet) {
        grid-template-columns: [gutter-start] $hero-banner-padding [content-start] minmax(0, 710px) [center] auto [content-end] minmax(0, auto) $hero-banner-padding [gutter-end];
        grid-template-rows: [image-start] 50px minmax($hero-banner-height, 1fr) 50px [image-end];
        margin-top: -50px;
    }

    .background-media__content {
        grid-row: 5;
        grid-column: 2 / 5;
        align-self: end;
        text-align: left;
        width: 100%;
        justify-self: left;
        padding: 5px 0 30px;

        @include bp(tablet) {
            grid-row: 2;
            background: transparent;
            grid-column: 2 / 3;
            padding: 15px 0 30px;
            margin-bottom: 80px;
        }

        .container {
            width: auto;
        }

        .hero-banner__title {
            margin: 0;
            margin-bottom: 20px;
            color: $primary-text-color;
        }

        .hero-banner__description {
            margin: -20px 0 0 0;

            @include bp(tablet) {
                margin: -20px 0 20px 0;
            }
        }

        .hero-banner__button-collection {
            margin-top: 10px;
            display: flex;
            flex-wrap: wrap;
            gap: 10px;

            @include bp(tablet) {
                margin-top: 20px;
            }
        }
    }

    .video-block {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    .video-block__control {
        color: $primary-color;
        background-color: $white;
        padding: 4px;
        position: absolute;
        z-index: 1;
        border-radius: 30px;
        align-items: center;
        justify-content: center;
        top: auto;
        left: auto;
        bottom: gap(2);
        right: gap(2);

        &:focus-visible {
            outline: none;
            box-shadow: 0 0 0 5px rgba($primary-color, 0.5);
        }
    }

    .video-block__control--pause {
        @include font-size(32);
        display: flex;
    }

    .video-block__control--play {
        @include font-size(32);
        display: none;
    }

    .video-block--is-paused {
        & .video-block__control--play {
            display: flex;
        }

        & .video-block__control--pause {
            display: none;
        }
    }

    &.hero-banner--white {
        @include bp(tablet) {
            .hero-banner__title {
                color: $white;
            }
        }
    }
}

