// ----------------------------------------------------------------------------
// Cart
// ----------------------------------------------------------------------------
//
// A page template with a responsive cart product table.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Cart Template -- Default
// -------------------------------------
.page-section--cart {
    padding-top: 80px;

    form {
        width: 100%;
        margin: 0 auto;
    }

    .button-collection {
        margin-top: gap(4);
        justify-content: flex-end;
        align-items: center;

        & .form__button {
            margin-right: gap(3);
        }
    }

    .free-shipping-progress__message {
        background-color: $white;
    }
}

// -------------------------------------
// Cart Grid
// -------------------------------------
.cart__grid {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 50px;

    @include bp(laptop) {
        grid-template-columns: repeat(3, 1fr);
    }
}

.cart__main {
    @include bp(laptop) {
        grid-column: 1 / span 2;
    }

    .cart-subtotal__container,
    .button-collection--cart-page {
        @include bp(laptop) {
            display: none;
        }
    }
}

.cart__header {
    padding-bottom: gap(2);

    @include bp(laptop) {
        padding: 0;
    }
}

.cart__title {
    @include font-size(24);
    @include line-height(33);
    font-weight: 400;
    margin: 0 0 gap(2);
}

.cart__sidebar {
    @include bp(laptop) {
        grid-row: 1;
        grid-column: 3;
    }
}

.cart__footer {
    @include bp(laptop) {
        grid-column: 1 / 3;
    }

    .accordion {
        border: 0;
    }
}

// -------------------------------------
// Cart Header
// -------------------------------------
.cart__undo-message {
    opacity: 0;

    &.js--fade-in {
        transition: 2s opacity;
        opacity: 1;
    }

    p {
        @include font-size(16);
        padding: gap(.5) gap(1.5);
        margin: 0 0 gap(2);
        background-color: rgba($red, 0.2);
        color: $red;

        .cart__undo {
            color: $error-color;
            padding: 0;
            background: none;
            text-decoration: underline;

            &:hover {
                color: rgba($error-color, .7);
            }
        }
    }
}

.button__cart-header {
    width: 100%;
}


// -------------------------------------
// Cart Item
// -------------------------------------
.cart__row {
    border-top: $border--light;
    border-bottom: $border--light;
    padding: gap(2) 0;
}

.cart__line-item {
    display: grid;
    grid-template-columns: 100px auto;
    grid-template-rows: repeat(3, auto);
    grid-gap: $grid-gutter--mobile;

    @include bp(tablet) {
        grid-template-columns: 1fr 200px 1fr 1fr 1fr;
        grid-template-rows: auto;
        grid-gap: $grid-gutter;
    }

    .product__price {
        margin: 0;
    }

    .price__sale {
        display: block;
    }
}

.cart__header-item {
    margin-bottom: gap(.5);
}

.cart__item--image {
    grid-column: 1;
    grid-row: 1 /  span 3;

    @include bp(tablet) {
        grid-row: auto;
        grid-column: auto;
    }
}

.cart__item--image-link {
    &:focus-visible {
        img {
            outline: $focus-state;
        }
    }
}

.cart__product-title {
    @include line-height(23);
    display: block;
    font-weight: 400;
    text-decoration: none;
    color: $primary-text-color;
}

.cart__item--title {
    grid-row: 1;
    grid-column: 2;

    @include bp(tablet) {
        grid-row: auto;
        grid-column: auto;
    }

    .cart__product-varient {
        @include font-size(16);
    }
}

.cart__item--price {
    @include font-size(16);
    grid-row: 3;
    grid-column: 2;


    @include bp(tablet) {
        grid-row: auto;
        grid-column: auto;
    }

    .cart__header-item {
        display: none;

        @include bp(tablet) {
            display: block;
        }
    }
}

@keyframes gradient {
  0% { background-position: 0% 50% }
  99% { background-position: 100% 50% }
  100% { background-position: 0% 50% }
}

.product__price,
.cart-info__subtotal,
.cart__subtotal-price {
    position: relative;

    &:before {
        opacity: 0;
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: linear-gradient(to right, #e9e6e6 0%, #e9e6e6 25%, #dadada 51%, #e9e6e6 75%, #e9e6e6 100%);
        background-size: 300% 300%;
        z-index: 1;
        border-radius: 4px;
    }

    &.loading {
        &:before {
            animation: gradient 1s ease infinite;
            opacity: 1;
        }
    }
}

// Final Sale Tag - On both Cart Page, Cart Modal and Mini Cart
.line-item__final-sale {
    @include font-size(16);
    font-weight: 400;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: $border--light;
    margin-top: gap(1.5);
    padding: gap(0.3) gap(.6);

    span {
       text-transform: uppercase;
    }

    .tooltip {
        margin-left: gap(.5)
    }
}

.cart__item--quantity {
    @include font-size(16);
    grid-row: 2;
    grid-column: 2;


    @include bp(tablet) {
        grid-row: auto;
        grid-column: auto;
    }

    .form__input-container {
        max-width: 100px;
    }

    .cart__item--quantity__max-qty-message {
        @include font-size(14);
        color: $red;
    }
}

.cart__quantity {
    @include font-default;
    border-radius: 0;
    color: $primary-text-color;
    padding: gap(1) gap(.5) gap(1) gap(1.5);
    width: 80px;
    display: flex;
    align-items: center;

    @include bp(laptop) {
        display: block;
    }

    &:focus-visible {
        border-color: $border-color--light;
    }
}

.cart__item--message {
    p {
        @include font-size(16);
        padding: gap(.5) gap(1.5);
        margin: 0 0 gap(2);
        background-color: rgba($red, 0.2);
        color: $red;
    }

    .cart__remove {
        color: $error-color;
        margin-top: 10px;

        &:hover {
            color: rgba($error-color, .7);
        }
    }
}

.cart__item--total {
    @include font-size(16);
    grid-row: 4;
    grid-column: 2;

    @include bp(tablet) {
        grid-row: 1;
        grid-column: 5;
        text-align: right;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        justify-content: space-between;
    }
}

.cart__item--total-wrapper {
    display: none;

    @include bp(tablet) {
        display: block;
        width: 100%;
    }

}

.cart__remove {
    @include font-size(14);
    color: $primary-color;

    &:hover {
        color: rgba($primary-color, .7);
    }
}

.cart__total-wrapper {
    display: none;

    @include bp(tablet) {
        display: block
    }
}


// -------------------------------------
// Cart Summary
// -------------------------------------
.cart__summary-wrapper {
    position: sticky;
    top: 60px;
}

.cart__summary {
    @include font-size(16);
    background-color: $background-color;
    padding: gap(2.5);
}

