.cart__subtotal {
    @include font-size(20);
    font-weight: 400;
    border-top: $border--light;
    margin-top: gap(2);
    padding-top: gap(2);
}

.cart__subtotal-label,
.cart__subtotal-price {
    margin: 0;
}

.cart-price--taxes {
    text-align: right;
    margin-left: gap(1.5);
}

.button__cart {
    width: 100%;

    &:focus-visible {
        outline: $focus-state;
    }
}

