.additional-checkout-buttons {
    margin-top: gap(3);
}


// -------------------------------------
// Cart Footer
// -------------------------------------
.cart__footer {
    border: 0;

    @include bp(laptop) {
        margin: gap(3) 0 0;
    }

    .accordion__title {
        @include bp(laptop) {
            display: none;
        }
    }

    .accordion__title-wrapper {
        @include font-default;
        font-weight: 400;
        display: flex;
        align-items: center;

        .icon {
            width: 25px;
            height: 25px;
            margin-right: gap(1);
        }
    }

    .accordion__item {
        box-shadow: none;
        border: 0;
    }

    .accordion__content {
        @include bp(laptop) {
            display: flex;
            height: auto;
            padding: 15px;
            background-color: $background-color;
        }
    }
}

.cart__additional-info {
    margin: 0;
    list-style-type: disc;

    @include bp(laptop) {
        display: flex;
        list-style-type: none;
        padding: 0;
    }
}

.cart__info {
    @include font-size(16);
    display: flex;
    align-items: flex-start;

    & + .cart__info {
        @include bp(laptop) {
            margin-left: gap(2);
        }
    }

    .icon {
        display: none;

        @include bp(laptop) {
            display: block;
             margin-right: gap(1);
            width: 25px;
            height: 25px;
        }
    }

    a {
        color: $primary-color;

        &:hover {
            color: rgba($primary-color, .7);
        }
    }

    p {
        margin: 0;
    }
}

.cart__info--shipping {
    .icon {
        width: 40px
    }
}
