// ----------------------------------------------------------------------------
// Snippet: Swym Wishlist
// ----------------------------------------------------------------------------
//
// ----------------------------------------------------------------------------


// Styles
@import 'init';


.wishlist-content {
    .swym-ui-component.swym-wishlist-page {
        max-width: 100%;
        margin: 0;
    }

    .swym-wishlist-outer-container {
        width: 100%;
    }

    .swym-ui-component.swym-wishlist-page .swym-wishlist-container-title-bar .swym-wishlist-main-title {
        @include font-primary;
        letter-spacing: 0;
    }

    .swym-ui-component.swym-wishlist-page .swym-wishlist-container-title-bar .swym-user-login-status {
        @include font-primary;
        letter-spacing: 0;
    }

    .wishlist-content .swym-ui-component.swym-wishlist-page .swym-wishlist-container-title-bar .swym-wishlist-main-title, .wishlist-content .swym-ui-component.swym-wishlist-page .swym-wishlist-container-title-bar .swym-user-login-status {
        @include font-primary;
        letter-spacing: 0;
    }

    .swym-ui-component *, .swym-ui-component .swym-is-button, .swym-ui-component .swym-is-anchor, .swym-ui-component .swym-title, .swym-ui-component .swym-heading, .swym-ui-component .swym-label {
        @include font-primary;
        letter-spacing: 0;
    }

    .swym-ui-component .swym-empty-wishlist-container .swym-empty-wishlist-title {
        @include font-default;
        @include responsive-fontsize(24, 30, 34, 42);
        font-weight: 300;
        color: $primary-text-color;
    }

    .swym-ui-component .swym-empty-wishlist-container .swym-empty-wishlist-text {
        @include font-primary;
        letter-spacing: 0;
    }

    .swym-ui-component .swym-empty-wishlist-container .swym-empty-wishlist-continue-btn,
    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-add-to-cart-btn {
        @include font-default;
        font-size: 12px;
        line-height: normal;
        font-weight: 400;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        color: $white;
        background: $primary-color !important;
    }

    .swym-ui-component.swym-wishlist-page .swym-wishlist-list .swym-wishlist-selector {
        max-width: 100%;
    }

    .swym-ui-component .swym-wishlist-list-card {
        box-shadow: none;
    }

    .swym-ui-component .swym-wishlist-list-card .swym-wishlist-name {
        font-weight: 300;
        color: $primary-text-color;
    }

    .swym-ui-component .swym-wishlist-list-card .swym-wishlist-link {
        @include font-default;
        font-size: 12px;
        line-height: normal;
        font-weight: 400;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        color: $primary-text-color;
    }

    .swym-ui-component.swym-wishlist-page .swym-wishlist-detail .swym-action-bar .swym-back-btn,
    .swym-ui-component.swym-wishlist-page .swym-simple-wishlist-¸ .swym-action-bar .swym-back-btn {
        @include font-caption;
        color: $primary-text-color;
        letter-spacing: 0;
        text-transform: none;
    }

    .swym-ui-component .swym-wishlist-selector-dropdown .swym-selected-wishlist-container .swym-selected-wishlist-name {
        font-weight: 400;
        color: $primary-text-color;
    }

    .swym-ui-component .swym-wishlist-grid {
        max-width: 100%;
    }

    .swym-ui-component .swym-wishlist-detail-content .swym-wishlist-image-wrapper {
        aspect-ratio: 3 / 4;
        height: auto !important;
        max-height: none;
        position: relative;
        background-color: #f4f1ec;

        &:after {
            content: "";
            display: block;
            padding-top: 20%;
        }
    }

    .swym-ui-component .swym-action-bar .swym-back-btn .swym-icon::before {
        font-size: 10px;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-image-wrapper {
        min-width: 100%;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-delete-btn .swym-icon::before {
        color: $primary-text-color;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item {
        border: 0;
        background: transparent;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-title {
        @include font-size(16);
        @include line-height(24);
        font-weight: 400;
        margin: gap(.7) 0;
        text-decoration: none;
        color: $primary-text-color;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-variant-title {
        display: none;
    }

    .swym-ui-component .swym-wishlist-list .swym-wishlist-header-recently-viewed-btn {
        display: none !important;
    }

    .swym-ui-component.swym-wishlist-page .swym-wishlist-container-title-bar {
        padding: 0;
    }



    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-product-final-price,
    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-product-original-price {
        display: none;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-add-to-cart-btn {
        padding: 13px 30px;
    }

    .swym-variant-transformed-title {
        margin: gap(.7) 0;
    }

    .swym-ui-component .swym-wishlist-grid .swym-wishlist-item .swym-product-price {
        padding: 0;
        margin: gap(.7) 0 gap(2);
    }
}

 // Modal Styling
 .swym-ui-components {
    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-product-name,
    .swym-ui-component .swym-save-wishlist-modal .swym-save-wishlist-modal-dialog .swym-text-heading {
        @include font-primary;
        @include font-size(18);
        font-weight: 400;
        letter-spacing: 0;
    }

    .swym-ui-component .swym-save-wishlist-modal .swym-save-wishlist-modal-dialog .swym-text-content {
        @include font-primary;
        letter-spacing: 0;
    }

    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-wishlist-items-title {
        @include font-caption;
        color: $primary-text-color;
        letter-spacing: 0;
    }

    .swym-wishlist-name {
        @include font-primary;
    }

    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-new-wishlist-btn,
    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-add-to-list-btn,
    .swym-ui-component .swym-save-wishlist-modal .swym-save-wishlist-modal-dialog .swym-save-summary .swym-login-btn  {
        @include font-default;
        font-size: 12px;
        line-height: normal;
        font-weight: 400;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        width: calc(50% - 5px);
    }

    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content {
        background: $background-color--base;
    }

    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-add-to-list-btn,
    .swym-ui-component .swym-save-wishlist-modal .swym-save-wishlist-modal-dialog .swym-save-summary .swym-login-btn {
        color: $white;
        background: $primary-color !important;
    }

    .swym-ui-component .swym-add-wishlist-modal .swym-modal-content .swym-new-wishlist-btn {
        background: $background-color--base;
        color: $primary-text-color;
        border: 1px solid $primary-text-color;
    }

    .swym-notification-wishlist .swym-details {
        @include font-primary;
        letter-spacing: 0;
    }
 }


