// ----------------------------------------------------------------------------
// Icon Block
// ----------------------------------------------------------------------------
//
// A section with a Header, optional background image, button link and a
// repeating icon item (image, title and content).
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$icon-background-color: $background-color;
$icon-text-color: $primary-text-color;


// ------------------------------
// Icon Block -- Default Styles
// ------------------------------

.page-section--icon-block {
    padding: gap(6) 0;
    background-color: $icon-background-color;

    @include bp(tablet) {
        text-align: center;
    }

    @include bp(laptop) {
        padding: gap(10) 0;
    }
}

.icon-block {
    @include bp(tablet) {
        text-align: center;
    }

    .icon-block__header + &,
    .icon-block__content + & {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: gap(5);
        }
    }
}

.icon-block__header {
    color: $icon-text-color;
    margin: 0 0 gap(1);
}

.icon-block__content {
    margin-top: gap(1);
    color: $icon-text-color;

    @include bp(laptop) {
        @include container--wysiwyg;
        text-align: center;
    }
}

.icon-block__list {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;

    @include bp(tablet) {
        flex-flow: row nowrap;
        margin: 0 0 gap(3);
    }
}

.icon-block__item {
    display: flex;
    padding: 0 0 gap(3);
    align-items: center;
    justify-content: flex-start;

    @include bp(tablet) {
        display: inline-block;
        flex: 0 1 100%;
        max-width: 425px;
        padding: 0 gap(2);
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.icon-block__icon {
    display: block;
    width: 90px;
    min-width: 90px;
    margin: 0;

    @include bp(tablet) {
        margin: auto;
    }

    @include bp(laptop) {
        width: 116px;
        min-width: 116px;
    }
}

.icon-block__content-wrapper {
    text-align: left;
    margin-left: gap(2.5);

    @include bp(tablet) {
        margin-left: 0;
        text-align: center;
    }
}

.icon-block__title {
    color: $icon-text-color;
    margin: 0 0 gap(.5);

    @include bp(tablet) {
        margin: gap(1.5) 0 gap(.5);
    }
}

.icon-block__copy {
    margin: 0;
    color: $icon-text-color;
}
