// ----------------------------------------------------------------------------
// Blog Posts
// ----------------------------------------------------------------------------
//
// This is a section that displays 3 most recent blog posts
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Blog Post -- Default
// -------------------------------------

.blog-post__title {
    margin: 0 0 gap(4);
}

.blog-post {
    list-style: none;
    margin: 0;
    padding: 0;
}

.blog-count {
    @include font-caption;
    margin-top: gap(3);
}
