// ----------------------------------------------------------------------------
// Search
// ----------------------------------------------------------------------------
//
// A page template to dynamic search results.
// To be used for page level styles, not section/snippet level styles.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Search -- Search Results List
// -------------------------------------
.page-section--search-results {
    grid-column: full;

    .search-results__list {
        list-style: none;
        margin: 0;
        padding: 0;
        gap: 30px 15px;
    }

    .search-results__item {
        border-bottom: $border--light;
        display: flex;
        flex-flow: row nowrap;
        padding: $grid-gutter--mobile 0;

        @include bp(laptop) {
            padding: $grid-gutter 0;
        }
    }

    .search-results__image {
        flex: 0 1 16.667%;
        margin-right: $grid-gutter--mobile;

        @include bp(laptop) {
            max-width: 150px;
            margin-right: $grid-gutter;
        }
    }

    .search-results__content {
        flex: 0 1 83.333%;

        @include bp(laptop) {
            max-width: calc(8/12 * 100%);
        }
    }

    .search-results__title {
        margin: 0 0 5px;

        @include bp(laptop) {
            margin-bottom: 10px;
        }
    }

    .search-results__link {
        padding-bottom: 0;
    }
}
