// ----------------------------------------------------------------------------
// Button
// ----------------------------------------------------------------------------
//
// Default button styles to be included in various components. Contains styles
// for the default button effect (fill) only; to use different styles, include
// the associated variant's SCSS file in your project.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Default Variables
// -------------------------------------

$button-color-palette: $color-palette !default;
$button-border-radius: 0px !default;
$button-transition: $anim !default;
$button-focus-state: $focus-state !default;


// -------------------------------------
// Default Button Styles
// -------------------------------------

button,
.button,
[type=button] {
    // Shopify Defaults
    appearance: none;
    border: 0;
    cursor: pointer;
    display: inline-block;
    line-height: 1;
    text-decoration: none;
    user-select: none;
    white-space: nowrap;

    // iamota Customizations
    border-radius: $button-border-radius;
    padding: 13px 30px;
    text-align: center;
    transition: $button-transition;

    @include font-default;
    font-size: 12px;
    line-height: normal;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    // Disabled Styles live here because they override everything
    &.button--disabled,
    &:disabled {
        background-color: map-get($button-color--disabled, color);
        color: map-get($button-color--disabled, contrast);
        cursor: default;

        &:hover,
        a:hover & {
            background-color: map-get($button-color--disabled, color);
            color: map-get($button-color--disabled, contrast);
            border-color: transparent;
        }

        &.button--text {
            background-color: initial;
            opacity: 0.3;

            &:hover,
            a:hover & {
                background-color: initial;
            }
        }

        .btn--add-to-cart__pricing {
            display: none;
        }
    }

    &:focus-visible,
    a:focus-visible & {
        outline: $button-focus-state;
    }
}

// Disabled "link" style
.link.link--disabled {
    color: map-get($button-color--disabled, color);
    cursor: default;
    opacity: 0.3;

    &:hover,
    a:hover & {
        color: map-get($button-color--disabled, color);
    }
}


// -------------------------------------
// Default Colors & Sizes
// -------------------------------------

// Buttons
.button--primary {
    background-color: $primary-color;
    color: $white;
    border: 1px solid transparent;

    &:hover,
    &:active,
    a:hover & {
        background-color: $secondary-color;
        color: $white;
        border: 1px solid $secondary-color;
    }

    &:focus-visible,
    a:focus-visible & {
        color: $white;
        border-color: $secondary-color;
    }
}

.button--secondary {
    background-color: $background-color--base;
    color: $primary-text-color;
    border: 1px solid $primary-text-color;

    &:hover,
    &:active,
    a:hover & {
        background-color: $secondary-color;
        color: $white;
        border: 1px solid $secondary-color;
    }

    &:focus-visible,
    a:focus-visible & {
        background-color: $background-color--base;
        color: $primary-text-color;
        border: 1px solid $secondary-color;
    }
}

.button--tertiary {
    background-color: $primary-color;
    color: $white;
    border: 1px solid $white;

    &:hover,
    &:active,
    a:hover & {
        background-color: $secondary-color;
        color: $white;
        border: 1px solid $secondary-color;
    }

    &:focus-visible,
    a:focus-visible & {
        background-color: $primary-color;
        color: $white;
        border: 1px solid $white;
    }
}

.button--white {
    background-color: $white;
    color: $primary-text-color;
    border: 1px solid transparent;

    &:hover,
    &:active,
    a:hover & {
        background-color: transparent;
        color: $white;
        border: 1px solid $white;
    }

    &:focus-visible,
    a:focus-visible & {
        color: $primary-text-color;
    }
}

// Links
.link--primary {
    color: $primary-color;

    &:hover,
    a:hover & {
        color: $secondary-color;
    }
}

.link--secondary {
    color: $secondary-color;

    &:hover,
    a:hover & {
        color: $primary-color;
    }
}

.link--white {
    color: $white;

    &:hover,
    a:hover & {
        color: $primary-color;
    }
}


// -------------------------------------
// Button Collection
// -------------------------------------

.button-collection {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    list-style: none;
    margin: 0;
    margin-top: $grid-gutter;
    padding: 0;

    &.button-collection--left {
        justify-content: flex-start;
    }

    &.button-collection--center {
        justify-content: center;
    }

    &.button-collection--right {
        justify-content: flex-end;
    }
}
