// ----------------------------------------------------------------------------
// CSS Grid -- Alternating
// ----------------------------------------------------------------------------
//
// Container component that allows for items to be wrapped in an alternating size
// CSS grid. Defaults as 2:1 size blocks on large screens.
//
// @import 'components/containers/css-grid/css-grid';
// @import 'components/containers/css-grid/css-grid--alternating';
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@use "sass:math";

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$css-grid-gutter--mobile: $grid-gutter--mobile !default;
$css-grid-gutter: $grid-gutter !default;
$css-grid-columns-large: 6 !default;
$css-grid-columns-medium: 4 !default;
$css-grid-columns-small: 2 !default;


// -------------------------------------
// Pre-defined alternating grid
// -------------------------------------
.css-grid--alternating {
    // inherits from base

    @include bp(laptop) {
        grid-template-columns: repeat(3, 1fr);

        .css-grid__item {
            width: calc(33.333% - (#{$css-grid-gutter} * 2));

            @supports (display: grid) {
                margin: 0;
                width: initial;
            }
        }

        .css-grid__item:nth-child(4n-3),
        .css-grid__item:nth-child(4n-4) {
            width: calc(66.666% - (#{$css-grid-gutter} * 2));

            @supports (display: grid) {
                grid-column-end: span 2;
                margin: 0;
                width: initial;
            }
        }
    }
}
