// ----------------------------------------------------------------------------
// Mixins
// ----------------------------------------------------------------------------
//
// SASS mixins to help extend common classes and functions easily.
//
// Included:
// Clearfix
// Media Queries
// Typography
// Utilities
// - Aspect Ratio
// - Visually Hidden
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@use "sass:math";


// -------------------------------------
// Clearfix
// -------------------------------------
// Forces an element to self-clear its
// children.
//
// Usage:
// .selector {
//   @include clearfix;
// }
// -------------------------------------
@mixin clearfix() {
    &::after {
        content: '';
        display: table;
        clear: both;
    }
}


// -------------------------------------
// Media Queries
// -------------------------------------
// Sets a media query for the specified
// breakpoint.
//
// Accepts:
// $media-query: {String} Breakpoint variable (list found in variables.scss)
//
// Usage:
// .selector {
//   @include media-query($medium-up) {
//     color: red;
//   }
// }
// -------------------------------------
@mixin media-query($media-query) {
    $breakpoint-found: false;

    @each $breakpoint in $breakpoints {
        $name: nth($breakpoint, 1);
        $declaration: nth($breakpoint, 2);

        @if $media-query == $name and $declaration {
            $breakpoint-found: true;

            @media only screen and #{$declaration} {
                @content;
            }
        }
    }
}


// Progressive Enhancement Breakpoints
@mixin bp($point) {
    $bp-mobile: '(min-width: 0)';
    $bp-tablet: '(min-width: #{$container--tablet})';
    $bp-laptop: '(min-width: #{$container--laptop})';
    $bp-desktop: '(min-width: #{$container--desktop})';
    $bp-desktop-xl: '(min-width: #{$container--desktop-xl})';

    @if $point == desktopXL {
        @media #{$bp-desktop-xl} { @content; }
    }
    @else if $point == desktop {
        @media #{$bp-desktop} { @content; }
    }
    @else if $point == laptop {
        @media #{$bp-laptop} { @content; }
    }
    @else if $point == tablet {
        @media #{$bp-tablet}  { @content; }
    }
    @else if $point == mobile {
        @media #{$bp-mobile}  { @content; }
    }
}

// Graceful Degradation Breakpoints (using sparingly)
@mixin bpgd($point) {
    $bpgd-mobile: '(max-width: #{$container--tablet})';
    $bpgd-tablet: '(max-width: #{$container--laptop})';
    $bpgd-laptop: '(max-width: #{$container--desktop})';

    @if $point == laptop {
        @media #{$bpgd-laptop} { @content; }
    }
    @else if $point == tablet {
        @media #{$bpgd-tablet}  { @content; }
    }
    @else if $point == mobile {
        @media #{$bpgd-mobile}  { @content; }
    }
}



// -------------------------------------
// Typography Mixins
// -------------------------------------

// Font Size/Line Height
@mixin font-size($size) {
    font-size: $size + px; // Fallback in px
    font-size: calculate-rem($size);
}

@mixin line-height($size) {
    line-height: $size + px; // Fallback in px
    line-height: calculate-rem($size);
}

// Headings
@mixin headings($from: 1, $to: 6) {
    @for $i from $from through $to {
        h#{$i} {
            @content
        }
    }
}

@mixin ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


// -------------------------------------
// Container Mixins
// -------------------------------------

@mixin container {
    margin: 0 auto;
    max-width: $max-width;
    padding: 0;
    width: calc(100% - (#{$grid-gutter--mobile} * 2));

    @include bp(laptop) {
        width: calc(100% - (#{$grid-gutter} * 2));
    }

}

// WYSIWYG Container
// Narrowed text container for pleasant reading
@mixin container--wysiwyg {
    @include container;

    @include bp(laptop) {
        width: calc(75% - (#{$grid-gutter} * 2));
    }
}


// -------------------------------------
// Utility Mixins
// -------------------------------------
/**
 * Outputs list of transition properties
 *
 * Accepts:
 * - $transitions: {String} List of transition properties to set
 *
 * Usage:
 * .selector {
 *   @include transition(width, height 0.5s ease-in);
 * }
 */
@mixin transition($transitions...) {
    $output: ();

    @each $transition in $transitions {
        $output: append($output, get-transition-properties($transition), comma);
    }
    transition: $output;
}


/**
 * Hide element but make it accessible to screen readers
 *
 * Usage:
 * .selector {
 *   @include visually-hidden;
 * }
 */
@mixin visually-hidden() {
    // stylelint-disable-next-line
    position: absolute !important;
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
}


/**
 * Reverse the properties applied by @mixin visually-hidden
 *
 * Accepts:
 * - $position: {String} Positioning method for element
 *
 * Usage:
 * .selector {
 *   @include visually-shown(relative);
 * }
 */
@mixin visually-shown($position: inherit) {
    // stylelint-disable-next-line
    position: $position !important;
    overflow: auto;
    width: auto;
    height: auto;
    margin: 0;
    clip: auto;
}


// Image Aspect Ratio
@mixin aspect-ratio($width, $height) {
    &::before {
        content: '';
        float: left;
        height: 0;
        margin-left: -1px;
        padding-top: math.div($height, $width) * 100%;
        width: 1px;
    }

    &::after {
        clear: both;
        content: '';
        display: table;
    }
}


// Hamburger Icon Animation
// crosses when opened
@mixin hamburger($width, $height, $thickness) {
    cursor: pointer;
    display: block;
    height: $height;
    position: relative;
    width: $width;

    span,
    span::before,
    span::after {
        background: currentColor;
        content: '';
        display: block;
        height: $thickness;
        position: absolute;
        top: calc(50% - (#{$thickness} / 2));
        width: $width;
    }

    span::before {
        transform: translateY(-$thickness * 4);
        transition: transform .2s ease;
    }

    span::after {
        transform: translateY($thickness * 4);
        transition: transform .2s ease;
    }

    &.js--is-open {
        span {
            background: transparent;
        }

        span::before {
            transform: rotate(45deg);
        }

        span::after {
            transform: rotate(-45deg);
        }
    }
}


// Plus Icon Animation
// changes to minus when opened
@mixin plus-to-minus($width, $height, $thickness) {
    position: relative;
    height: $height;
    cursor: pointer;

    span,
    span::after {
        background: $jet;
        border-radius: $thickness * 0.5;
        content: '';
        display: block;
        height: $thickness;
        pointer-events: none;
        transition: all .25s ease;
        width: $width;
    }

    span::before {
        transform: translateY(-$thickness * 3);
    }

    span::after {
        position: absolute;
        transform: translateY($thickness * 3);
        transform: rotate(90deg);
    }

    &.is-open {
        span::after {
            background: transparent;
        }
    }
}

@mixin swiper-scrollbar () {
    position: relative;
    bottom: auto;
    width: 100%;
    background-color: #6E6E6E;
    height: 1px;
    border-radius: 0;
    left: 0;
    margin-top: 0;

    .swiper-scrollbar-drag {
        height: 2px;
        top: -1px;
        background-color: $primary-text-color;
    }
}
