// ----------------------------------------------------------------------------
// Content Panels
// ----------------------------------------------------------------------------
//
// Content sections in blocks, side by side with media.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$content-panel-content-padding: gap(3) gap(12) !default;


// -------------------------------------
// Content Panel, General Styles
// -------------------------------------

.page-section--content-panel {
    & + .page-section--content-panel {
        //margin-top: 0;

        .content-panel {
            .content-panel__container {
                @include bp(tablet) {
                    padding-top: 0;
                }

                @include bp(laptop) {
                    padding-top: 0;
                }
            }
        }
    }
}

.content-panel {
    position: relative;

    .content-panel__content {
        background: transparent;
        padding: gap(3) 0 gap(4);

        @include bp(tablet) {
            padding: gap(3) gap(6);
        }

        @include bp(laptop) {
            padding: $content-panel-content-padding;
        }
    }

    &.align-left {
        .content-panel__meta {

            @include bp(tablet) {
                grid-column: 10 / span 15;
            }
        }

        .content-panel__content {
            padding-left: $grid-gutter--mobile;
            padding-right: $grid-gutter--mobile;

            @include bp(tablet) {
                padding-left: $grid-gutter;
                padding-right: 0;
                grid-column: 1 / span 8;
            }
        }
    }

    &.align-right {

        .content-panel__meta {

            @include bp(tablet) {
                grid-column: 1 / span 15;
            }
        }

        .content-panel__content {
            padding-left: $grid-gutter--mobile;
            padding-right: $grid-gutter--mobile;

            @include bp(tablet) {
                grid-column: 17 / span 8;
                padding-right: $grid-gutter;
                padding-left: 0;
            }
        }
    }

    .content-panel__meta {
        position: relative;
    }

    .content-panel__image {
        display: block;
    }

    @include bp(tablet) {

        .content-panel__content {
            margin-bottom: 0;
        }
    }


    @include bp(tablet) {

        .content-panel__container {
            display: grid;
            grid-template-columns: repeat(24, 1fr);
        }

        .content-panel__content {
            flex: 0 1 60%;
        }

        .content-panel__meta {
            flex: 0 1 40%;
        }

        .content-panel__image {
            display: block;
        }

        &.align-left {
            .content-panel__content {
                order: 1;
            }

            .content-panel__meta {
                order: 2;
            }
        }

        &.align-right {
            .content-panel__content {
                order: 2;
            }

            .content-panel__meta {
                order: 1;
            }
        }
    }
}


// -------------------------------------
// Content Panel - Video
// -------------------------------------
.content-panel {
    .content-panel__meta--video {
        &:focus-visible {
            outline: $focus-state;
            outline-offset: gap(.5);

            .video-block__control {
                .icon__play {
                    outline: $focus-state;
                    outline-offset: gap(.5);
                }
            }
        }
    }

    button.content-panel__meta {
        padding: 0;
        border-radius: 0;

        .icon.content-panel__video-icon {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: $white;
            font-size: 120px;
        }
    }
}


// -------------------------------------
// Content Panel - Project Specific
// -------------------------------------

.content-panel {
    .content-panel__container {
        @include bp(tablet) {
            align-items: center;
        }
    }

    .content-panel__meta {
        height: auto;
        overflow: hidden;
        background-color: $background-color;

        @include bp(tablet) {
            flex: 0 1 60%;
        }
    }

    .content-panel__content {
        @include bp(tablet) {
            flex: 0 1 40%;
        }

        .copy-block__title-tag {
            @include font-tags;
            text-transform: uppercase;
            font-weight: 800;
            color: $secondary-text-color;
            margin-bottom: gap(1.5);
        }

        .copy-block__title {
            margin-top: 0;
            margin-bottom: gap(2);
        }

        .copy-block__text li {
            padding-left: 5px;
        }

        .copy-block__text ul {
            font-size: 6px;
            margin-top: 20px;
            list-style-type: disc;
        }

        .copy-block__text ul li {
            font-size: 18px;
            margin-bottom: 10px;
        }

        .button--secondary {
            margin-top: gap(2);
        }
    }
}

.content-panel__list {
    margin: gap(2) 0;
    padding: 0;
    list-style-type: none;
}

.content-panel__list-item {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: gap(1.5);
}

.content-panel__icon {
    display: block;
    color: $primary-text-color;
    margin-right: gap(1);
    width: 30px;
    height: 30px;

    .icon {
        width: 30px;
        height: 30px;
    }
}
