// ----------------------------------------------------------------------------
// Menu
// ----------------------------------------------------------------------------
//
// Navigation patterns for the site; the default functionality is simply to pare
// the component down to its most base styles. Leverage additional variants to
// power up the menu into more complex patterns.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$menu-background-color: transparent !default;
$menu-item-margin: 0 !default;
$menu-link-background-color: transparent !default;
$menu-link-color: $primary-text-color !default;
$menu-link-padding: 30px 20px !default; // use padding because menu items can have backgrounds



// -------------------------------------
// Menu
// -------------------------------------
.menu {
    display: block;
}

.menu__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.menu__item {
    margin: $menu-item-margin;
}

.menu__link {
    color: $menu-link-color;
    cursor: pointer;
    display: block;
    text-decoration: none;
}

.menu__title {
    @include font-navigation;
    line-height: 1;
    margin: 0;
}


// -------------------------------------
// Menu -- Default
//
// Defaults to a horizontal flow typical
// of most site menus.
// -------------------------------------
.menu--default {
    background-color: $menu-background-color;

    .menu__list {
        display: flex;
        flex-flow: column nowrap;

        @include bp(laptop) {
            flex-flow: row nowrap;
        }
    }

    .menu__link {
        background-color: $menu-link-background-color;
        padding: 10px $grid-gutter--mobile;
        transition: $anim;

        @include bp(laptop) {
            padding: gap(3) gap(1);
        }

        @include bp(desktop) {
            padding: $menu-link-padding;
        }

        &.menu__link--active {
            background-color: $background-color;
        }

        &:hover {
            background-color: $background-color;
            color: $menu-link-color;
        }
    }
}


// -------------------------------------
// Menu -- Header Nav
// -------------------------------------
.menu--header {

    .menu__item--dropdown {

        @include bpgd(tablet) {
            display: flex;
            flex-flow: row wrap;
            align-items: center;
            justify-content: space-between;

            > .menu__link {
                flex-grow: 1;
            }
        }

        > .dropdown {

            @include bpgd(tablet) {
                width: 100%;
            }
        }

        > .dropdown__icon {

            @include bpgd(tablet) {
                right: 15px;
            }

            @include bp(laptop) {
                margin-right: 20px;
                margin-left: -35px;
            }
        }

        > .menu__link  {

            &.menu__link--active,
            &:focus-visible {

                @include bpgd(tablet) {
                    display: block;
                    position: relative;

                    &::after {
                        content: "";
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 150%;
                        height: 100%;
                        background-color: $background-color;
                    }

                    .menu__title {
                        position: relative;
                        z-index: 1;
                    }
                }
            }

            @include bp(laptop) {
                padding: 30px 40px 30px 20px;
            }
        }
    }
}

// -------------------------------------
// Menu -- Secondary Nav
// -------------------------------------
.menu--secondary {
   margin: gap(2) $grid-gutter--mobile;
   padding: gap(2.5) 0 0;
   border-top: $border--light;

   @include bp(laptop) {
        margin: 0;
        padding: 0;
        border: 0;
   }

   .header--white:not(.header--background) & {
    .menu__link {
        color: var(--header-text-color);

        a {
            color: var(--header-text-color);
        }
    }
   }

   .menu__list {
        display: flex;

        flex-flow: column nowrap;

        @include bp(laptop) {
            flex-flow: row nowrap;
             align-items: center;
        }
   }

    .menu__item {
        display: inline-block;
        margin-right: gap(2);
        margin-bottom: gap(2.5);

        @include bp(laptop) {
            margin: 0;
            padding: gap(2.8) gap(1);
        }
    }

    .menu__item--cart {
        display: none;

        @include bp(laptop) {
            display: inline-block;
            padding-right: 0;
        }
    }

    .menu__link {
        &:hover {
            text-decoration: underline;

            a {
                text-decoration: underline;
            }
        }

        a {
            @include font-default;
            text-decoration: none;
            color: $menu-link-color;
        }
    }

    .menu__title {
        @include font-default;
    }
}
