// ----------------------------------------------------------------------------
// Newsletter
// ----------------------------------------------------------------------------
//
// The newletter component is a custom form that allows a user to sign up for
// a mailing list. This file provides base styles for the newsletter snippet
// (the form partial only) as well as section-specific styles for the full
// newsletter section.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$newsletter-height: 45px !default;
$newsletter-button-width: 60px !default;

// Newsletter section
$newsletter-section-background: $background-color;
$newsletter-section-text-color: $primary-text-color;

// Newsletter footer
$newsletter-footer-text-color: $primary-text-color;


// -------------------------------------
// Newsletter Layout
// This configures the side-by-side
// layout for the newsletter
// -------------------------------------
.page-section--newsletter {
    background-color: $newsletter-section-background;
}

#newsletter {
    scroll-margin-top: 100px;
}

.newsletter--default {
    display: block;

    .form--newsletter {
        @include clearfix;
        max-width: 500px;
        scroll-padding: 100px;
    }

    .newsletter__input,
    .newsletter__submit {
        height: $newsletter-height;
        padding: 12px 0px 11px;
    }

    .newsletter__submit {
        padding-right: 14px;
        padding-left: 13px;
    }

    .newsletter__input {
        background-color: transparent;
        border: 0;
        color: $primary-text-color;
        padding-right: 0;
        padding-left: 14px;

        &::placeholder {
            color: $caption-text-color;
        }

        &:-moz-placeholder {
            color: $caption-text-color;
        }

        &:-ms-input-placeholder {
            color: $caption-text-color;
        }

        &::-ms-input-placeholder {
            color: $caption-text-color;
        }

        &::-webkit-input-placeholder {
            color: $caption-text-color;
        }
    }

    // Newsletter Input Wrapper
    .form__field--email {
        margin-bottom: 0;
        float: left;
        width: 100%;

        &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color:    #6E6E6E;
        }
        &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
           color:    #6E6E6E;
           opacity:  1;
        }
        &::-moz-placeholder { /* Mozilla Firefox 19+ */
           color:    #6E6E6E;
           opacity:  1;
        }
        &:-ms-input-placeholder { /* Internet Explorer 10-11 */
           color:    #6E6E6E;
        }
        &::-ms-input-placeholder { /* Microsoft Edge */
           color:    #6E6E6E;
        }

        &::placeholder { /* Most modern browsers support this now. */
           color:    #6E6E6E;
        }

        .form__label {
            display: none;
        }
    }

    // Newsletter Success Message
    .newsletter__success {
        color: $newsletter-section-text-color;
    }

    // Newsletter Submit Button Wrapper
    .form__field--button {
        margin-bottom: 0;
        float: left;

        .newsletter__submit {
            @include font-size(13);
            display: flex;
            justify-content: flex-end;
            align-items: center;
            width: 100%;
        }
    }
}



// -------------------------------------
// Newsletter Section
// -------------------------------------
.newsletter-section {
    background-color: $newsletter-section-background;
    padding: 40px 0;

    @include bp(tablet) {
        text-align: center;
    }

    @include bp(laptop) {
        padding: 60px $grid-gutter;
    }

    .newsletter__title {
        color: $newsletter-section-text-color;
        margin: 0 0 gap(2);
    }

    .newsletter__content {
        @include bp(tablet) {
            @include container--wysiwyg;
        }
    }

    .form--newsletter {
        margin: gap(2) auto;

        @include bp(tablet) {
            margin: gap(2) auto;
        }

        @include bp(laptop) {
            margin-bottom: 0;
        }
    }

    .newsletter__wrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) auto;
        background-color: $white;
    }

    .newsletter__success {
        color: $newsletter-section-text-color;
    }

    .newsletter__input,
    .newsletter__submit {
        background-color: transparent;
        border: 0;
        border-radius: 0;
        color: $newsletter-section-text-color;
    }

    .newsletter__input {
        &:focus {
            outline: $focus-state;
        }
    }
}




