    // Carousel dot styles
.swiper-pagination.swiper-pagination-bullets {
    display: block;
    line-height: 1;
    list-style: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;
    padding: 0;
    text-align: center;

    .swiper-pagination-bullet {
        display: inline-block;
        height: auto;
        width: auto;
        opacity: 1;

        &:not(:last-child) {
            margin: $carousel-dot-margin;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    .swiper-pagination-bullet {
        background: $carousel-dot-background;
        border: $carousel-dot-border;
        border-radius: 50%;
        font-size: 0; // hides slide number visually while keeping for accessibility
        padding: 0;
        height: $carousel-dot-size;
        width: $carousel-dot-size;
    }

    .swiper-pagination-bullet-active {
        background: $carousel-dot-background-active;
    }
}

// // -------------------------------------
// // Carousel -- Testimonials
// // -------------------------------------
.carousel-section--testimonials {

    .swiper-arrow {
        @include font-size(16);
        box-shadow: none;
        color: $primary-color;

        @include bp(laptop) {
            @include font-size(18);
            width: 40px;
            height: 40px;
        }

        &.swiper-next {
            right: 0;
        }

        &.swiper-prev {
            left: 0;
        }

        &:after {
            display: none;
        }
    }
}

.carousel--testimonials {
    .swiper-wrapper {
        display: flex;
        align-items: center;
        position: relative;
        padding: 0;
        list-style-type: none;
        padding-bottom: 30px;
        margin: 0;

        @include bp(laptop) {
            padding-bottom: 40px;
        }
    }

    // Carousel dot styles
    .swiper-list {
        margin-bottom: $grid-gutter--mobile;
    }

    .swiper-slide {
        padding: 0 $grid-gutter;

        &:focus-visible {
            outline: none;

            .swiper__quote-container {
                outline: $focus-state;
            }
        }
    }

    .testimonials__quote {
        @include container--wysiwyg;
        @include font-primary;
        margin-bottom: 5px;
    }

    .testimonials__author {
        @include font-caption;
    }

    .swiper-button-next:after,
    .swiper-button-prev:after {
        content: "";
    }

    .swiper-pagination-bullet {

        &:only-child {
            display: none;
        }
    }
}

// // -------------------------------------
// // Carousel -- Testimonials with Image
// // -------------------------------------
.swiper-slide--with-image {

    .swiper__quote-container {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
        padding: 0 gap(2);
        margin-top: $focus-width; // to make space for the outline on focus state

        @include bp(tablet) {
            flex-flow: row nowrap;
        }

        @include bp(laptop) {
            padding: 0 gap(10);
        }

        .swiper__quote-meta {
            @include aspect-ratio(1,1);
            position: relative;
            width: 100%;
            max-width: 300px;
            margin-top: gap(1.5);
            margin-bottom: gap(2.5);

            .background-image {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            @include bp(tablet) {
                margin-top: 0;
                margin-bottom: 0;
            }

            img {
                border-radius: 50%;
            }
        }

        .swiper__quote-content {
            text-align: center;
            width: 100%;
            margin-bottom: gap(5);

            @include bp(tablet) {
                text-align: left;
                padding-left: gap(6.5);
                margin-bottom: 0;
            }
        }

        .testimonials__quote {
            margin-left: 0;
            width: 100%;
        }
    }
}
