// ----------------------------------------------------------------------------
// Pick-up Availability
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------

.bopis-container {
    background-color: $background-color;
    padding: 10px 7px;

    @include bp(tablet) {
        padding: 10px 15px;
    }
}

.online-inventory {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;

    .bopis-body-text {
        text-align: right;
    }
}

.bopis-heading {
    @include font-tags;
    @include font-size(13);
    line-height: normal;

    @include bp(tablet) {
        @include font-size(14);
    }
}

.bopis-body-text {
    @include font-caption;
    @include font-size(13);
    line-height: normal;

    @include bp(tablet) {
        @include font-size(14);
    }
}

.out-of-stock {
    color: $error-color;
}

.location-map-hours {
    a {
        @include font-size(12);
        line-height: 1;
        font-weight: 300;
    }
}

.pickup-availability-container {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .accordion__button {
        padding: 0;
        gap: 5px;
    }

    .accordion__content {
        margin-bottom: 0;
    }

    .accordion__item,
    .accordion {
        border: 0;
    }

    .accordion__icon {
        margin: auto 0;
        margin-left: 5px;

        .icon {
            width: 12px;
            height: 15px;
        }
    }

    .accordion--text {
        display: inline-flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: flex-end;
        text-align: right;
    }
}

.pickup-availabilities-list {
    max-height: 375px;
    overflow-y: auto;
    list-style: none;
    padding: 0;
    margin: 0;

    & > * {
        @include font-size(11);
        line-height: 1.2;
        font-weight: 300;
    }

    .pickup-availability-address {
        font-style: normal;
    }
}

.pickup-availability-list__item {
    &:first-of-type {
        border-top: 1px solid $border-color--light;
    }

    border-bottom: 1px solid $border-color--light;
    padding: 10px 0;
    margin-right: 10px;

    .bopis-heading {
        margin: 0;
        padding-bottom: 5px;
    }
}

.pickup-availability-list__item-content {
    display: grid;
    grid-template-columns: 1fr auto;
    gap: 15px;

    .item-content__right {
        text-align: right;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px;
    }
}

.pickup-availability__description {
    padding: 10px 0;
}