// ----------------------------------------------------------------------------
// Social Sharing
// ----------------------------------------------------------------------------
//
// Social sharing icons displayed on a blog article
// ----------------------------------------------------------------------------


// Styles
@import 'init';


// -------------------------------------
// Social Sharing - Default
// -------------------------------------

.social-sharing {
    margin-top: gap(3);

    @include bp(laptop) {
        margin-top: gap(5);
    }

    a {
        color: $primary-color;
        text-decoration: none;

        @include bp(laptop) {

            &:hover {
                opacity: .75;
            }
        }

        &:first-child {
            margin-left: gap(.5);
        }

        &:not(:last-child) {

            .icon {
                margin-right: gap(.5);
            }
        }
    }

    .icon {
        vertical-align: baseline;
    }
}
