// ----------------------------------------------------------------------------
// Search
// ----------------------------------------------------------------------------
//
// Contains styles for the two search forms (header, search page)
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$search-box-height: 44px !default;
$search-box-button-width: 150px !default;
$search-box-transition: $anim !default;


// -------------------------------------
// Default Search
// -------------------------------------

.search-box--default {
    display: block;

    .search-box__toggle {
        display: none;
    }

    .form--search {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-around;
    }

    .form__label {
        display: none;
    }

    .form__field {
        flex: 0 1 100%;
        margin: 0;
    }

    .search__input,
    .search__submit {
        border-radius: 0;
        height: $search-box-height;
    }

    .search__submit {
        display: flex;
        align-items: center;
    }

    .search__input {
        &::-webkit-search-cancel-button{
           -webkit-appearance: none;
        }
    }

    .search__close {
        display: flex;
        padding: 0;
        background: transparent;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translatey(-50%);
        color: $black;
    }
}



// -------------------------------------
// Search -- Header
// -------------------------------------

.search-box--header {
    display: none;
    padding: 0 0 gap(1);

    .header__mobile-links & {
        display: block;
        padding: 0;


        .search-box__toggle-title {
            display: none;
        }

        .form__field--search {
           // padding-left: gap(1);
        }
    }

    @include bp(laptop) {
        display: block;
        padding: gap(3) gap(1);
    }

    .search-box__toggle {
        @include font-default;
        @include font-size(16);
        color: var(--header-text-color);
        background: transparent;
        text-decoration: none;
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        transition: none;

        @include bp(laptop) {
            margin: 0;
            padding: 0;

            &:hover {
                color: $secondary-color;
            }
        }

        .icon {
            width: 16px;
            vertical-align: super;
        }
    }

    .search-box__toggle-title {
        margin-left: gap(0.5);
    }

    .form--search {
        justify-content: flex-start;
        width: 100%;

        @include bp(laptop) {
            justify-content: flex-end;
            width: auto;
        }
    }

    .form__label {
        @include font-size(16);
        //display: none;
        margin-bottom: 0;
    }

    .form__field--search {
        //display: none;
        min-width: 200px;
        width: 100%;

        @include bp(laptop) {
            flex: 0 1 75%;
            min-width: 150px;
        }
    }

    .form__field--button {
        //display: none;
        flex: 0 1 44px;
        padding: 0 gap(1);
        margin-right: gap(-1);
    }

    .search__input {
        background-color: transparent;
        border: 0;
        padding: gap(1) 0;
        border-radius: 0;
        appearance: none;
        -webkit-appearance: none;

        &:focus {
            outline: none;
        }
    }

    .search__submit {
        @include font-size(18);
        background-color: transparent;
        padding: 10px 0;
        width: 18px;
        height: 18px;

        @include bp(laptop) {
            @include font-size(24);
        }

        .icon {
            color: $black;
            width: 16px;
            vertical-align: initial;
        }
    }

    // When toggled, this changes:
    &.is-active {
        .form__label {
            display: none;
        }

        .form__field--search {
            display: inline-block;
        }

        .form__field--button {
            display: flex;
            align-items: center;
        }

        .search-container {
            display: block;
        }
    }

    .search-container {
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: $background-color;

        .form__field--search {
            flex: 1;
        }

        .form__field--button {
            flex: 0;
        }
    }
}


// -------------------------------------
// Search -- Search Results Form
// -------------------------------------

.search-box--search-results {
    .form--search {
        border-bottom: 1px solid $primary-color;
        flex-flow: row nowrap;
        margin-bottom: gap(1);
        width: 100%;

        @include bp(laptop) {
            max-width: calc(8/12 * 100%);
        }
    }

    .form__control {
        @include responsive-fontsize(22, 32, 32, 36);
        @include font-header;
    }

    .form__field {
        margin: 0;
    }

    .form__field--search {
        flex: 0 1 100%;
        width: 100%;
    }

    .form__field--button {
        flex: 0 1 auto;
        width: auto;
    }

    .search__input {
        border: 0;
        padding-left: 0;
        background-color: transparent;
    }

    .search__submit {
        @include font-size(24);
        background-color: transparent;
        padding-right: 0;

        @include bp(laptop) {
            @include font-size(32);
        }
    }
}
