// ----------------------------------------------------------------------------
// App Wrapper
// ----------------------------------------------------------------------------
//
// A component that allows for an app block to have container width and styling
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

// Styles
@import 'init';

.page-section--app-wrapper {
    margin: 0;

    & .app-wrapper--padding {
        padding: gap(3) 0;

        @include bp(tablet) {
            padding: gap(5) 0;
        }
    }

    & .app-wrapper--background {
        background-color: $background-color;
    }

    .app-wrapper {
        .malomo-order-lookup-customer-email-label,
        .malomo-order-lookup-number-label,
        .malomo-order-lookup-tracking-number-label {
            font-weight: 400 !important;
        }

       .malomo-shipment-tracker-events-modal {
            height: 70% !important;

            @include bp(tablet) {
                height: auto;
            }
        }
        #shopify-block-malomo_order_tracking_shipment_tracker_t9UUVe {
            justify-content: start;
        }
    }

    .app-wrapper__header {
        margin: 0 0 gap(3);
    }



    // -------------------------------------
    // Rebuy Recommended Product Styles
    // -------------------------------------
    .rebuy-widget,
    .rebuy-recommended-products {
        padding: 0;

        .rebuy-product-grid.large-carousel {
            padding-right: 0;
            padding-left: 0;
            padding-bottom: gap(3);

            @include bp(laptop) {
                padding-bottom: gap(5);
            }
        }

        .rebuy-widget-content {
            .primary-title {
                display: none;
            }
        }

        .rebuy-product-block {
            padding: 0 gap(.7);

            .rebuy-product-options,
            .rebuy-product-actions {
                display: none !important;
            }

            .rebuy-product-info {
                text-align: left;
                padding: 10px 0 0 0;

                @include bp(laptop) {
                    padding: 20px 0 0 0;
                }
            }

            .rebuy-product-info .rebuy-product-title {
                @include font-size(16);
                @include line-height(24);
                font-weight: 400;
                margin: gap(.7) 0;
                text-decoration: none;
                margin: 0;

                @include bp(laptop) {
                    @include font-size(16);
                    line-height: normal;
                }
            }

            .rebuy-product-info .rebuy-product-price {
                @include font-default;
                @include font-size(14);
                @include line-height(22);
                font-weight: 300;
                text-decoration: none;

                @include bp(laptop) {
                    @include font-size(16);
                    line-height: normal;
                }
            }

            .rebuy-money.sale {
                color: $sale-color;
            }

            .rebuy-product-media img {
                object-fit: cover;
                width: 100% !important;
            }
        }

        .rebuy-carousel__arrows .rebuy-carousel__arrow {
            border-radius: 0;
        }

        .splide.is-focus-in .splide__arrow:focus {
            outline: $focus-state;
        }

        .rebuy-carousel__slide {
            visibility: hidden;
            transition: $anim;

            &.is-visible {
                visibility: visible;
                transition: $anim;
            }
        }
        .powered-by-rebuy {
            display: none !important;
        }

        .rebuy-carousel__pagination {
            display: flex;
            bottom: 0;

            li {
                flex: auto;
            }

            .splide__pagination__page {
                flex: auto;
                margin: 0;
                border-radius: 0;
                height: 2px;
                width: 100%;
                background: $dark-grey;


                &.is-active {
                    background: $black;
                }
            }


        }

        .rebuy-carousel__arrows {
            .rebuy-carousel__arrow {
                background: transparent;
                color: $black;

                svg {
                    display: none;
                }

                &:hover {
                    background: transparent;
                    color: $secondary-color;
                }
            }

            .rebuy-carousel__arrow--next {
                top: -45px;
                right: 0;

                &:after {
                    content: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24' fill='none'%3e%3cpath d='M0.0885289 10.9544L0.0885289 13.1337L21.8813 13.1337L21.8813 10.9544L0.0885289 10.9544Z' fill='currentColor'/%3e%3cpath d='M21.5461 10.5031L9.98873 22.0604L11.5297 23.6014L23.087 12.0441L21.5461 10.5031Z' fill='currentColor'/%3e%3cpath d='M11.5297 0.48674L9.98873 2.02772L21.5461 13.5851L23.087 12.0441L11.5297 0.48674Z' fill='currentColor'/%3e%3c/svg%3e");
                    height: 40px;
                    width: 40px;
                    display: block;
                }
            }

            .rebuy-carousel__arrow--prev {
                top: -45px;
                right: 40px;
                left: auto;

                &:after {
                    content: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='23' height='24' viewBox='0 0 23 24' fill='none'%3e%3cpath d='M22.9974 13.1337L22.9974 10.9544L1.20468 10.9544L1.20468 13.1337L22.9974 13.1337Z' fill='currentColor'/%3e%3cpath d='M1.53987 13.585L13.0972 2.0277L11.5562 0.486723L-0.00110835 12.0441L1.53987 13.585Z' fill='currentColor'/%3e%3cpath d='M11.5562 23.6014L13.0972 22.0604L1.53987 10.5031L-0.00110835 12.0441L11.5562 23.6014Z' fill='currentColor'/%3e%3c/svg%3e");
                    height: 40px;
                    width: 40px;
                    display: block;
                }
            }
        }
    }
}

// -------------------------------------
// Instafeed Style Overrides
// -------------------------------------
#insta-feed {
    padding-top: gap(3);

    @include bp(laptop) {
        padding-top: gap(5);
    }

    h2 {
        margin: 0 $grid-gutter--mobile gap(3);

        @include bp(laptop) {
            margin: 0 $grid-gutter gap(3);
        }
    }
}

.instafeed-lightbox {
    z-index: $z-index--lightbox !important;
}

// -------------------------------------
// Okendo review widget Style Overrides
// -------------------------------------
.app-wrapper .oke-w {
    --oke-title-fontWeight: 400;
    --oke-title-fontSize: var(--oke-text-regular, 18px);
    --oke-title-fontFamily: inherit;
    --oke-bodyText-fontWeight: 300;
    --oke-bodyText-fontSize: var(--oke-text-regular, 16px);
    --oke-bodyText-fontFamily: inherit;
    --oke-stars-foregroundColor: #1C2646;
    --oke-stars-backgroundColor: #E5E5E5;
    --oke-stars-borderWidth: 0;
    --oke-button-borderColor: transparent;
    --oke-button-borderWidth: 1px;
    --oke-button-backgroundColor: #1C2646;
    --oke-attributeBar-shadingColor: #1C2646;
    --oke-highlightColor: #1C2646;
    --oke-attributeBar-markerColor: #1C2646;
    --oke-avatar-backgroundColor: #fff;
    --oke-text-secondaryColor: #000;
}

.okeReviews[data-oke-container] .oke-w-breakdownModule-distribution-bar, div.okeReviews .oke-w-breakdownModule-distribution-bar {
    background-color: $dark-grey !important;
}

.okeReviews[data-oke-container] a, div.okeReviews a,
.okeReviews[data-oke-container] .oke-w-selectAttr-item-title, div.okeReviews .oke-w-selectAttr-item-title,
.okeReviews[data-oke-container] .oke-w-reviewer-name, .okeReviews[data-oke-container] .oke-w-reviewer-verified, div.okeReviews .oke-w-reviewer-name, div.okeReviews .oke-w-reviewer-verified,
.okeReviews[data-oke-container] .oke-w-navBar-item, div.okeReviews .oke-w-navBar-item {
    font-weight: 400 !important;
}

.oke-w-recommendsModule-value,
.oke-w-ratingAverageModule-rating {
    font-weight: 300 !important;
}
