// ----------------------------------------------------------------------------
// Animations
// ----------------------------------------------------------------------------
//
// Stores all iamota animation variation for reference into individual components.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Variables -- Animation Variables
// -------------------------------------
:root {
    --animation-delay: 100ms;
    --animation-timing: 600ms;
    --animation-ease: cubic-bezier(.59,.28,.39,.83);
    --animation-loop: 1;
    --animation-type: transform;
    --animation-transform: 0,0;
    --animation-target: all;
}


// -------------------------------------
//  Default -- Fade In
// -------------------------------------
.animate-in {
    //opacity: 0;
    transform: translate(var(--animation-transform));
    transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);
    will-change: var(--animation-type);

    // -------------------------------------
    //  Animation -- Slide Up
    // -------------------------------------
    &.animate-in--slide-up {
        --animation-delay: 0ms;
        --animation-timing: 800ms;
        --animation-transform: 0, 50%;
        --animation-type: all;

        transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);
    }

    // -------------------------------------
    //  Animation -- Slide Left
    // -------------------------------------
    &.animate-in--slide-left {
        --animation-transform: -100%, 0;
        --animation-type: all;
        --animation-timing: 300ms;

        transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);
    }

    // -------------------------------------
    //  Animation -- Slide Left
    // -------------------------------------
    &.animate-in--slide-right {
        --animation-transform: 100px, 0;
        --animation-type: all;
        // transform: translate(var(--animation-transform));
        transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);
        // will-change: var(--animation-type);
    }

    &.animate-in--content-panel {
        --animation-transform: -100%, 0;
        --animation-type: all;
        --animation-timing: 1000ms;

        transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);

        .align-left & {
            --animation-transform: 100%, 0;
        }
    }

    &.is-animated,
    &.already-visible {
        opacity: 1;
        transform: translate(0,0);
    }
}

.animate-in--scale-down {
    transform: scale(1.2);
    transform-origin: center center;
    transition: var(--animation-timing) var(--animation-type) var(--animation-delay) var(--animation-ease);
    will-change: var(--animation-type);

    .is-animated &,
    .already-visible & {
        opacity: 1;
        transform: scale(1);
    }
}


// -------------------------------------
//  Animation -- List Delay
// -------------------------------------
@for $i from 1 through 5 {
    .animate-in--list-delay:nth-child(#{$i}n) {
        --animation-delay: #{$i * 200}ms;
        --animation-transform: 0, 100px;
        --animation-type: transform, opacity;
        opacity: 0;
        transform: translate(var(--animation-transform));
        transition: 800ms transform var(--animation-delay) var(--animation-ease), 800ms opacity var(--animation-delay) var(--animation-ease);
        will-change: var(--animation-type);

        .is-animated &,
        .already-visible & {
            opacity: 1;
            transform: translate(0,0);
        }
    }
}
