// ----------------------------------------------------------------------------
// Rebuy templates
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

#rebuy-cart {
    &.rebuy-cart {
        .rebuy-cart__shipping-and-pickup {
            margin-bottom: 15px;
            color: #6B6B6B !important;
            @include font-primary;
            font-weight: 400;
        }

        .rebuy-cart__flyout-subtotal {
            margin-bottom: 5px;
        }

        .rebuy-cart__pickup-only {
            display: flex;
            align-items: center;
            gap: 2px;
            @include font-caption;
            font-weight: 400;
            margin-top: 8px;
            display: none;

            .pickup-only__icon {
                width: 18px;
            }
        }
    }
}
