// ----------------------------------------------------------------------------
// Modal
// ----------------------------------------------------------------------------
//
// A modal, or pop-up, that appears when the user clicks on an element.
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Modal - Variables
// -------------------------------------
$modal-background-color: $white !default;
$modal-color: $primary-text-color !default;
$modal-overlay-background-color: $black !default;
$modal-overlay-background-opacity: .75 !default;
$modal-padding: $grid-gutter !default;
$modal-z-index: $z-index--lightbox !default;


// -------------------------------------
// Modal - Base
// -------------------------------------


// -------------------------------------
// Default
// -------------------------------------
.modal {
    display: inline-block;
    opacity: 0;
    z-index: -100;
    transition: $anim;
    background: pink;
    position: relative;

    .modal__overlay {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        background: $modal-overlay-background-color;
        transition: $anim;
    }
    &.modal--open {
        z-index: $modal-z-index;
        opacity: 1;
        transition: $anim;

        .modal__overlay {
            position: fixed;
            z-index: calc(#{$modal-z-index} - 1);
            opacity: $modal-overlay-background-opacity;
            transition: $anim;
        }
    }
}

.modal__content {
    display: flex;
    position: fixed;
    color: $modal-color;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: $modal-background-color;
    padding: $modal-padding;
    min-width: calc(100% - #{$grid-gutter});
    z-index: $modal-z-index;
    @include bp(tablet) {
        padding: $modal-padding * 2;
        min-width: initial;
    }
}

.modal__close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    padding: 0;
    background: transparent;
    cursor: pointer;
}

.modal__trigger {
    cursor: pointer;
}


// -----------------------------------
//  Modal -- Cart Confirmation
// -----------------------------------
.modal--cart-confirmation {
    .modal__content {
        flex-direction: column;
        padding: $grid-gutter--mobile;

        @include bp(tablet) {
            padding: gap(2) gap(4) gap(4.5);
            min-width: 400px;
        }

        @include bp(laptop) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: auto auto;
            min-width: 800px;
        }
    }

    .modal__close-button {
        top: 20px;
        right: 20px;
    }

    .button__edit-cart {

        &:focus-visible {
            color: $white;
        }
    }
}



// ---------------------------------------
//  Modal -- Video Modal for Content Panel
// ---------------------------------------
.modal--video-modal {
    display: block;

    &.modal--open  {
        .modal__popup {
            display: block;
        }

        .modal__overlay {
            opacity: 0.95;
        }
    }

    .modal__close-button {
        top: -50px;
        right: -20px;
        background-color: transparent;
        border: 0;

        &:hover {
            color: $white;
            background-color: transparent;
        }
    }

    .modal__popup {
        display: none;

        .modal__content {
            width: calc(90% - 80px);
            max-width: 1024px;
            min-width: initial;
            height: auto;
            background: transparent;
            padding: 0;

            @include bp(laptop) {
                width: calc(100% - 180px);
            }

            .video-block {
                background: transparent;
                background-image: none;
                background-size: cover !important;
            }

            .video-block__video {
                padding-bottom: 56.25%;
                background-color: transparent;
            }

            .button--primary {
                position: absolute;
                top: -57px;
                right: -30px;
                color: $white;
                background-color: transparent;
                border: none;
                padding: gap(1);

                @include bp(laptop) {
                    padding: gap(2) gap(3);
                }
            }

            .video-block {
                min-height: initial;
            }

            iframe {
                background: $black;
            }
        }
    }
}
