@import 'init';

// Variables
$content-slider-height: 662px;
$single-slide-height: 757px;

.page-section--content-slider {
    overflow-x: hidden;
    overflow-x: clip;
}

.content-slider {

    .section-title {
        margin: 0;
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: gap(3);
        }
    }

    .swiper-slide {
        height: auto;
    }

    .content-slider__slide {
        height: 100%;
        display: flex;
        flex-direction: column;

        @include bp(laptop) {
            max-height: $content-slider-height;

            &.single-slide {
                max-height: $single-slide-height;
            }

            .content-slider__image img {
                max-height: $content-slider-height;
            }
            .content-slider__image--single-slide img {
                max-height: $single-slide-height;
            }
        }

        .content-slider__content {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            padding: 18.84px 14.13px;
            background: $white;
            gap: gap(1.13);
            height: 100%;

            @include bp(desktop) {
                display: grid;
                grid-template-columns: 1fr auto;
                grid-template-rows: auto 1fr;
                gap: 0;
                padding: 20px 26.22px;
            }

            .content-slider__description {
                margin-bottom: gap(1.13);
                margin-top: gap(1.13);

                @include bp(laptop) {
                    // max-width: 350.426px;
                    max-width: 450px;
                }

                @include bp(desktop) {
                    grid-row: 2;
                    grid-column: 1;
                }
            }

            .content-slider__title {
                margin: 0;
                max-width: 450px;

                @include bp(desktop) {
                    grid-row: 1;
                    grid-column: 1;
                }
            }

            .button {
                padding: 0;

                @include bp(desktop) {
                    grid-row: 1;
                    grid-column: 2;
                    align-self: center;
                    margin-right: 13.11px;
                }
            }
        }



        @include bp(laptop) {
            display: grid;
            grid-template-columns: 49.47% 1fr;
            grid-template-rows: 48.34% 1fr;

            .content-slider__image {
                grid-column: 1;
                grid-row: 1 / span 2;
            }

            .content-slider__image--small {
                grid-column: 2;
                grid-row: 1;
            }

            .content-slider__content {
                grid-column: 2;
                grid-row: 2;
            }
        }
    }



    // Swiper Styles

    .swiper {
        overflow: visible;
    }

    .swiper-wrapper {
        padding: 0;
        margin: 0;
        .swiper-slide {
            list-style-type: none;

            @include bp(desktop) {
                width: 1171px;
            }
        }
    }

    // Swiper Controllers - Counter and Arrows
    .swiper-controllers {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: gap(2);

        @include bp(laptop) {
            margin-top: gap(3);
        }
    }

    .swiper-counter {
        @include font-size(12);
        font-weight: 400;
        line-height: normal;
        letter-spacing: 0.5px;
    }

    .swiper-arrow__container {
        display: flex;
        justify-content: space-between;
        gap: 34px;
        .swiper-arrow {
            position: static;
            margin-top: 0;
            border: 0;
            color: $black;
            height: 24px;
            width: 24px;

            @include font-size(24);

            display: flex;
            align-items: center;
            justify-content: center;


            &:after {
                display: none;
            }

            &.swiper-button-next {
                .icon {
                    position: relative;
                    right: 12px;

                    @include bp(laptop) {
                        right: 0;
                    }
                }
            }

            &.swiper-button-lock {
                display: none;
            }

            &.swiper-button-next.swiper-button-disabled, &.swiper-button-prev.swiper-button-disabled {
                color: $grey;
                opacity: 1;
            }
        }

    }

}
