// Base Swatch styling
// ----------------------------------------------------------------------------
//
// Sizes
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.swatch__option {
    input[value='Extra Small'] + label .form__option-label-inner:after {
        content: "XS";
    }
    input[value='Small'] + label .form__option-label-inner:after {
        content: "S";
    }
    input[value='Medium'] + label .form__option-label-inner:after {
        content: "M";
    }
    input[value='Large'] + label .form__option-label-inner:after {
        content: "L";
    }
    input[value='Extra Large'] + label .form__option-label-inner:after {
        content: "XL";
    }


    input[value='Extra Small'], input[value='Small'], input[value='Medium'], input[value='Large'], input[value='Extra Large'] {
        & + label .form__option-label-visually-hidden {
            border: 0;
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            width: 1px;
        }
    }


    input[name='options[Denominations]'] + .form__option-label {
        width: 60px;
    }
}

.swatch__title {
    order: -1;
    width: 100%;
    padding: 0 5px;
    margin-bottom: 10px;
    color: $primary-text-color;
    line-height: 24px;

    &:last-of-type {
        margin-bottom: 5px;
    }
}

.swatch__title .swatch__subtitle.show-swatch {
    display: inline;
}

.form__field--radio-text {
    .swatch--disabled {
        color: $border-color--light;

        &::after {
            content: "";
            background: lightgray;
            width: 1px;
            height: 136%;
            transform: translate(-50%,-50%) rotate(45deg);
            position: absolute;
            top: 50%;
            left: 50%;
        }
    }

}

.form__field--radio-color {
    .swatch--disabled {
        .form__option-label-inner {
            opacity: 0.5;
        }
    }
    .swatch--disabled:after {
        content: "";
        background: $black;
        width: 2px;
        height: 100%;
        transform: translate(-50%, -50%) rotate(45deg);
        position: absolute;
        top: 50%;
        left: 50%;
    }
    .form__option-label {
        border: 1px solid lightgray;
    }
}
