// ----------------------------------------------------------------------------
// Collection
// ----------------------------------------------------------------------------
//
// A page template which displays a collection of products.
//
// ----------------------------------------------------------------------------

// Styles
@import 'init';

.collection-grid-container {
    @include bp(laptop) {
        display: grid;
        grid-template-rows: auto 1fr;
        grid-template-columns: auto 1fr;
        row-gap: gap(1.5);
        column-gap: gap(3.5);
    }

    .sort__wrapper {
        grid-row: 1;
        grid-column: 1/-1;
        display: flex;
        justify-content: right;

        &.desktop-only {
            @include bp(laptop) {
                display: flex;
            }
        }
    }

    .filters-sort__wrapper {
        grid-row: 2;
        grid-column: 1;

        @include bp(laptop) {
            min-width: 250px;
            max-width: 300px;
        }
    }

    .product-grid {
        padding: 0;
        margin: 0;
        gap: gap(3) gap(1.5);

        @include bp(laptop) {
            margin-top: gap(2.5);
        }
    }
}
