// ----------------------------------------------------------------------------
// Breadcrumbs
// ----------------------------------------------------------------------------
//
// Breadcrumb navigation provides links to each previous page through which the
// user navigated, as well as their current location in the website. This is,
// essentially, a small menu, so it uses the .menu default classes with a
// wrapper class .breadcrumbs for positioning it in relation to other blocks.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Menu -- Breadcrumbs
// -------------------------------------
.menu--breadcrumbs {
    .menu__list {
        display: flex;
        flex-flow: row wrap;
        align-items: baseline;

        @include bp(tablet) {
            flex-flow: row nowrap;
        }
    }

    .menu__item,
    .menu__link {
        @include font-default;
        @include font-size(16);
        @include line-height(24);

        &:hover {
            color: $primary-color;
        }
    }

    .menu__item--separator {
        margin: 0;
        margin-right: 5px;
        display: flex;
        align-items: center;
        position: relative;
        bottom: -2px;

        .icon {
            width: 5px;
        }
    }
}


.template-product,
.template-article {
    .menu--breadcrumbs {
        margin-top: gap(2);
    }
}
