// Snippet - Specs Table
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$tooltip-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);


.toggle-container {
    position: relative;
    @include bp(tablet) {
        vertical-align: middle;
        display: inline-flex;
    }
}

.toggle-tooltip {
    position: absolute;
    z-index: 666;
    padding: 0;
    box-shadow: $tooltip-shadow;
    border-radius: 2px;
    border: $border--light;
    bottom: auto;
    top: 25px;
    left: -100px;
    width: calc(100vw - 40px);
    max-width: 200px;
    right: 0;
    transform: none;

    &::before {
        content: '';
        position: absolute;
        bottom: auto;
        top: -10px;
        left: 86px;
        transform: translate(24px,5px) rotate(45deg);
        background: white;
        width: 10px;
        height: 10px;
        z-index: -1;
        box-shadow: $tooltip-shadow;
    }


    .toggle-tooltip__wrapper {
        background: $white;
        border-radius: 2px;
        padding: 20px 14px 20px 25px;
        text-align: left;
    }


    .toggle-tooltip__title {
        margin: 0;
        line-height: 1;
    }

    .toggle-tooltip__close {
        float: right;
        border: 0;
        transform: translate(3px, -13px);
        padding: 0;
        background-color: transparent;

        img {
            width: 9px;
        }
    }


    .toggle-tooltip__content {
        p {
            font-weight: 400;
            margin-bottom: 0;

            @include bp(laptop) {
                @include font-size(16);
            }
        }
    }
}

.button--tooltip {
    background-color: transparent;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;

    svg {
        pointer-events: none;
        width: 13px;

        @include bp(laptop) {
            width: 15px;
        }
    }
}
