// ----------------------------------------------------------------------------
//  Intro Block
// ----------------------------------------------------------------------------
//
// Minor component that may include a logo or link, includes a title and content
// - two styles, one with a logo the other without
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Section Intro - Default
// -------------------------------------

.intro-block__title {
    margin-bottom: 0;
}


// -------------------------------------
// Section Intro - Brand/Default
// -------------------------------------

.intro-block {

    .intro-block__container {
        max-width: 1920px;
        margin: auto;
        padding: gap(5) gap(1.5);
        @include bp(tablet) {
            padding: gap(6) gap(10);
        }
    }

    .intro-block--icon-on {
        @include bp(tablet) {
            padding: gap(6) gap(10) gap(9);
        }
    }

    .intro-block--navy {
        background-color: $primary-color;
        color: $white;
    }

    .intro-block--shoreline {
        background-color: $background-color--base;
        color: $primary-color;
    }


    .intro-block__icon {
        display: block;
        margin: 0 auto 30px;
        font-size: 30px;

        @include bp(tablet) {
            font-size: 40px;
        }
    }

    .intro-block__content {
        text-align: center;

        @include bp(tablet) {
            flex: 0 1 60%;
        }

        &.intro-block__content--left {
            text-align: left;
        }

        &.intro-block__content--center {
            text-align: center;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
        }
    }

    .intro-block__body {
        margin: gap(1) 0 0;
    }

    .button {
        margin: gap(4) 0 0 0;
    }
}
