// ----------------------------------------------------------------------------
// Pages
// ----------------------------------------------------------------------------
//
// Add generic page template styles here.
// To be used for page level styles overrides, not section/snippet level styles.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.page-section--page {
    .container {
        width: 100%;
        max-width: 100%;
        padding: 0;

        @include bp(laptop) {
            @include container--wysiwyg;
            margin: 0;
            padding: 0;
        }

        h2:first-of-type,
        h3:first-of-type {
            margin-top: 0;
        }
    }

    .page-header--text {
        margin-bottom: gap(3);
    }
}

// ----------------------------
// Shopify Policies Page
// ----------------------------
.shopify-policy__container {
    margin: 80px 0 auto !important;
    padding: 40px 0 !important;

    h1 {
        margin-top: 0;
        text-align: left;
    }
}

body .uwy,
body .uwy .uai img, body .uwy .ulsti img, body .uwy.uon .uwif,
body .uw-sl {
    z-index: $z-index--lightbox !important;
}
body .uwy .uai, body .uwy .ulsti {
    width: 30px !important;
    height: 30px !important;
}
