// ----------------------------------------------------------------------------
// HTML Tags
// ----------------------------------------------------------------------------
//
// Styles for the base HTML tags
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// HTML Tag Styles
// -------------------------------------

html {
    font-size: 62.5%;
}

body {
    @include font-primary;
    font-size: 1.6rem;
}

body,
input,
textarea,
button,
select {
    -webkit-font-smoothing: antialiased;
    text-size-adjust: 100%;
}

p {
    margin: 0;
    margin-bottom: gap(2);

    &:last-of-type {
        margin-bottom: 0;
    }
}

a {
    @include font-medium;
    color: $primary-text-color;

    &:hover {
        color: $secondary-color;
        transition: $anim;
    }

    &:focus-visible {
        color: inherit;
        outline: $focus-state;
    }
}

small {
    @include font-caption;
}

// Lists
ul,
ol,
dl {
    @include font-primary;
}



// -------------------------------------
// Headings
// -------------------------------------

@include headings(1, 6) {
    @include font-header;
}


h1,
.h1 {
    @include responsive-fontsize(32, 40, 54, 67);
    font-weight: 300;
}

h2,
.h2 {
    @include responsive-fontsize(28, 35, 44, 55);
    font-weight: 300;
}

h3,
.h3 {
    @include responsive-fontsize(24, 30, 34, 42);
    font-weight: 300;
}

h4,
.h4 {
    @include responsive-fontsize(20, 25, 24, 30);
    font-weight: 400;

    @include bp(laptop) {
        font-weight: 300;
    }
}

h5,
.h5 {
     @include responsive-fontsize(18, 22, 20, 25);
     font-weight: 400;
}

h6,
.h6 {
    @include font-size(18);
    font-weight: 400;
}


// -------------------------------------
// Additional Custom Classes
// -------------------------------------

// Example
.text--caption {
    @include font-caption;
}
