// ----------------------------------------------------------------------------
// Background Media
// ----------------------------------------------------------------------------
//
// Container component that allows media to be placed as the background of
// component sections.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$background-image-background-color: $background-color !default;
$background-image-overlay-color: rgba($black, .2);


// -------------------------------------
// Background Media
// -------------------------------------
.background-media {
    display: grid;
    grid-gap: 0;
    overflow: hidden;
}

.background-image {
    background-color: $background-image-background-color;
    object-fit: cover;
    object-position: center center;
    width: 100%;
    height: 100%;
    outline: none;
}

.background-media__background {
    -ms-grid-column: 1;
    -ms-grid-column-span: 5;
    -ms-grid-row: 1;
    -ms-grid-row-span: 4;
    grid-column: gutter-start / gutter-end;
    grid-row: image-start / image-end;
    z-index: -1;
    height: 100%;
}

.background-media__content {
    grid-column: content;
    grid-row: content;
    z-index: 1;

     @include bp(laptop) {
        grid-column: center;
        grid-row: content;
    }
}

.background-media__content--left {
    grid-column: content;
    grid-row: content;

    @include bp(laptop) {
        grid-column: content-start;
        grid-row: content;
    }
}

.background-media__content--right {
    grid-column: content;
    grid-row: content;

    @include bp(laptop) {
        grid-column: 3;
        grid-row: content;
    }
}

.background-media__overlay {
    -ms-grid-column: 1;
    -ms-grid-column-span: 5;
    -ms-grid-row: 1;
    -ms-grid-row-span: 4;
    grid-column: gutter-start / gutter-end;
    grid-row: image-start / image-end;
    z-index: 0;
    background-color: $background-image-overlay-color;
}


// -------------------------------------
// Background Images
// -------------------------------------
.background-media--image {
    grid-template-columns: [gutter-start] 20px [content] 1fr 20px [gutter-end];
    grid-template-rows: [image-start] minmax(20px, 1fr) [content] auto minmax(20px, 1fr) [image-end];

    @include bp(laptop) {
        grid-template-columns: [gutter-start] minmax(#{$grid-gutter}, 1fr) [content-start] minmax(0, #{$max-width*0.5}) [center] auto [content-end] minmax(0, #{$max-width*0.5}) minmax(#{$grid-gutter}, 1fr) [gutter-end];
        grid-template-rows: [image-start] minmax(60px, 1fr) [content] auto minmax(60px, 1fr) [image-end];
    }
}

// -------------------------------------
// Background Video
// -------------------------------------
.background-media--video {
    grid-template-columns: [gutter-start] 20px [content] 1fr 20px [gutter-end];
    grid-template-rows: [image-start] minmax(0,1fr) [content] auto 60px [image-end];

    @include bp(laptop) {
        grid-template-columns: [gutter-start] minmax(#{$grid-gutter}, 1fr) [content-start] minmax(0, #{$max-width*0.5}) [center] auto  minmax(0, #{$max-width*0.5}) [content-end] minmax(#{$grid-gutter}, 1fr) [gutter-end];
        grid-template-rows: [image-start] minmax(0,1fr) [content] auto 80px [image-end];
    }

    .video-block {
        -ms-grid-column: 1;
        -ms-grid-column-span: 5;
        -ms-grid-row: 1;
        -ms-grid-row-span: 4;
        grid-column: gutter-start / gutter-end;
        grid-row: image-start / image-end;
    }

    .background-media__content {
        grid-column: content;
        z-index: 1;

         @include bp(laptop) {
            grid-column: content-start / content-end;
            grid-row: content;
        }
    }
}

// -------------------------------------
// Background Focal Point
// -------------------------------------
.background-image {
    &.background-image--top-center {
        object-position: center top;
    }
    &.background-image--top-left {
        object-position: left top;
    }
    &.background-image--top-right {
        object-position: right top;
    }
    &.background-image--center {
        object-position: center center;
    }
    &.background-image--left {
        object-position: left center;
    }
    &.background-image--right {
        object-position: right center;
    }
    &.background-image--bottom-center {
        object-position: center bottom;
    }
    &.background-image--bottom-left {
        object-position: left bottom;
    }
    &.background-image--bottom-right {
        object-position: right bottom;
    }
}
