// ----------------------------------------------------------------------------
// Menu -- Header
// ----------------------------------------------------------------------------
//
// Navigation for the site - inherits from menu.
//
// The mobile menu can be one of two styles:
//
//   - "Slide": Top-level menu items with children slide to a full-height
//     display with children and promo content. Second-level menu items with
//     children are accordion-style.
//   - "Accordion": All levels are accordion-style.
//
// The desktop menu is a hover-style dropdown with a column display for three
// level menu items.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$menu-background-color: $white !default;
$menu-text-color: $primary-text-color !default;
$menu-text-hover-color: $primary-text-color;
$menu-margin-top: gap(3);
$menu-transition: 200ms all .2s ease, visibility 0s linear .01s, z-index 0s linear 0s !default;

$mobile-menu-grandchild-list-bg-color: $background-color--base;
$mobile-menu-item-padding: gap(2) 0;
$mobile-child-menu-item-padding: gap(1.5) gap(1.5) gap(1.5) gap(4);
$mobile-grandchild-menu-item-padding: gap(1.5) gap(3) gap(5.5);
$mobile-menu-item-button-right-space: 0;
$mobile-menu-back-padding: gap(2) 0 gap(2.4);
$mobile-menu-back-border-color: $background-color--base;

$desktop-dropdown-background-color: $background-color--white;
$desktop-menu-item-padding-tb: gap(2.8);
$desktop-menu-item-padding-lr: gap(1.5);
$desktop-menu-dropdown-padding: gap(2);
$desktop-child-menu-item-padding: 0 0 gap(3);
$desktop-grandchild-menu-item-padding: 0 0 gap(1.5);


// -------------------------------------
// General styles
// -------------------------------------

.header-menu {
    //margin-top: $menu-margin-top;

    @include bp(laptop) {
        margin-top: 0;
        justify-self: center;
    }
}

.header-menu__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.header-menu__list--level-2 {
    display: none;
}

.header-menu__list--level-3 {
    display: none;
}

.header-menu__link {
    @include font-navigation;
    display: block;
    color: $menu-text-color;
    text-decoration: none;
    font-weight: 400;

    &:hover {
        color: $menu-text-hover-color;
        text-decoration: underline;

        .promo__title {
            color: $menu-text-color;

        }

        .promo__cta {
            color: $menu-text-hover-color;
        }
    }

    &.header-menu__link--promo {
        &:hover {
            text-decoration: none;
        }
    }

    &.header-menu__link--active {
        color: $menu-text-hover-color;
        text-decoration: underline;
    }

    .promo__title {
        @include font-size(16);
        font-weight: 400;
        margin-top: gap(1.5);
    }

    .promo__cta {
        @include font-size(16);
        margin-top: gap(0.5);
        font-weight: 300;
    }
}


// -------------------------------------
// Mobile - Accordion (default)
// -------------------------------------

.header-menu__list--mobile {
    @include bp(laptop) {
        display: none;
    }

    .header-menu__item--level-1 {
        > .header-menu__button, > .header-menu__link {
            @include font-size(20);
            font-weight: 400;
            text-transform: none;
            padding: gap(2) 0 gap(0.5);
        }
    }

    .header-menu__list--level-3 {
        background-color: $background-color--white;
    }

    .header-menu__item {
        width: 100%;

        &.js--is-open {
            > .header-menu__list {
                display: block;
            }

            > .header-menu__button .button__icon {
                transform: translateY(-50%) rotate(180deg);
            }
        }
    }

    .header-menu__item--level-2 {

        > .header-menu__button, > .header-menu__link {
            @include font-size(18);
            border-bottom: $border--light;
            padding: $mobile-child-menu-item-padding;
            text-transform: none;
            font-weight: 400;
        }

        &:last-of-type {
            > .header-menu__button, > .header-menu__link {
                border-bottom: 0;
            }
        }
    }

    .header-menu__item--level-3 {
        > .header-menu__button, > .header-menu__link {
            padding: $mobile-grandchild-menu-item-padding;
            font-weight: 300;
            @include font-size(14);
        }
    }

    .header-menu__item--promo {
        padding: 0;
    }

    .header-menu__button {
        background: none;
        width: 100%;
        padding: $mobile-menu-item-padding;
        @include font-primary;
        @include font-size(16);
        @include line-height(26);
        text-align: left;
        position: relative;

        .button__icon {
            @include font-size(19);
            @include line-height(19);
            position: absolute;
            right: $mobile-menu-item-button-right-space;
            top: 50%;
            transform: translateY(-50%);
            transform-origin: center center;
            transition: $anim;
        }

        &.header-menu__button--active {
            color: $menu-text-hover-color;
            text-decoration: underline;
        }
    }

    .header-menu__link {
        padding: $mobile-menu-item-padding;
    }
}


// -------------------------------------
// Mobile - Slide
// -------------------------------------

.header__nav-wrapper.mobile-slide .header-menu__list--mobile {
    &.header-menu__list--level-1 {
        position: relative;
        padding: 0 $grid-gutter--mobile;
    }

    .header-menu__list--level-2 {
        display: block;
        visibility: hidden;
        position: absolute;
        top: 0px;
        left: 0;
        right: 0;
        height: calc(100vh - 80px);
        width: 100%;
        transform: translate(100%, 0);
        transform-origin: center center;
        will-change: transform;
        transition: $anim;
        z-index: 1;
        background-color: $background-color--base;
        overflow-y: visible;
        padding: 0 $grid-gutter--mobile;
    }

    .header-menu__list--level-3 {
        margin: 0 -15px;
        padding: 15px 0;
    }

    .header-menu__item--level-1 {
        > .header-menu__button .button__icon {
            transform: translateY(-50%) rotate(-90deg);
        }
    }

    .header-menu__item--level-2 {
        > .header-menu__button, > .header-menu__link {
            padding: $mobile-menu-item-padding;
        }
    }

    .header-menu__item--level-3 {
        > .header-menu__button, > .header-menu__link {
            padding: $mobile-child-menu-item-padding;
        }
    }

    .header-menu__item {
        &.js--is-open {
            .header-menu__list--level-2 {
                visibility: visible;
                transform: translate(0, 0);
                overflow-y: auto;
            }

            &.header-menu__item--level-2 {
                .header-menu__button {
                    border-bottom-color: transparent;
                }
            }
        }
    }

    .header-menu__back {
        position: relative;
        border-bottom: $border--light;

        .back__button {
            position: absolute;
            top: 0;
            bottom: 0;
            height: 100%;
            text-align: center;
            padding: $mobile-menu-back-padding;
            @include font-size(20);
            font-weight: 400;
            transform: rotate(90deg);
            cursor: pointer;

            .icon {
                vertical-align: middle;
            }
        }

        .back__title {
            display: block;
            width: 100%;
            text-align: center;
            padding: $mobile-menu-back-padding;
            @include font-size(20);
            font-weight: 400;
        }
    }

    .header-menu__item--promo {
        padding: 0;
    }

    .header__featured-menu {
        background-color: $background-color;
        padding: gap(5) $grid-gutter--mobile gap(10);
        margin: 0 -15px;

        .header-menu__link {
            padding: 0 0 gap(2.5);
            border-bottom: 0;
        }

        .header-item__image {
            margin-bottom: gap(0.5);
        }

        .header-menu__item--level-3 {
            .header-menu__link {
                padding: 0 0 gap(2);
            }
        }

    }
}


// -------------------------------------
// Desktop
// -------------------------------------

.header-menu__list--desktop {
    display: none;

    @include bp(laptop) {
        display: flex;
    }

    .header-menu__list--level-2 {
        display: block;
        position: absolute;
        top: 100%;
        background-color: $desktop-dropdown-background-color;
        padding: gap(2) gap(2) 0;
        margin-left: -($desktop-menu-item-padding-lr);
        visibility: hidden;
        opacity: 0;
        //transition: $anim;

        &.is-mega {
            display: grid;
            grid-template-columns: [full-start] minmax(#{$grid-gutter}, 1fr) [main-start] minmax(0, 1820px) [main-end] minmax(#{$grid-gutter}, 1fr) [full-end];
            left: 0;
            width: 100%;
            margin-left: 0;

            padding: gap(4) 0;

            .header-menu_columns {
                grid-column: main;
                grid-template-columns: 2fr 1fr 1fr 1fr;
                grid-gap: 15px;
            }

            .header-menu__item {
                &.header-menu__item--level-2 {
                    padding: $desktop-child-menu-item-padding;
                }
            }
        }

        &:not(.is-mega) {
            .header-menu__item {
                &.header-menu__item--level-2 {
                    > .header-menu__link {
                        @include font-size(15);
                    }
                }
            }
        }
    }

    .header-menu__item {
        padding: $desktop-menu-item-padding-tb $desktop-menu-item-padding-lr;

        &.header-menu__item--level-1 {
            &.js--is-open {
                &:after {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 30px;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                }
            }

            > .header-menu__link,
            > .header-menu__button {
                color: var(--header-text-color);



                &:hover {
                    text-decoration: none;
                }
            }
        }

        &.header-menu__item--level-2 {
            padding: 0 0 gap(2);

            > .header-menu__link {
                @include font-size(20);
            }
        }

        &.header-menu__item--level-3 {
            padding: $desktop-grandchild-menu-item-padding;
        }

        &.js--is-open {
            .header-menu__list--level-2, .header-menu__list--level-3 {
                opacity: 1;
                visibility: visible;
            }

            .header-menu__list--level-3 {
                display: block;
            }

            .header-menu__button .button__icon {
                transform: rotate(180deg);
            }

            .header-menu__button {
                text-decoration: underline;
            }
        }
    }

    .header-menu__link {
        display: inline-block;
    }

    .header-menu__button {
        @include font-size(12);
        background: none;
        padding: 0;
        text-transform: none;

        .button__icon {
            display: flex;
            align-items: center;
            transform-origin: center center;
        }

        &.header-menu__button--active {
            color: $menu-text-hover-color;
            text-decoration: underline;
        }
    }

    .header-menu_columns {
        display: grid;
        grid-template-columns: 3fr 1fr;

        .header-menu__column--links.multi-level {
            display: grid;
            grid-template-columns: repeat(3, 1fr);

            .header-menu__item--level-2 > .header-menu__link {
                border-bottom: $border--light;
                font-weight: 400;
                margin-bottom: 24px;
                padding-bottom: 21px;
                width: 100%;

                &:hover {
                    text-decoration: none;
                }

                &.header-menu__link--active {
                    text-decoration: underline;

                    &:hover {
                    }
                }
            }
        }

        .header__featured-menu {
            display: grid;
            grid-template-columns: 1fr 1fr;
            background-color: $background-color;


            .header-menu__item.header-menu__item--level-2 {
                padding: gap(4) gap(5);


                & > .header-menu__link {
                    border-bottom: $border--light;
                    font-weight: 400;
                    margin-bottom: 25px;
                    padding-bottom: 25px;
                    width: 100%;

                    &:hover {
                        text-decoration: none;
                    }
                }
            }

            .header-menu__item--level-3 {
                padding: 0;

                .header-menu__link {
                    padding-bottom: 15px;
                }
            }

            .featured-menu__image {
                display: grid;
                grid-template-columns: auto;
                grid-template-rows: auto;
            }

            .featured-menu__image-wrapper {
                grid-column: 1;
                grid-row: 1;
                z-index: 1;
                aspect-ratio: 3 / 4;
            }

            .featured-menu__collection-image {
                grid-column: 1;
                grid-row: 1;
                z-index: -1;
                aspect-ratio: 3 / 4;

                img {
                    width: 100%;
                }
            }
        }
    }

    .header-menu__promo {
        grid-column: 2 / span 3;
        display: flex;
        justify-content: end;
        gap: 15px;

        .header-menu__item--promo {
            width: calc(100% / 3);
            padding: 0;
        }
    }


}


#women-menu,
#men-menu {
    padding: 0;

    .header-menu_columns {
        grid-column: main-start / full-end;
        grid-template-columns: 1fr 1fr;

        & > .header-menu__column--links {
            padding-right: gap(5);
        }
    }

    .header-menu__column {
        padding: gap(4) 0;
        grid-auto-flow: column;
        grid-gap: 80px;

        .header-menu__item--level-2:first-of-type {
            grid-column: span 3;
        }

        .header-menu__item--level-2:nth-of-type(2) {
            grid-column: span 1;
        }

        .header-menu__item--level-2:first-of-type {
            .header-menu__list--level-3 {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
            }
        }

        .header-menu__item--level-2:only-of-type {
            .header-menu__list--level-3 {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }
}

