// ----------------------------------------------------------------------------
// Account Details
// ----------------------------------------------------------------------------
//
// For Page template within Account screen including  order details
// and order table and account address
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Default Elements
// -------------------------------------
.page-section--account-details {
    .address {
        margin: gap(2) 0;
    }
}

.order-table__container {
    overflow: auto;
    padding-bottom: 30px;

    @include bp(laptop) {
        padding-bottom: 0;
    }
}

.account-details__title {
    margin-top: 0;
}

.account__wishlist {
    display: inline-flex;
    gap: gap(.5);
    margin-left: gap(1.5);
}

.form-success {
    padding-bottom: gap(3);
}

.breadcrumbs-container {
    margin-bottom: gap(2);
}

.button__log-out {
    margin-top: gap(2);
}

.form__cancel-link {
    display: block;
    margin-top: gap(2);
}


// -------------------------------------
// Order Table Styles
// -------------------------------------
.order-table__table {
    width: 100%;
    border-collapse: collapse;

    th,
    td {
        border: 0;
    }

    tr:nth-child(2n) {
        background: $white;
    }
}

tr.order-table__header {
    border-bottom: $border--light;
}

.order-table__header-item {
    @include font-caption;
    color: $primary-text-color;
    text-align: left;
    padding: gap(1) 0 0;

    &:last-of-type {
        text-align: right;
        @include bp(tablet) {
            padding-right: 0;
        }
    }

    @include bp(tablet) {
        text-align: left;
        padding: gap(0) gap(1) gap(2) 0;
    }
}

td.order-table__item {
    padding: gap(1) 0 0;
    min-width: 100px;

    @include bp(tablet) {
        text-align: left;
        padding: gap(2) gap(.5);
    }

    &:last-of-type {
        text-align: right;

        padding: gap(1) 0 gap(1);

        @include bp(tablet) {
            padding: gap(2) 0;
        }
    }

    &:not(.order-table__item-price) {
        @include font-size(16);
    }
}

.order-table__row {
    border-top: $border--light;

    @include bp(tablet) {
        border-top: 0;
    }
}

.order-table__footer {
    .order-table__row {
        border-top: 0;
        width: 100%;

        &:first-of-type {
           border-top: $border--light;
        }

        & .small--hide {
            display: none;

            @include bp(tablet) {
                display: table-cell;
            }
        }

        .order-table__item {
            padding: gap(1) 0 0;

            @include bp(tablet) {
                padding: gap(2) 0 0;
            }
        }
    }
}

// -------------------------------------
// Address Styles -- Default
// -------------------------------------
.address {
    & p {
        margin-bottom: 0;
    }

    .address__title {
        margin-bottom: gap(.5);
    }

    .button-collection--address {
        align-items: center;
        margin-bottom: 0;
        margin-top: gap(2);

        .form__button {
            margin-right: gap(2);
        }
    }
}

#address_form_new {
    margin-top: gap(3);
}

.form--address-edit {
    margin-top: gap(2);
}

.page-section--account-details {
    margin: 0 0 30px 0;

    @include bp(laptop) {
        margin: 50px 0;
    }

    a {
        @include font-size(16);
    }
}

.template-addresses {
    .form__wrapper {
        margin-left: 0;
    }

    .button--link {
        @include font-medium;
        color: $primary-color;
        background-color: transparent;
        text-decoration: underline;
        padding: 0;
        margin: 0;
        opacity: 1;
        letter-spacing: 0;

        &:hover {
            color: $secondary-color;
            transition: $anim;
        }

    }

    .button--address-update {
        margin-right: gap(1);
    }
}

// -------------------------------------
// Order Page Styles
// -------------------------------------
.order-page__title {
    margin-top: 0;
    margin-bottom: gap(.5);
}

.order-page__subtitle {
    margin-bottom: gap(4);
}

.order-addresses {
    margin-bottom: 40px;
}

// -------------------------------------------------------------------
// Page Header Styles on Account related pages
// -------------------------------------------------------------------
.page-header--account-form {
    .page-header__content {
        padding-bottom: 0;
        text-align: center;
    }
}

.template-account {
    .page-header__title {
        margin-bottom: gap(3);
    }
}

.page-header--account {
    .page-header__content {
        padding-top: 100px;
        padding-bottom: 0px;
    }

}

.page-header--addresses {

    .page-header__content {
        padding-bottom: gap(2);
        padding-top: 100px;
    }

    .menu--breadcrumbs {
        .menu__item--separator {
            margin-left: 5px;
        }
    }
}

.form__register-link {
    display: block;
    margin-bottom: gap(1);
}

// -----------------------------------------------
// Add custom styles to reCaptcha Challenge page
// -----------------------------------------------

.shopify-challenge__container {
    padding: gap(6) 0;

    .shopify-challenge__error {
        padding-left: 0;
        @include font-size(14);
        @include line-height(19);
        color: $error-color;
        margin-bottom: 20px;
    }

    .shopify-challenge__button {
        @include font-size(14);
        font-weight: bold;
        padding: 19px 25px 17px;
        text-align: center;
        text-transform: uppercase;
        background-color: $primary-color;
        color: $white;
        margin-top: 30px;
    }
}


// -----------------------------------------------------------
// Add custom styles to page section divider on Account Page
// -----------------------------------------------------------
.page-section__divider {
    width: 100%;
    margin: 0;
    height: 1px;
    background-color: $border-color--light;
    border: none;
}




