// ------------------------------------------------------------------------------
// Locations Section
// ------------------------------------------------------------------------------
//
// This is a section that displays location cards
//
// ------------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';

// -------------------------------------
// Locations Page Styling
// -------------------------------------
.stockist-feature-color {color: #000 !important;} .stockist-feature-bg-color {background-color: #000 !important;}


.page-section--locations {
    .container {
        width: 100%;
        max-width: 100%;

        h2:first-of-type,
        h3:first-of-type {
            margin-top: 0;
        }
    }
    .container [data-stockist-widget-tag] {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        border: 1px solid currentColor;
        border-right-color: transparent;
        border-radius: 100%;
        width: 1em;
        height: 1em;
        animation: loading-spinner .5s linear infinite
    }

    @media(min-width: 768px) {
        .container {
            margin-bottom: 7.5rem
        }
    }

    .container .Page_Container {
        max-width: 100%
    }

    @media(min-width: 1200px) {
        .container .Page_Container {
            margin-top: -4rem
        }
    }

    @media(min-width: 1440px) {
        .container .Page_Container {
            padding: 0 9.6875rem
        }
    }

    #stockist-widget .stockist-result-list ul {font-size: 14px;}

    #stockist-widget .stockist-list-result .stockist-result-distance {
        width: 50px !important;
    }

    #stockist-widget .stockist-list-result>div {
        padding-left: 50px !important;
    }

    #stockist-widget .stockist-list-result .stockist-result-distance {
        padding: 0 !important;
    }

    #stockist-widget .stockist-horizontal .stockist-side-panel {
        width: 300px;

        @include bp(tablet) {
            width: 400px;
        }
    }

     #stockist-widget .stockist-map {
        left: 420px;
    }
}

// -------------------------------------
// Carousel -- Locations
// -------------------------------------
$carousel-arrow-border-radius: 0;
$carousel-arrow-background: transparent !default;
$carousel-arrow-color: $primary-text-color;
$carousel-arrow-padding: 0;





.locations {
    position: relative;

    &.locations--animation {
        max-width: 100%;
        overflow: hidden;
    }

    .carousel--locations {
        margin-bottom: 0;
        @include bp(laptop) {
            padding-top: gap(1);
        }

        .swiper-wrapper {
            position: relative;
            padding: 0;
            list-style-type: none;

            @include bp(laptop) {
                &:hover {
                    transform: translateY(-50%) scale(1.05);
                    transform-origin: center;
                }
            }
        }
    }

    .swiper-controllers {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: gap(3.6);
        gap: gap(2.3);

        @include bp(tablet) {
            margin-top: gap(0);
        }

        .swiper-arrows__wrapper {
            display: none;

            @include bp(tablet) {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: gap(3.4);
            }
        }

        .swiper-arrow__container {
            display: flex;
            justify-content: center;
            align-items: center;

            .swiper-arrow {
                position: static;
                margin-top: 0;
                background: $carousel-arrow-background;
                border: 0;
                border-radius: $carousel-arrow-border-radius;
                color: $carousel-arrow-color;
                padding: $carousel-arrow-padding;
                height: 36px;
                width: 36px;

                display: flex;
                align-items: center;
                justify-content: center;
                @include font-size(24);

                &:after {
                    display: none;
                }

                &.swiper-button-prev {
                    border-radius: $carousel-arrow-border-radius;

                    .icon {
                        position: relative;
                        left: 1px;
                    }
                }

                &.swiper-button-lock {
                    display: none;
                }
            }
        }
    }

    .swiper-scrollbar {
        @include swiper-scrollbar;
    }
}

