// ----------------------------------------------------------------------------
// Gift Card
// ----------------------------------------------------------------------------
//
// A page template with gift card content.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

.giftcard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 30px 15px;

    @include bp(tablet) {
        padding: 80px 15px;
    }

    h1 {
        @include responsive-fontsize(36, 40, 48, 60);
        margin: 0;
        margin-bottom: 30px;
        //font-weight: 700;
    }

    .gift-card__image {
        width: 400px;
        border-radius: 0 !important;
    }

    h2 {
        @include responsive-fontsize(20, 24, 28, 26);
        margin-top: 40px;
        margin-bottom: 5px;
        //font-weight: 700;
    }

    p {
        @include font-size(16);
        line-height: 24px;
        margin: 0;
    }

    .giftcard__code {
        @include font-size(18);
        margin-top: 25px;
        font-weight: 600;
        background: #F2F2F2;
        padding: 12px 103px;

        @include bp(tablet) {
            padding: 12px 100px;
        }
    }

    .giftcard-tag {
        font-weight: 700;
        font-size: 14px;
        line-height: 24px;
        margin-top: 12px;
    }

    .redeem {
        margin-top: 25px;
    }

    .button {
        border-radius: 0;
        // background: #787878;
        // color: $white;
        // padding: 15px 43.5px;
       // font-weight: 700;
        width: 100%;
    }

    .gift-card__buttons {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        row-gap: 7px;
        column-gap: 10px;
        width: 100%;
        margin-top: 40px;

        .giftcard .button {
            width: 100%;
        }

        @include bp(tablet) {
            flex-direction: row;
            align-items: flex-start;
            width: auto;
            .giftcard .button {
                width: 50%;
            }
        }
    }
}

.giftcard-qr {
  img {
    display: block;
    margin: 0 auto;
  }
}

.apple-wallet-image {
  display: block;
  margin: 0 auto;
}

@media print {
    @page {
        margin: 0.5cm;
    }

    .noprint {
        display: none;
    }

    p {
        orphans: 3;
        widows: 3;
    }

    .print-giftcard,
    .apple-wallet {
        display: none;
    }
}
