// ----------------------------------------------------------------------------
// Snippet - Specs Table
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
.specification-table {
    border-collapse: collapse;
    width: 100%;

    .specification-title {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    td,
    th {
        @include font-size(14);
        border: none;
        padding: 5px 10px;

        @include bp(laptop) {
            @include font-size(16);
            padding: 10px;
        }
    }

    tr:nth-child(even){
        background-color: #f2f2f2;
    }

    th {
        opacity: 0;
    }

    td.specification-title {
        @include font-medium;
        @include font-size(14);
        position: relative;
        min-width: 120px;
    }



    .toggle-tooltip {
        left: -68px;
        max-width: 300px;

        &::before {
            left: 54px;
        }

        @include bp(tablet) {
            top: 50%;
            transform: translate(30px, -50%);
            left: 10px;
            width: 100vw;
            max-width: 337px;

            &::before {
                content: '';
                position: absolute;
                top: 50%;
                right: calc(100% + 18px);
                left: auto;
                transform: translate(23px, -50%) rotate(45deg);
            }
        }
    }
}


