// ----------------------------------------------------------------------------
// CSS Grid -- Accordion
// ----------------------------------------------------------------------------
//
// Container component that allows for a grid of items that can be clicked to
// expand into a detail panel about that item.
//
// @import 'components/containers/css-grid/css-grid';
// @import 'components/containers/css-grid/css-grid--accordion';
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$css-grid-gutter--mobile: $grid-gutter--mobile !default;
$css-grid-gutter: $grid-gutter !default;
$css-grid-columns-large: 6 !default;
$css-grid-columns-medium: 4 !default;
$css-grid-columns-small: 2 !default;


// -------------------------------------
// Variables
// -------------------------------------
$css-grid-arrow-color: $background-color;
$css-grid-arrow-max-size: 16px;

$accordion-transition: $anim !default;

// -------------------------------------
// Accordion Grid -- Default
// -------------------------------------
.css-grid--accordion {
    display: flex;
    flex-flow: row wrap;

    @supports (display: grid) {
        display: grid;
        grid-auto-flow: dense;
        grid-template-rows: repeat(2, auto);
    }

    .css-grid__item {
        display: inline-flex;
        float: left;
    }

    .css-grid__item--card {
        align-items: flex-start;
        cursor: pointer;
        position: relative;
        transition: $anim;
        border-bottom: 3px solid $primary-color;


        &.js-active {
             border-bottom: 0;

            @include bp(laptop) {
                border-bottom: 3px solid $border-color;
            }
        }

        &:focus {
            outline: none;
        }

        &:hover {
            .tab__title {
                color: $border-color;
            }
        }
    }

    .css-grid__item--panel {
        display: none;
        grid-column: -1 / 1;
        -ms-grid-column: 1;
        margin-left: 0;
        margin-right: 0;
        width: 100%;
        padding: gap(.5) 0 gap(3.5);
        text-align: left;
        opacity: 0;
        transition-delay: 200ms;
        transition: opacity 400ms ease;


        p {
            &:first-of-type {
                margin-top: 0;
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }

        @include bp(laptop) {
            padding: gap(5) 0 gap(5);
        }

        &.js-active {
            display: block;
            border-bottom: 3px solid $border-color;
            opacity: 1;
            transition-delay: 200ms;
            transition: opacity 400ms ease;

            @include bp(laptop) {
               border-bottom: 0;
            }
        }
    }

    // Support .css-grid--[small,medium,large]-[1-12]
    @for $columns from 1 through $css-grid-columns-small {
        &.css-grid--small-#{$columns} .css-grid__item--panel {
            -ms-grid-column-span: $columns;
        }
    }

    @include bp(tablet) {
        @for $columns from 1 through $css-grid-columns-medium {
            &.css-grid--medium-#{$columns} .css-grid__item--panel {
                -ms-grid-column-span: $columns;
            }
        }
    }

    @include bp(laptop) {
        @for $columns from 1 through $css-grid-columns-large {
            &.css-grid--large-#{$columns} .css-grid__item--panel {
                -ms-grid-column-span: $columns;
            }
        }
    }
}
