// ----------------------------------------------------------------------------
// Variant Picker - Snippet
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';

.variant-picker {
    width: 100%;

    .form__field--custom-select {
        .form__label {
            font-weight: 300;
        }
    }
}


.ProductSection_OptionButton {
    border: 1px solid $border-color--light;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    font-size: 12px;
    color: $black;
    text-decoration: none;
    font-weight: 300;
    margin-left: 5px;
    margin-right: 5px;

    &:hover {
        color: $black;
    }
}
