// ----------------------------------------------------------------------------
// Card -- Default
// ----------------------------------------------------------------------------
//
// A card component, which can be integrated as a collection within the css-grid
// or flex-grid components to create content drivers.
//
// Default card styles match most closely with a wireframeing example of cards,
// with an image, title, description, and button inheriting core styles.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$card-background-color: transparent !default;
$card-border: transparent !default;
$card-content-padding: $grid-gutter !default;
$card-box-shadow: 0 12px 34px rgba(0, 0, 0, .14);

// -------------------------------------
// Card -- Base
// -------------------------------------
// There are no base styles to override;
// allows for easier variant styling.
.card {
    width: 100%;
}

.card__feature.background-image {
    @include aspect-ratio(16, 9);
}

.card__wrapper {
    display: block;
    height: 100%;
}

// -------------------------------------
// Card -- Default
// -------------------------------------
.card--default {
    position: relative;
    background-color: $card-background-color;
    border: none;
    overflow: visible;

    a {
        text-decoration: none;
    }

    .card__content {
        padding: 0;
    }

    .card__title {
        margin: gap(2) 0 gap(1);
    }

    .card__link {
        text-decoration: none;
        margin-top: gap(2.5);

        &::after {
            bottom: 0;
            content: "";
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
        }
    }

    .card__tags-list {
        margin: 0 0 gap(1);
        padding: 0;
        list-style-type: none;

        li {
            display: inline-block;

            & + li {
                margin-left: gap(1);
            }
        }

        a {
            text-decoration: underline;
        }
    }



    .card__button {
        @include font-tags;
        font-weight: 700;
        display: flex;
        align-items: center;
        opacity: 0;
        position: absolute;
        bottom: 20px;
        right: 20px;
        background-color: $white;
        color: $primary-text-color;
        z-index: 4;
        box-shadow: 0 2px 7px 0 rgba(0,0,0,0.08);
        border-radius: 17px;
        padding: gap(.3) gap(1.4);

        .icon {
            margin-left: 3px;
            width: 25px;
        }
    }

    .card__tag {
        position: absolute;
        top: -12px;
        left: 10px;
        z-index: 3;
    }

    .card__image-wrapper {
        height: 300px;
        margin: 0;
        display: block;
        position: relative;
        transform: translate(0);
        transition: box-shadow 400ms ease, transform 400ms ease;
        border-radius: 2px;
        overflow: hidden;

        @include bp(laptop) {
            border-radius: 3px;
        }
    }

    &:focus-visible {
        outline: $focus-state;
    }
}

// -------------------------------------
// Card -- Post
// -------------------------------------
.card--post {
    @include bp(laptop) {
         &:hover {
            .card__image-wrapper {
                transform: translateY(-15px);
                box-shadow: $card-box-shadow;
            }

            .card__title {
                color: $primary-color;
            }
        }
    }
}

// -------------------------------------
// Card -- Product
// -------------------------------------
.card--product {
    border-radius: 3px;
    width: 100%;

    .card__image-wrapper:after {
        content: "";
        display: block;
        padding-top: 137%;
        position: relative;
    }

    .background-image {
        position: absolute;
        top: 0;
    }

    .card__image-wrapper {
        height: auto;
        border-radius: 0;

        .card__image--primary {
            opacity: 1;
        }

        .card__image--secondary {
            opacity: 0;
        }

        &.hover {
            .card__image--primary {
                opacity: 0;
            }

            .card__image--secondary {
                opacity: 1;
            }
        }

    }

    .card__tag {
        z-index: 6;
        @include font-size(10);
        top: 5px;
        left: 5px;
        padding: 3px 5px;
        line-height: normal;
        @include bp(tablet) {
            @include font-size(14);
            top: 10px;
            left: 10px;
        }
        @include bp(laptop) {
            @include font-size(10);
            top: 5px;
            left: 5px;
        }
        @include bp(desktop) {
            @include font-size(14);
            top: 10px;
            left: 10px;
        }
    }

    .card__content {
        display: flex;
        flex-direction: column;
        gap: 10px;
        padding: 10px 0 0 0;
        text-decoration: none;
        color: $primary-text-color;

        @include bp(laptop) {
            gap: 20px;
            padding: 20px 0 0 0;
        }
    }

    .card__content-heading {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 4.936px;
        align-self: stretch;
    }

    .card__title {
        @include font-size(16);
        @include line-height(24);
        font-weight: 400;
        margin: gap(.7) 0;
        text-decoration: none;
        margin: 0;

        @include bp(laptop) {
            @include font-size(16);
            line-height: normal;
        }
    }

    .card__price {
        @include font-default;
        @include font-size(14);
        @include line-height(22);
        font-weight: 300;
        text-decoration: none;

        @include bp(laptop) {
            @include font-size(16);
            line-height: normal;
        }
    }

    .card__promo-message {
        color: $secondary-color;
        @include font-size(14);
        @include line-height(22);
    }

    .card__price--sale {
        color: $grey;

        & + .card__price--original {
            color: $sale-color;
            padding-left: gap(.5);
        }
    }

    .card__link {
        margin: 0;

        &.hover {
            color: $secondary-color;
            transition: $anim;

        }

        &::after {
            bottom: 0;
            content: "";
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
        }
    }

    .wishlist-button {
        z-index: 6;
        top: 10px;
        right: 10px;
        line-height: normal;
        position: absolute;
    }

}

// -------------------------------------
// Card -- Collection
// -------------------------------------
.card--collection {
    .card__image-wrapper {
        border-radius: 0;
        height: auto;
    }

    .card__content {
        display: block;
        padding: gap(2) 0 0;
        text-decoration: none;
        color: $primary-text-color;

        @include bp(laptop) {
            padding: gap(1.7) 0 0;
        }
    }

    .card__title {
        margin: 0;
        text-decoration: none;
    }

    .card__description {
        @include font-caption;
        color: $dark-grey;
        margin-top: 5px;
    }

    .card__title-icon {
        display: inline-block;
        opacity: 0;
        position: relative;
        top: 3px;
        margin-left: 10px;
    }

    .card__button {
        @include font-size(12);
        letter-spacing: 0.5px;
        text-transform: uppercase;
        font-weight: 400;
        box-shadow: 0;
        border-radius: 0;
        background-color: $tertiary-color;
        border: 1px solid $black;
        padding: 7px 16px;
        bottom: 9px;
        right: 9px;
    }

    @include bp(laptop) {
        // Card Hover styles
        &:hover {
            .card__title {
                color: $secondary-color;
                text-decoration: underline;
            }

            .card__button {
                opacity: 1;
            }

            .card__title-icon {
                opacity: 1;
            }
        }
    }
}

// ---------------------------------------------
// Card -- Product add to cart confirmation item
// ---------------------------------------------
.card--line-item {
    display: flex;
    grid-gap: 15px;
    border: 0;

    @supports (display: grid) {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .card__title {
        @include font-size(18);
        @include line-height(23);
        margin: 0 0 gap(1);
    }

    .line-item__variant-title {
        @include font-size(16);
        margin: 0;
        font-weight: 400;
    }

    .line-item__added-quantity {
        @include font-size(16);
        margin: 0 0 gap(.5);
        font-weight: 400;
    }

    .card__price {
        @include font-size(16);
        font-weight: 400;
    }

    .card__price--sale {
        @include font-size(16);
        color: $grey;

        & + .card__price--original {
            color: $red;
            padding-left: gap(.5);
        }
    }

    a {
        text-decoration: none;
        color: $primary-text-color;
    }
}

// ---------------------------------------------
// Card -- Color Swatches
// ---------------------------------------------
.card__color-swatches {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 13px;
    margin-bottom: 0;

    .card__swatch--color-option {
        border-radius: 50%;
        border: 1px solid $grey;

        &:nth-child(n+4) {
            display: none;

            @include bp(tablet) {
                display: block;
            }
        }

        &.selected {
            border-color: $black;

            &:hover {
                border-color: $black;
            }
        }

        &:focus-visible {
            outline: 4px solid $secondary-color;
        }

        &:hover {
            border-color: #757575;
            cursor: pointer;

            label {
                cursor: pointer;
            }
        }
    }

    .card__swatch-option-label {
        position: relative;
        display: block;
        padding: 0;
        border-radius: 50%;
        color: black;
        width: auto;
        border: 3px solid transparent;

        .card__swatch-option-label-inner {
            display: block;
            border-radius: 50%;
            padding: 0;
            width: 20px;
            height: 20px;
            background-size: cover;
            background-position: center;
        }

        .card__swatch-option-label-visually-hidden {
            border: 0;
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            width: 1px;
        }

    }

    .card__more-colors-link {
        align-items: center;
        padding-left: 0;
        display: none;
        visibility: hidden;

        @include bp(tablet) {
            display: flex;
            visibility: visible;
        }


        &.card__more-colors-link--mobile {
            display: flex;
            visibility: visible;
            @include bp(tablet) {
                display: none;
                visibility: hidden;
            }
        }

        a {
            @include font-size(16);
            line-height: normal;
            color: $secondary-text-color;
            text-decoration: none;
            font-weight: 300;

            &:hover {
                color: rgba($secondary-text-color, .7);
                transition: $anim;
            }

            &:focus-visible {
                color: inherit;
                outline: $focus-state;
            }
        }
    }
}

// -------------------------------------
// Card -- Location
// -------------------------------------
.card--location {
    .card__image-wrapper {
        height: auto;
    }

    .card__content {
        display: block;
        padding: gap(1) gap(.5);
        text-decoration: none;
        color: $primary-text-color;

        @include bp(laptop) {
            padding: gap(1.5) gap(0);
        }
    }

    .card__title {
        margin: 0;
        text-decoration: none;
    }

    @include bp(laptop) {
        // Card Hover styles
        &:hover {
            .card__image-wrapper {
                transform: translateY(-15px);
                box-shadow: $card-box-shadow;
            }

            .card__title {
                color: $primary-color;
            }

            .card__button {
                opacity: 1;
            }
        }
    }
}
