// ----------------------------------------------------------------------------
// Button -- Text
// ----------------------------------------------------------------------------
//
// Buttons with a simple text CTA without any border or backgrounds.  Hit area
// of the button is just like the other buttons assist mobile users.
//
// @import 'base/button/button';
// @import 'base/button/button--text';
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Button Colors & Styles
// -------------------------------------
.button--primary,
.button--white {
    &.button--text {
        position: relative;
        background-color: transparent;
        border: 0;
        padding: gap(2.5) 0;

        &:hover,
        a:hover & {
            background-color: transparent;
        }

        .button__icon {
            display: inline-block;
            margin-left: 5px;
            vertical-align: middle;
            top: -1px;
            position: relative;
        }
    }
}

.button--primary {
    &.button--text {
        color: $primary-text-color;

        &:hover,
        a:hover & {
            color: $secondary-color;
        }
    }
}

