// ----------------------------------------------------------------------------
// Rebuy Cart Styling overrides
// ----------------------------------------------------------------------------
//


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';

#rebuy-cart {
    .rebuy-cart__flyout {
        border-radius: 0;
        padding: 0;
        margin: 0;
        z-index: $z-index--lightbox !important;
    }



    .rebuy-cart__flyout-announcement-bar {
        background-color: $primary-color;
        color: $white;
    }

    .rebuy-cart__flyout-header,
    .rebuy-cart__flyout-body,
    .rebuy-cart__flyout-recommendations,
    .rebuy-cart__flyout-footer {
        background-color: $background-color--base;
    }

    .rebuy-cart__flyout-header {
        padding: 15px 20px 5px;
        border-bottom: 0;
    }

    .rebuy-cart__flyout-title {
        @include font-header;
        @include responsive-fontsize(24, 30, 34, 42);
        font-weight: 300;
        text-transform: none;
    }

    .rebuy-cart__progress-bar-meter {
        background-color: $white;
    }

    .rebuy-cart__flyout-subtotal {
        font-weight: 400;
    }

    .rebuy-cart__flyout-subtotal {
        margin-bottom: 20px;
        font-weight: 400;
    }

    .rebuy-product-title{
        font-size: 16px;
        font-weight: 400;
    }

    .rebuy-product-price {
        font-size: 16px;
        font-weight: 300;
        margin: 10px 0 0;
    }

    .rebuy-button,
    .rebuy-cart__flyout-empty-cart a {
        font-size: 12px;
        padding: 13px 0;
    }

    // Recommended Section Styling
    .rebuy-cart__flyout-recommendations {
        padding: 0;
    }

    .rebuy-cart__flyout-item-product-title {
        font-size: 16px;
        font-weight: 400;
        text-decoration: none;
    }

    .rebuy-cart__flyout-item-price {
        font-size: 16px;
        font-weight: 300;
        margin: 20px 0 0;

        .rebuy-money.compare-at {
            color: $primary-text-color;
        }
    }

    .rebuy-cart__flyout-item-variant-title {
        font-size: 14px;
        color: $primary-text-color;
        margin-top: 10px;
    }

    .rebuy-cart__flyout-item-quantity-widget-button {
        background: transparent;
    }

    .rebuy-cart__flyout-item-discount-message {
        font-size: 14px;
        margin-top: 5px;
    }

    .rebuy-widget-content {
        padding: 20px 15px 15px;

        .primary-title {
            @include responsive-fontsize(18, 22, 20, 25);
            margin: 0;
        }
    }

    .rebuy-cart__flyout-empty-cart a {
        text-decoration: none;
    }

    .rebuy-cart__flyout-item-quantity {
        margin: 20px 0 0;
    }

    .rebuy-cart__flyout-item-remove {
        color: $primary-text-color;
    }

    .rebuy-money.compare-at {
        color: $primary-text-color;
    }


    // Free Gift Widget Styling
    #rebuy-widget-137589 {
        background-color: $background-color--white;

        .rebuy-widget-content {
            padding: 20px 5px 15px;

            .primary-title {
                padding-bottom: 15px;
            }
        }

        .rebuy-product-block {
            padding: 10px;
        }

        .rebuy-product-info {
            text-align: left;
        }

        .rebuy-select {
            font-size: 12px;
        }

        .rebuy-product-options {
            margin-top: 5px;
        }
    }
}

