// ----------------------------------------------------------------------------
// Snippet: Icon
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Icon base styles
// -------------------------------------
.icon {
    background: transparent;
    display: inline-block;
    fill: currentColor;
    height: 1em;
    width: 1em;

    .no-svg & {
        display: none;
    }
}

