// ----------------------------------------------------------------------------
// Order Page
// ----------------------------------------------------------------------------
//
// Page template with a single order details table
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Order Page Styles
// -------------------------------------
.order-page__page-section {
    overflow: auto;
    padding-bottom: 30px;

    @include bp(laptop) {
        padding-bottom: 0;
    }
}

.order-page__title {
    margin-top: 0;
    margin-bottom: gap(.5);
}

.order-page__subtitle {
    margin-bottom: gap(4);
}

.page-section--billing-address {
    margin: 10px 0 30px 0;

    @include bp(laptop) {
        margin: 50px 0;
    }
}

.order-addresses {
    margin-bottom: 40px;
}
