// ----------------------------------------------------------------------------
// Video Block
// ----------------------------------------------------------------------------
//
// Content component that positions a video in a custom-sized block, or in
// the flex-position or flex-grid.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

.video-block--section {
    position: relative;
    display: grid;

    .video-block__video--desktop, .video-block__video--mobile {
        grid-column: 1;
        grid-row: 1;
    }

    .video-block__cover {
        grid-column: 1 / -1;
        grid-row: 1 / -1;
        width: 100%;
        height: 100%;
    }

    .video-block__video {
        position: relative;
    }

    video {
        grid-column: 1;
        grid-row: 1;
        width: 100%;
        height: auto;
    }

    .video-block--mobile {
        display: grid;
        @include bp(tablet) {
            display: none;
        }
    }

    .video-block--desktop {
        display: none;
        @include bp(tablet) {
            display: grid;
        }

    }

    .video-block__play-button {
        padding: 0;
        grid-column: 1;
        grid-row: 1;
        display: grid;
        grid-template-columns: 1fr 50px 1fr;
        grid-template-rows: 1fr 50px 1fr;
    }

    .video-block__control {
        width: 50px;
        height: 50px;
        grid-column: 2;
        grid-row: 2;

        .icon {
            width: 100%;
            height: 100%;
        }
    }


    .video-block--desktop, .video-block--mobile {
        .video-block__play-button {
            z-index: 1;
        }
    }

    .video-block--desktop.video-block--playing, .video-block--mobile.video-block--playing {
        .video-block__play-button {
            display: none;
        }
    }




    &::before, &::after {
        content: unset;
    }
}
