// ----------------------------------------------------------------------------
// Collection filters
// ----------------------------------------------------------------------------
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$filters-sort-spacing: gap(1.5);
$filters-item-spacing: gap(0.5);

// Filters
$filter-group-border-color: #CCC;
$filter-list-margin-mobile: gap(3) 0 0;
$filter-list-margin-desktop: 0 0;
$filter-item-gap-mobile: gap(1);
$filter-item-gap-desktop: gap(1);
$button-padding: gap(0.8) gap(1.5);
$button-color-primary: $primary-color;
$button-color-secondary: $white;
$button-color-disabled-bg: $light-grey;
$button-color-disabled-text: #BBC1C6;
$input-border-color: $black;

// Sort
$bar-padding-bottom: gap(1);
$bar-margin-bottom: gap(3);
$bar-border-color: #E8E8E8;
$collection-sort-gap: gap(0.5);
$collection-sort-label-margin-right: gap(0.5);

// -------------------------------------
// Filters & Sort
// -------------------------------------
.collection-grid-container {
    .filters-sort__wrapper,
    .sort__wrapper {
        &.mobile-only,
        .mobile-only {
            @include bp(laptop) {
                display: none;
            }
        }

        &.desktop-only,
        .desktop-only {
            display: none;

            @include bp(laptop) {
                display: block;
            }
        }
    }
}

.collection-filters-sort-container {
    .filters__heading {
        margin-bottom: $filters-sort-spacing;
    }


}


// -------------------------------------
// Filters
// -------------------------------------
.collection-filters-container {
    max-width: 100%;

    .filters__summary {
        list-style: none;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: 5px;
        cursor: pointer;

        .collection-filter__toggle {
            width: 8px;
        }

        &::-webkit-details-marker {
            display:none;
        }
    }

    .filters__display {
        margin-top: $filters-sort-spacing;
    }

    .filter__type {
        font-weight: 500;
    }

    .filters__list {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    .filters__item {
        &:not(:last-of-type) {
            margin-bottom: $filters-item-spacing;
        }
    }

    .filter-checkbox {
        flex-grow: 1;
        position: relative;
        font-size: 16px;
        display: flex;
        align-items: center;
        word-break: break-word;
        column-gap: gap(1);

        .filter-checkmark {
            position: absolute;
            width: 11px;
            height: 7px;
            left: 2px;
            visibility: hidden;
        }

        input[type=checkbox] {
            width: 16px;
            height: 16px;
            appearance: none;
            margin: auto 0;
            border: 1px solid $input-border-color;
            cursor: pointer;

            &:checked {
                ~ .filter-checkmark {
                    visibility: visible;
                }
            }
        }
    }

    .filters__price {
        display: flex;
        flex-direction: row;
        justify-content: left;
        align-items: center;
        gap: 5px;
    }

    .field {
        position: relative;
        display: flex;
        align-items: center;
        max-width: 47%;
         margin-left: 10px;
         flex: 1;


        .field__label {
            position: absolute;
            left: gap(1);
            @include font-size(16);
        }

        .field__input {
            border: 1px solid $input-border-color;
            padding: gap(1.4) gap(1);
            @include font-size(16);
            width: 100%;

            &::placeholder {
                opacity: 0;
            }

            &:not(:placeholder-shown),
            &:-webkit-autofill,
            &:focus {
                padding: gap(2) gap(1) gap(0.8);

                ~ .field__label {
                    @include font-size(10);
                    line-height: 1;
                    top: gap(0.8);
                }
            }
        }
    }

    .filters-wrap {
        border: none;
        padding: 0;
        margin: 0;
    }

    .filter-group {
        border-top: 1px solid $filter-group-border-color;
        padding: $filters-sort-spacing 0;

        @include bp(laptop) {
            width: 250px;
        }
    }
}

.active-filters-wrapper {
    margin-bottom: $filters-sort-spacing;

    .filters__heading {
        margin-bottom: 0;
    }

    .active-filters-header {
        display: flex;
        justify-content: space-between;
        gap: gap(1.5);
        margin-bottom: gap(1.5);
    }

    .active-filters-header {
        &:only-child {
            filter-remove {
                display: none;
            }
        }
    }

    .active-filters__button-remove {
        color: $primary-text-color;
        font-weight: 300;
        @include font-size(16);
        line-height: 1;
        text-underline-offset: 2px;
        letter-spacing: 0;
    }

    .active-filters__button {
        color: $primary-text-color;
        font-weight: 300;
        letter-spacing: 0;
        text-decoration: none;
        display: inline-block;
        margin-left: -5px;

        .active-filters__button-inner {
            box-shadow: 0 0 0 1px $filter-group-border-color;
            border-radius: 26px;
            padding: 5px 10px;
            @include font-size(14);
            @include line-height(24);
            display: inline-flex;
            flex-direction: row;
            align-items: center;
            gap: 5px;
            transition: $anim;
            margin: 5px;
        }

        .filter-remove__icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 10px;
            height: 10px;
        }

        &:hover {
            .active-filters__button-inner {
                box-shadow: 0 0 0 2px $filter-group-border-color;
            }
        }
    }

    &.active-filters-wrapper--mobile {
        margin-top: gap(1.5);

        .active-filters__button-wrapper {
            margin-left: 5px;
        }

        filter-remove {
            &:only-of-type {
                display: none;
            }
        }
    }
}

.mobile-filters__footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-top: gap(2.5);

    .mobile-filters__clear {
        width: 100%;
    }

    .modal__close-button {
        &.button {
            position: unset;
            margin: 0;
        }
    }
}

.collection-filters {
    outline: none;
    margin: $filter-list-margin-mobile;

    @include bp(laptop) {
        margin: $filter-list-margin-desktop;
    }

    .collection-filters__title {
        margin-top: 0;
        margin-bottom: $filters-sort-spacing;
    }
}

.filter-group {
    &:not([open]) {
        .collection-filter__toggle {
            transform: rotate(180deg);
        }
    }
}


// -------------------------------------
// Sorting
// -------------------------------------
.collection-sort {
    display: flex;
    flex-direction: row;

    .product-count {
        margin-left: $filters-sort-spacing;
    }

    &.collection-sort__mobile {
        flex-direction: column-reverse;

        .product-count {
            margin-bottom: $filters-sort-spacing;
            margin-left: 0;
        }
    }
}

.collection-sort__selector-wrapper {
    position: relative;
    display: flex;
    align-items: center;

    label {
        margin-right: $collection-sort-label-margin-right;
    }

    .collection-sort__selector {
        background-color: transparent;
        color: $primary-color;
        border: none;
        cursor: pointer;
        z-index: 1;
    }

    .collection-sort__toggle {
        height: 13px;
        width: 13px;
        text-align: center;
        right: 0;
    }
}

.filters-sort-trigger__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: gap(1);

    .filters-sort-trigger {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: gap(1);
        background-color: transparent;
        padding: 0;
        font-weight: 400;

        .icon__filters-sort {
            width: 20px;
            height: 20px;
        }
    }
}

// -------------------------------------
// Modal
// -------------------------------------

.collection-filters-sort-modal {
    &.modal {
        @include bp(laptop) {
            opacity: 1;
            position: unset;
        }
    }

    .modal__content {
        background: $background-color--base;
        @include bpgd(tablet) {
            height: 100vh;
            width: 100vw;
            overflow-y: scroll;
            max-width: 80%;
            padding: gap(4);
            display: block;
        }

        @include bp(laptop) {
            position: unset;
            padding: 0;
            transform: none;
            transition: none;
        }
    }

    .modal__close-button {
        @include bpgd(tablet) {
            margin: gap(4)
        }
    }

    .modal__overlay {
        @include bp(laptop) {
            display: none;
        }
    }
}

