// ----------------------------------------------------------------------------
// Carousel
// ----------------------------------------------------------------------------
//
// Styles for the basic carousel styles and functions.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$carousel-arrow-background: transparent;
$carousel-arrow-border-radius: 0 !default;
$carousel-arrow-color: $primary-color;
$carousel-arrow-padding: 0;

$carousel-dot-background: rgba($primary-color, .15);
$carousel-dot-background-active: $primary-color;
$carousel-dot-border: 0;
$carousel-dot-margin: 0 15px 0 0;
$carousel-dot-size: 16px;


// -------------------------------------
// Base Carousel Styles
// -------------------------------------
// These are basic styles that all carousels
// contain. They style our base Slick classes.

.carousel {
    position: relative;
    margin-bottom: 40px;

}

// -------------------------------------
// Carousel -- Default Section
// -------------------------------------

.carousel-section {
    text-align: center;

}
