// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// FAQs, Block Styles
// -------------------------------------

.faq-block {
    @include container--wysiwyg;

    .accordion__button {
        display: flex;
        align-items: center;
    }

    .accordion__icon {

        .icon {
            width: 20px;
            height: 20px;
        }
    }

    .accordion__title {

        .accordion__button {
            padding: gap(2) 0;
        }
    }

    .accordion__item {

        &:last-child {
            border-bottom: none;
        }
    }

    .accordion__content.is-open {
        margin: 0 0 gap(2);
    }

    .accordion__item {
        
        &:hover {
            border-color: $black;

            .accordion__button {
                color: $black;
            }
        }
    }
}

.faq-block__h2 {
    margin-top: 0;
    margin-bottom: gap(2);


    @include bp(laptop) {
        text-align: center;
    }

    & + .accordion {
        margin-top: gap(3);

        @include bp(laptop) {
            margin-top: gap(5);
        }
    }
}

.faq-block__description {
    margin-bottom: gap(2);

    @include bp(laptop) {
        text-align: center;
    }
}

.faq-block__footer {

    @include bp(laptop) {
        text-align: center;
    }
}


// -------------------------------------
// FAQs Section
// -------------------------------------

.page-section--faq {
    background-color: $background-color;

    padding: gap(6) 0;

    @include bp(laptop) {
        padding: gap(10) 0;
    }
}

