// ----------------------------------------------------------------------------
// Helpers
// ----------------------------------------------------------------------------
//
// Classes to make small tasks easier
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Helper Styles
// -------------------------------------

/* Hiding class, making content visible only to screen readers but not visually */
/* "sr" meaning "screen-reader" */

.sr-only:not(:focus):not(:active) {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }

.clearfix {
    @include clearfix;
}

.visually-hidden {
    @include visually-hidden;
}

.js-focus-hidden:focus {
    outline: none;
}

.is-sticky {
    position: sticky;
}

.label-hidden {
    @include visually-hidden;

    // No placeholders, so force show labels
    .no-placeholder & {
        @include visually-shown;
    }
}

.visually-shown {
    @include visually-shown;
}

// Only show when JS is not supported
.no-js:not(html) {
    display: none;

    .no-js & {
        display: block;
    }
}

// Only show when JS is supported
.js {
    .no-js & {
        display: none;
    }
}

// Only show when browser cookies are not supported
.supports-no-cookies {
    display: none;

    html:not(.cookies) & {
        display: block;
    }
}

// Only show when browser cookies are supported
.supports-cookies {
    html:not(.cookies) & {
        display: none;
    }
}

// Skip to content button
// Overrides .visually-hidden when focused
.skip-link:focus {
    @include visually-shown(absolute);
    z-index: 10000;
    padding: $grid-gutter * 0.5;
    background-color: $background-color--base;
    color: $primary-text-color;
    transition: none;
}

//  Hide / Show
// -------------------------------------
.hide {
    /* stylelint-disable declaration-no-important */
    display: none !important;
}

.hidden,
.js-partial {
    display: none;
}

.clear-left {
    clear: left;
}

// Used to add a scroll margin based on header height
.scroll-margin {
    scroll-margin-top: 80px;
}
