// ----------------------------------------------------------------------------
// Single Product Page
// ----------------------------------------------------------------------------
//
// Single Product page with grid product image slider, quantity select and
// add to cart button
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// --------------------------------
// Product Hero
// --------------------------------

.page-section--product {
    padding-top: 80px;
}

.product {
    @include bp(laptop) {
        display: grid;
        grid-template-columns: 5fr 3fr;
        padding-top: 0px;
    }

    .background-image {
        width: 100%;
        height: 100%;
    }

    .form__label--product {
        margin-bottom: gap(.8)
    }
}

.product__breadcrumb {
    @include font-caption;
    display: block;
    text-decoration: none;


    .icon {
        font-size: 8px;
        top: -1px;
        position: relative;
    }

    &.product__breadcrumb--desktop {
        display: none;

        @include bp(laptop) {
            display: block;
        }
    }

    &.product__breadcrumb--mobile {
        margin: 0 $grid-gutter--mobile 20px;
        @include bp(laptop) {
            display: none;
        }
    }
}

// --------------------------------
// Product Image Slider
// --------------------------------
.product__images {
    position: relative;

    @include bp(tablet) {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto;
        grid-column: 1;
        align-self: start;
    }

    .swiper-scrollbar {
        background: $grey;

        @include bp(laptop) {
            display: none;
        }

        .swiper-scrollbar-drag {
            background-color: $black;
        }
    }

    .pagination-wrapper {
        margin-top: 20px;
    }

    .swiper-scrollbar.swiper-scrollbar-horizontal {
        height: 2px;
        left: 15px;
        width: calc(100% - 30px);
    }
}


.product__feature-image {
    grid-row: 1;
    border-radius: 4px;
    overflow: hidden;

    @include bp(laptop) {
        grid-column: 2;
    }

    .swiper-wrapper {
        @include bp(laptop) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            row-gap: 5px;
            transform: none !important;
            height: auto !important;

            .swiper-slide {
                width: auto !important;
                transform: none !important;
            }

            // .swiper-slide:last-child:nth-of-type(odd) {
            //     grid-column: span 2;

            //     .product-single__media-wrapper {
            //         height: 650px;
            //     }
            // }
        }
    }
}

.carousel--featured-product {
    width: 100%;

    &:not(.current-variant) {
        display: none;
    }
}

.product-single__media {
    margin: 0 auto;
    min-height: 1px;
    width: 100%;
    height: 100%;
    position: relative;

    iframe,
    img,
    > video,
    model-viewer,
    .shopify-model-viewer-ui,
    .plyr,
    .media-item {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 100%;

        &:focus-visible {
            outline: none;
        }
    }

    iframe,
    model-viewer,
    .media-video,
    .shopify-model-viewer-ui,
    .media-item,
    .plyr,
    .plyr__video-wrapper {
        height: 100%;
    }

    .plyr__control--overlaid {
        background: rgba($white, .5);
        border-radius: 50%;
        color: $white;

    }
}

.product-single__media--video {
  background-color: $background-color;
}

// ---------------------------------
// Product Hero - Content
// --------------------------------
.product__content {
    padding: gap(1.5) $grid-gutter--mobile;

    @include bp(laptop) {
        padding: 0 0 0 gap(4);
        grid-column: 2;
    }

    .card__tag {
        margin-left: gap(2);
    }

    .okeReviews[data-oke-container],
    div.okeReviews {
        font-size: 14px !important;
        font-weight: 300;
        --oke-stars-foregroundColor: #000;
    }
}

.product__title {
    margin: 0 0 gap(1);

    @include bp(laptop) {
        margin: gap(2) 0 gap(1.5);
    }
}

.product__tag {
    color: $white;
    background-color: $primary-color;
    text-transform: uppercase;
    padding: gap(.3) gap(.8);
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 5;

    @include bp(laptop) {
        padding: gap(.6) gap(1.1);
        top: 20px;
        right: 40px;
    }
}

.product__type {
    @include font-default;
    margin: 0;
}

.product__description {
    @include font-size(14);
    padding-top: gap(2.5);

    @include bp(laptop) {
        @include font-size(16);
        padding-bottom: gap(2);
    }

    p,
    ul {
        &:first-of-type {
            margin-top: 0;
        }
    }
}

.product__sku {
    @include font-size(14);
    text-transform: uppercase;
}

.product__promo-message {
    @include font-size(14);
    color: $secondary-color;
    margin-top: gap(.5);
}

.price__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;


    .line-item__final-sale {
        @include font-size(16);
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border: $border--light;
        padding: gap(0.3) gap(.6);
        margin-left: gap(1.5);
        margin-top: gap(.5);

        span {
           text-transform: uppercase;
        }

        .tooltip {
            margin-left: gap(.5)
        }
    }
}

.product__form-wrapper {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;

    .add-to-cart {
        position: relative;
    }

    .form__input {
        border: 0;
        border-radius: 0;
        width: 70px;
    }
}

.product__quantity {
    display: none;
    // margin-right: gap(3);
    // margin-bottom: 0;

    // .form__input-container {
    //     position: relative;
    //     width: 80px;
    // }

    // .cart__quantity {
    //     margin: 0;
    // }
}

.product__container {
    display: inline-flex;
    flex-direction: row;
    align-items: flex-end;
    gap: gap(0);
    margin: gap(3) 0 0;
    width: 100%;

    @include bp(laptop) {
        flex-direction: row;
        align-items: flex-end;
        gap: gap(0);
        margin: gap(3) 0 0;
    }

    .selector-wrapper {
        width: 100%;
        margin-bottom: 0;

        & + .selector-wrapper {
            margin-top: gap(2.0) !important;
        }
    }

    .swym-btn-container .swym-add-to-wishlist:after {
        color: $white !important;

        font-size: 14px;
    }

    .swym-btn-container .swym-add-to-wishlist {
        background-color: $background-color;
        height: 42px;
    }

    .swym-btn-container[data-position=default] .swym-add-to-wishlist.swym-icon {
        background-color: $primary-color;
        height: 42px;
        line-height: 42px;
        margin-left: 5px;
    }

    .cart__quantity  {
        display: none;
    }

    .button--primary {
        width: calc(100% - 55px);
    }
}

.variant-picker__wrapper {
    position: relative;

    .modal__trigger {
        position: absolute;
        top: 0;
        right: 0;
        padding: 5px 0;
        text-transform: none;
        text-decoration: underline;
        font-size: 14px;
        font-weight: 300;
        background-color: none;
        letter-spacing: 0;
        width: auto;
    }
}

.product__button-footer {
    display: grid;
    grid-template-columns:1fr 1fr;
    gap: 5px;

    .modal__trigger {
        @include font-caption;
        letter-spacing: 0;
        padding: 0;
        text-transform: none;
        text-decoration: underline;
        justify-self: start;
    }
}

.product__shipping-notice {
    @include font-caption;
    justify-self: end;
    grid-column: 2;
}


// ----------------------------------------------------------------------------
// Product Sticky Cart
// ----------------------------------------------------------------------------
.product__sticky-container {
    .product__title {
        display: none;
    }
}

@include bp(laptop) {
    #productStickyContainer {
        min-height: 80px;
    }

    .product__sticky-container {
        transform: translateY(0);

        .product__sticky--show & {
            position: fixed;
            width: 100%;
            background: $background-color--base;
            bottom: 100%;
            left: 0;
            z-index: calc(#{$z-index--header} + 1);
            display: grid;
            grid-template-columns: [full-start] minmax(50px, 1fr) [main-start] minmax(0, 1920px) [main-end] minmax(50px, 1fr) [full-end];
            border-bottom: 1px solid #e5e5e5;
            padding: 19px 0;
            transform: translateY(100%);
            transition: transform 0.25s ease;

            .product__sticky {
                grid-column: main;
                display: grid;
                grid-template-columns: 1fr 2fr;
                align-items: center;
            }

            .swym-wishlist-button-bar {
                display: none;
            }

            .product__title {
                display: block;
                margin: 0;
            }

            .form__label {
                display: none;
            }

            .product__container {
                align-items: center;
                justify-content: flex-end;
                padding: 0;
                margin: 0;
            }

            .button--primary {
                max-width: 300px;
            }
        }

        .product__sticky--show.product__sticky--hide & {
            transform: translateY(0);
        }
    }
}

.cart__message--error {
    margin-top: gap(1);
    color: $red;
}

#productStickyContainer:not(.product__sticky--show) + {
    .product-sticky--mobile {
        opacity: 0;
        pointer-events: none;
    }
}

.product-sticky--mobile {
    display: flex;
    position: fixed;
    top: 0px;
    left: 0;
    width: 100%;
    justify-content: center;
    align-items: center;
    z-index: 10000;
    opacity: 1;
    transition: opacity 0.25s ease;
    background-color: $background-color--base;
    height: 78px;
    border-bottom: 1px solid #e5e5e5;
    padding: 0 $grid-gutter--mobile;

    @include bp(laptop) {
        display: none;
    }

    &.product-sticky--mobile {
        .button--mobile-cart {
            width: 100%;
        }
    }
}

.product__model-size {
    @include font-caption;
}


.accordion--product {
    margin-top: gap(4);
    padding-bottom: 20px;

    @include bp(laptop) {
        margin-top: gap(5)
    }

    .accordion__title {
        .accordion__button {
            @include font-size(16);
            font-weight: 400;
            padding: 25px 0;
        }

        .accordion__icon .icon {
            width: 16px;
            height: 16px;
        }
    }

    .accordion_icon-item {
        display: flex;
        align-items: center;
        margin-bottom: gap(1);

        .responsive-image__image {
            width: 35px;
        }
    }
    .accordion_icon-list {
        + .accordion_details {
            margin-top: gap(3);
        }
    }
}



.accordion_icon-title {
    margin-left: gap(1);
}


.product__payment-notice {
    @include font-size(14);
    padding: 10px 0;
    vertical-align: middle;
    display: inline-flex;
    align-items: center;
    line-height: 1.5;
    flex-wrap: wrap;
    column-gap: 5px;
}

.product__payment-logo {
    object-fit: contain;
    display: inline-block;
    margin-bottom: -.15em;
}

.product__payment-info {
    margin-bottom: -.15em;
    text-decoration: none;
    display: inline-block;

    svg {
        width: 12px !important;
    }
}


// Klaviyo Modal

.klaviyo-bis-trigger {
    font-weight: 300;
    padding-bottom: 10px;
    display: inline-block;
}

#klaviyo-bis-modal {
    h3 {
        @include font-primary;
        @include responsive-fontsize(20, 25, 24, 30);
        font-weight: 400;
    }

    p {
       @include font-primary;
       @include font-size(16);
    }

    button,
    .btn-success {
        background-color: $primary-color;
        color: $white;
        border: 1px solid transparent;
        @include font-default;
        font-size: 12px;
        line-height: normal;
        font-weight: 400;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        padding: 13px 30px;

        &:hover,
        &:active,
        a:hover & {
            background-color: $secondary-color;
            color: $white;
            border: 1px solid $secondary-color;
        }
    }

    .form-control {
        @include font-primary;
    }
}
