// ----------------------------------------------------------------------------
// Header
// ----------------------------------------------------------------------------
//
// Basic site announcement bar.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$announcement-bar-background: $primary-color !default;
$announcement-bar-text-color: $white !default;
// Must be higher than Header so that the menus of the Country and Language switcher are visible
$z-index--announcement: 1001;

// -------------------------------------
// Announcement Bar
// -------------------------------------
.announcement-bar {
    background-color: $announcement-bar-background;
    color: $announcement-bar-text-color;
    padding: 6px 0;
    text-align: center;
    position: relative;
    z-index:  $z-index--announcement;
    display: block;

    &.announcement-bar--hidden {
        display: none;
    }

    .localization-forms {
        display: none;

        @include bp(tablet) {
            display: flex;
        }
    }

    .container {
        display: flex;
        justify-content: center;
        position: relative;
    }

    .announcement-bar__link,
    .announcement-bar__text {
        @include font-default;
        @include font-size(14);
        color: $announcement-bar-text-color;
        text-decoration: none;
        margin: 0;
    }

    .announcement-bar__close {
        @include font-default;
        @include font-size(14);
        background-color: transparent;
        color: $announcement-bar-text-color;
        text-transform: none;
        font-weight: normal;
        text-decoration: underline;
        position: absolute;
        top: 0;
        right: 0px;
        padding: 3px 0;
    }
}
