// ----------------------------------------------------------------------------
// Product
// ----------------------------------------------------------------------------
//
// This is styling for product
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// Product Price
.product__price {
    font-weight: 400;
    margin:  0;

    @include bp(laptop) {
        @include font-size(18);
    }

    .price__sale {
        color: $primary-text-color;
    }

    .price__original--sale {
        color: $sale-color;
    }
}

// Cart Summary
.cart-summary__item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: gap(1.3);
    font-weight: 400;
}

//  Free Shipping Progress
.free-shipping-progress__message {
    @include font-size(16);
    background-color: $background-color;
    padding: gap(.5) gap(1.5);
    margin: 0 0 gap(1.3);
    text-align: center;
    display: block;
}
