// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

.search-box--default {
    &.search-box--predictive {
        //position: relative;
        flex: 1;

        @include bp(laptop) {
            position: unset;
        }

        .form__field--search {
            flex: 1;
        }

        .form__field--button {
            flex: 0;
        }
    }
}

.iam-predictive-search {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: $background-color--white;

    .is-active & {
        display: block;
    }
}

#predictive-search {
    position: absolute;
    z-index: 3;
    top: 100%;
    left: 0;
    right: 0;
    background: $white;
    border: 1px solid #F0F0F0;

    max-height: 80vh;
    overflow: auto;

    #predictive-search-results {
        @include container;
    }
}

#predictive-search-articles {
    margin-top: gap(3);
}

.predictive-search__results-list-container {
    padding: gap(2) 0;

    @include bp(laptop) {
        padding: gap(4) 0;
    }

    &.predictive-search__results-list-container--sidebar {
        @include bp(laptop) {
            display: grid;
            grid-gap: gap(2);
            grid-template-columns: 250px 1fr;
        }
    }

    .predictive-search__results-list {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    .predictive-search__heading {
        margin-bottom: gap(1.5);
        font-weight: 400;
    }

    .predictive-search__sidebar {
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: 0;
        }

        &.mobile-only {
            display: block;

            @include bp(laptop) {
                display: none;
            }

        }

        &.desktop-only {
            display: none;

            @include bp(laptop) {
                display: block;
            }
        }
    }

    .search-results__item__title {
        text-decoration: none;
    }

    .button {
        &.search-results__link {
            white-space: inherit;
            text-align: left;
        }
    }
}
