// ----------------------------------------------------------------------------
// Menu -- Dropdown
// ----------------------------------------------------------------------------
//
// Navigation for the site, consisting of an accordion-styled mega
// mobile menu and a hover-style dropdown desktop menu. Inherits from menu.
//
// @import 'components/site-elements/menu/menu';
// @import 'components/site-elements/menu/menu--dropdown';
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$menu-dropdown-background-color: $white !default;
$menu-dropdown-min-width: 170px !default;
$menu-dropdown-padding: gap(1.5) !default;
$menu-dropdown-item-margin: 0 0 gap(.5) !default;
$menu-dropdown-link-color: $primary-text-color !default;
$menu-dropdown-link-hover: $secondary-color;

// Transition Variables: property name | duration | timing function | delay
$menu-dropdown-transition: 200ms all .2s ease, visibility 0s linear .01s, z-index 0s linear 0s !default;



// ------------------------------
// Menu -- Dropdown
// ------------------------------
.menu--dropdown {
    @include bp(laptop) {
        .menu__list {
            align-items: flex-end;
        }
    }
}

.menu__item--dropdown {
    transition: $anim;
    position: static;

    .menu__link {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include bp(laptop) {
            display: inline-block;
        }
    }

    .icon {
        transition: $anim;
    }

    .dropdown__icon {
        @include font-size(16);
        @include line-height(16);
        position: relative;
        display: inline-block;
        margin-left: gap(.3);
        transform: rotate(0);

        @include bp(laptop) {
            transform: none;
        }

        .icon {
            transform-origin: center;
            transform: rotate(0deg);
            transition: $anim;
        }
    }

    // For mobile, turn the dropdown into a toggle
    &.js--is-open {
        .dropdown {
            display: block;
            opacity: 1;
            z-index: 1;
            pointer-events: all;
            position: relative;
            transform: translateX(0%);
        }

        .dropdown__icon {
            transform: rotate(180deg);

            &::after {
                visibility: hidden;
            }
        }
    }

    // Exclude dropdown hover to large screens
    @include bp(laptop) {
        &.js--is-open {
            background-color: $menu-dropdown-background-color;

            .menu__link {
                color: $menu-dropdown-link-color;
                background-color: $background-color;
            }

            .dropdown {
                display: block;
                opacity: 1;
                transform: translateY(0%);
                pointer-events: all;
                position: absolute;
                z-index: 1;
            }
        }
    }
}


// ------------------------------
// Dropdown Styles
// ------------------------------
.dropdown {
    display: none;
    position: absolute;
    opacity: 0;
    z-index: -1;
    pointer-events: none;
    padding: gap(1.5) gap(2.5);

    @include bp(laptop) {
        background-color: $menu-dropdown-background-color;
        min-width: $menu-dropdown-min-width;
        border: $border--light;
        position: absolute;
        top: 100%;
        padding: $menu-dropdown-padding;
        transform: none;

        // Transition animation
        opacity: 0;
        transition: $menu-dropdown-transition;
        pointer-events: none;
        z-index: -1;
        box-shadow: 5px 6px 9px #0000001a;
    }
}

.dropdown__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.dropdown__item {
    margin: 0 0 gap(2);

    @include bp(laptop) {
         margin: $menu-dropdown-item-margin;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.dropdown__link {
    @include font-primary;
    @include font-size(16);
    @include line-height(26);
    text-decoration: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    line-height: 1;


    &.dropdown__link--active {
        font-weight: 700;
        color: $menu-dropdown-link-hover;
    }

    @include bp(laptop) {
        color: $menu-dropdown-link-color;

        &:hover {
            font-weight: 700;
            color: $menu-dropdown-link-hover;
            text-decoration: underline;
        }
    }
}
