// ----------------------------------------------------------------------------
// Tabs Section
// ----------------------------------------------------------------------------
//
// Mobile - Collapsible tab block used to reveal extra product information when the
// user clicks an item's header or expand button.
//
// Desktop - Tabs
//
// Related JS file: tabs/tabs.js
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$tab-horizontal-button-padding: gap(3.5) gap(2.5) gap(1.5);

$tab-horizontal-arrow-background: $secondary-color;
$tab-horizontal-arrow-border-radius: 65px !default;
$tab-horizontal-arrow-color: $white;
$tab-horizontal-arrow-padding: 0;
$tab-horizontal-arrow-height-width: 24px;
$tab-horizontal-arrow-text-size: 11;
$tab-horizontal-arrow-bottom-align: 20%;

$tab-accordion-transition: $anim !default;
$tab-accordion-title-color: $primary-text-color !default;
$tab-accordion-box-shadow: 0 !default;
$tab-accordion-title-background-color: transparent !default;
$tab-accordion-title-padding-mobile: gap(1.5) 0 !default;
$tab-accordion-content-background-color: transparent !default;
$tab-accordion-content-padding: 0 !default;
$tab-accordion-active-content-padding: 0 0 gap(2.5) !default;
$tab-accordion-active-content-max-height: 5000px !default;


// -------------------------------------
// Horizontal styles
// -------------------------------------

.tabs--horizontal {
    text-align: center;

    .swiper--tabs {
        width: 100%;
    }

    .swiper-wrapper {
        list-style-type: none;
        padding: 0;
        margin: 0 auto;

        &.swiper-wrapper--tabs {
            position: relative;
        }
    }

    .swiper-navigation {
        display: none;

        @include bp(laptop) {
            position: absolute;
            top: -55px;
            display: flex;
            gap: 25px;
            right: $grid-gutter;
        }
    }

    .swiper-arrow {
        background: transparent;
        border: 0;
        border-radius: 0;
        color: $primary-text-color;
        height: 20px;
        width: 20px;

        &:after {
            display: none;
        }

        &.swiper-button-disabled {
            color: $disabled-color;
            opacity: 1;
        }

        &.swiper-button-next,
        &.swiper-button-prev {
            left: initial;
            right: initial;
            position: initial;
            display: block;
            margin: 0;

            .icon {
                position: relative;
                left: 1px;
                font-size: 20px;
            }
        }
    }

    .swiper-button-prev--tabs, .swiper-button-next--tabs {
        display: none;
    }

    .swiper-initialized {
        .swiper-button-prev--tabs, .swiper-button-next--tabs {
            display: flex;
        }

        .swiper-button-disabled {
            display: none;
        }
    }

    .swiper-controllers {
        padding-top: gap(3);
    }

    .swiper-scrollbar {
        background: $grey;

        .swiper-scrollbar-drag {
            background-color: $black;
        }

        &.swiper-scrollbar-horizontal {
            height: 2px;
            left: 0;
            width: calc(100% - $grid-gutter--mobile);

            @include bp(laptop) {
                width: calc(100% - $grid-gutter);
            }
        }
    }

    .tab__list--horizontal {
        @include bp(tablet) {
            display: grid;
            grid-auto-flow: dense;
            gap: 15px;

            &.grid-width-1 {
                grid-template-columns: 1fr;
            }

            &.grid-width-2 {
                grid-template-columns: repeat(2, 1fr);
            }

            &.grid-width-3 {
                grid-template-columns: repeat(3, 1fr);
            }
        }

        .tab__button--horizontal {
            display: block;
            position: relative;
            align-items: flex-start;
            color: $dark-grey;
            background: transparent;
            border-radius: 0;
            padding: 0;

            @include font-size(24);
            @include line-height(30);
            font-weight: 300;
            cursor: pointer;
            white-space: normal;
            text-align: left;
            text-transform: none;

            @include bp(laptop) {
                @include font-size(34);
                @include line-height(42);
            }

            &.is-open {
                color: $primary-text-color;
                font-weight: 300;
            }
        }
    }

    .tab__content--horizontal {
        display: none;
        grid-column: -1 / 1;
        -ms-grid-column: 1;
        margin-left: 0;
        margin-right: 0;
        width: 100%;
        padding: gap(3) 0 gap(5);
        text-align: left;
        opacity: 0;

        p {
            &:first-of-type {
                margin-top: 0;
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }

        &.is-open {
            display: block;
            opacity: 1;
            transition-delay: 200ms;
            transition: opacity 400ms ease;
            border-bottom: 0;
        }
    }

    .tab-content__wrapper {
        position: relative;
    }
}

.page-section--tabs-collection {
    grid-column: main-start / full-end;
}

.tabs-wrapper--collection-list {
    // margin-left: $grid-gutter--mobile;

    // @include bp(laptop) {
    //     margin-left: $grid-gutter;
    // }

    // @include bp(desktopXL) {
    //     margin-left: 0;
    // }

    .tabs--horizontal {
        .swiper--collection-tab {
            margin: 0;
        }
    }

    .tab__list--horizontal {

        .tab__button--horizontal {
            &:hover {
                color: $black;
                text-decoration: underline;
            }
        }
    }

    .tab__list-container--horizontal {
        width: 250px;
        margin-left: 0;

        @include bp(tablet) {
            width: 300px;
            margin-left: 0;
        }

        @include bp(laptop) {
            width: 300px;
            margin-left: 0;
        }
    }

    .swiper--collection-tab {
        width: 100%;


        @include bp(laptop) {
            margin: 0;
        }

        .swiper-slide {
            height: 100%;
        }
    }
}


// -------------------------------------
// Accordion Styles
// -------------------------------------

.tabs--accordion {
    width: 100%;
    border-top: $border--light;

    .tab__item--accordion {
        border-bottom: $border--light;

        &.is-open {
            box-shadow: $tab-accordion-box-shadow;
        }

        @include bp(laptop) {
            &:hover {
                border-color: $secondary-color;

                .tab__button {
                    color: $secondary-color;
                }
            }
        }
    }

    .tab__title--accordion {
        margin: 0;

        .tab__button--accordion {
            font-size: 1em;
            background-color: $tab-accordion-title-background-color;
            box-shadow: $tab-accordion-box-shadow;
            color: $tab-accordion-title-color;
            cursor: pointer;
            overflow: hidden;
            position: relative;
            text-align: left;
            text-transform: none;
            transition: $tab-accordion-transition;
            padding: $tab-accordion-title-padding-mobile;
            width: 100%;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            white-space: normal;

            &.is-open {
                box-shadow: none;

                .tab__icon--close {
                    display: block;
                }

                .tab__icon--open {
                    display: none;
                }
            }
        }
    }

    .tab__icon--accordion {
        display: inline-block;
        pointer-events: none;
        transition: $tab-accordion-transition;
        margin-left: gap(2);

        .icon {
            width: 27px;
            height: 27px;
        }

        .tab__icon--close {
            display: none;

        }

        .tab__icon--open {
            display: block;
        }
    }

    .tab__content--accordion {
        @include clearfix;
        background-color: $tab-accordion-content-background-color;
        max-height: 0;
        display: block;
        overflow: hidden;
        padding: $tab-accordion-content-padding;
        text-align: left;
        transition: $tab-accordion-transition;

        &.is-open {
            max-height: $tab-accordion-active-content-max-height;
            padding: $tab-accordion-active-content-padding;
            transition: $tab-accordion-transition;
        }

        p:last-child {
            margin-bottom: 0;
        }
    }

    @include bp(tablet) {
        display: none;
    }
}
