// ----------------------------------------------------------------------------
// Pagination
// ----------------------------------------------------------------------------
//
// List of paginated page numbers
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------


// -------------------------------------
// Default
// -------------------------------------

.page-section--pagination {
    margin-top: gap(3);

    @include bp(laptop) {
        margin-top: gap(6);
    }
}

.pagination__list {
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0;
    padding-top: gap(3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

.pagination__item {
    display: inline-block;
    text-decoration: none;
    background: transparent;
    border: 0;
    color: $primary-text-color;
    display: block;
    //padding: gap(1);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    font-weight: 300;

    &:hover,
    &:focus-visible,
    &:active {
        color: $secondary-color;
    }

    .icon {
        display: block;
        width: 25px;
        height: 25px;
    }

    & + .pagination__item {
        margin-left: gap(.5);
    }
}

.pagination__text {
    margin: 0 gap(2);
}

.pagination__item--disabled {
    opacity: .5;
    pointer-events: none;
}

.pagination__item--current {
    border-bottom: 1px solid $primary-text-color;
}


