// ----------------------------------------------------------------------------
// Collection Driver
// ----------------------------------------------------------------------------
//
// A component that allows for text over an image, with optional content and
// link behaviour. A background image overlay is also optionally included.
// Sits on the collection page and is tied to main-content--collection section.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

// Styles
@import 'init';


// -------------------------------------
// Collection Driver
// -------------------------------------

// -------------------------------------
// Variables
// -------------------------------------
$collection-driver-hover-transition: $anim !default;
$collection-driver-overlay-color: rgba($black, .3) !default;
$collection-driver-overlay-color-hover: rgba($black, .4) !default;


// -------------------------------------
// Banner Block -- Default
// -------------------------------------
.page-section--collection-driver {
    grid-column: span 2;

    @include bp(tablet) {
        grid-column: span 1;
    }

    .collection-driver {
        width: 100%;
        height: 100%;

        .collection-driver__link {
            height: 100%;
            display: block;
            text-decoration: none;
        }

        .background-media {
            height: 100%;
            grid-template-columns: [gutter-start] 25px [content] 1fr 25px [gutter-end];
            grid-template-rows: [image-start] 25px [content] auto 25px [image-end];
            justify-items: stretch;
        }

        .background-media__content {
            display: flex;
            grid-column: content;
            grid-row: content;
            align-self: end;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            @include bp(laptop) {
                @include font-size(25);
                @include line-height(30);
            }
        }

        .collection-driver__title {
            margin: 0;
            margin-bottom: gap(2);
            color: $white;
            font-size: 2.2rem;

            @include bp(laptop) {
                font-size: 3.2rem;
            }
        }
        .collection-driver__content {
            @include font-default;
             color: $white;

             ul li{
                color: $white;
             }
        }

        .button {
            margin-top: gap(2);
            width: 100%;
        }

        .collection-driver__content + .button {
            margin-top: $grid-gutter--mobile;

            @include bp(laptop) {
                margin-top: $grid-gutter;
            }
        }

    }
}





// -------------------------------------
// Collection Driver -- Overlay
// -------------------------------------

.collection-driver--with-overlay {
    .background-media__overlay {
        transition: $collection-driver-hover-transition;
        background: $collection-driver-overlay-color;
    }

    &:hover {
        .background-media__overlay {
            background: $collection-driver-overlay-color-hover;
        }
    }

}
