// ------------------------------------------------------------------------------
// Featured Collection Block
// ------------------------------------------------------------------------------
//
// This is a section that displays featured collection items and optional button
//
// ------------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Carousel -- Featured Collection
// -------------------------------------
$carousel-arrow-background: transparent !default;
$carousel-arrow-border-radius: 65px !default;
$carousel-arrow-color: $primary-text-color;
$carousel-arrow-padding: 0;


.page-section--feature-collection {
    overflow-x: hidden;
    overflow-x: clip;
}

.featured-collection{
    position: relative;

    .featured-collection__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: gap(2);

        .swiper-controllers {
            display: none;
            gap: 34px;
        }

        .featured-collection__title {
            margin: 0;
            // Mobile h3 and Desktop h2
            @include responsive-fontsize(24, 30, 44, 55);
        }

        @include bp(laptop) {
            margin-bottom: gap(2);

            .swiper-controllers {
                display: flex;
            }
        }

    }

    .carousel--featured-collection {
        overflow: visible;

        .swiper-wrapper {
            position: relative;
            padding: 0;
            list-style-type: none;

            @include bp(laptop) {
                &:hover {
                    transform: translateY(-50%) scale(1.05);
                    transform-origin: center;
                }
            }
        }
    }

    .swiper-arrow {
        position: static;
        margin-top: 0;
        background: $carousel-arrow-background;
        border: 0;
        border-radius: $carousel-arrow-border-radius;
        color: $carousel-arrow-color;
        padding: $carousel-arrow-padding;
        height: 36px;
        width: 36px;

        display: flex;
        align-items: center;
        justify-content: center;

        @include bp(laptop) {
            @include font-size(24);
        }

        &:after {
            display: none;
        }

        &.swiper-button-prev {
            border-radius: $carousel-arrow-border-radius;

            .icon {
                position: relative;
                left: 1px;
            }
        }

        &.swiper-button-lock {
            display: none;
        }
    }

    .swiper-scrollbar {
        @include swiper-scrollbar;
    }

    .featured-collection__buttons {
        margin-top: gap(4);
        text-align: center;
    }
}
