// ----------------------------------------------------------------------------
// Button -- Transparent Pattern
// ----------------------------------------------------------------------------
//
// Buttons with transparent background that fill on hover; include this SCSS
// file in your project to bring in an additional button style, like so:
//
// @import 'components/elements/links/button/button';
// @import 'components/elements/links/button/button--transparent';
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Button Colors & Styles
// -------------------------------------
.button--primary {
    &.button--transparent {
        background-color: rgba($secondary-color, 0);
        border-color: $primary-color;
        color: $primary-color;

        &:hover,
        a:hover & {
            background-color: rgba($secondary-color, 1);
            border-color: $secondary-color;
            color: $white;
        }
    }
}

