// Styles
@import 'init';


// -------------------------------------
// Default Elements
// -------------------------------------
.addresses [aria-expanded=true]~div.address__form {
    display: block;
}

.addresses [aria-expanded=false]~div.address__form {
    display: none;
}

.address__form--edit {
	width: 100%;
}
