// ----------------------------------------------------------------------------
//  Location
// ----------------------------------------------------------------------------
//
// Location page styling
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


.store__map-wrapper {
    display: grid;
    grid-template-columns: 5fr 9fr;
    gap: 40px;
    align-items: start;
    padding: 80px 150px;
}

@media(max-width: 768px) {
    .store__map-wrapper {
        grid-template-columns:1fr;
        gap: 24px;
        padding: 24px 20px 48px;
    }
}

.store__map-info {
    position: relative;
    display: grid;
    align-content: start;
    gap: 8px;
    padding-left: 72px;
    border-left: 2px solid ;
}

@media(max-width: 768px) {
    .store__map-info {
        padding-block:24px;
    }
}

.store__map-info-icon {
    position: absolute;
    top: 0;
    left: 36px;
    transform: translateX(-50%);
    height: 30px;
    width: auto;
}

@media(max-width: 768px) {
    .store__map-info-icon {
        top:24px;
    }
}

.store__map {
    aspect-ratio: 785/520;
    width: 100%;
    height: auto;
    display: block;
}

@media(max-width: 768px) {
    .store__map {
        aspect-ratio:1;
    }
}

.store__events-wrapper {
    --side-padding: 60px;
    padding: 80px var(--side-padding);
    display: grid;
    gap: 40px;
    text-align: center;
    background-color: #f4f1ec;
}

@media(max-width: 768px) {
    .store__events-wrapper {
        --side-padding: 20px;
        padding: 48px 0;
        gap: 24px;
    }
}

.store__events-header {
    display: grid;
    gap: 8px;
}

@media(max-width: 768px) {
    .store__events-header,.store__events-wrapper .Carousel_Container {
        padding-inline:20px;
    }
}

.store__events {
    --item-side-padding: 12px;
    --items-per-view: 3;
    white-space: nowrap;
    width: calc(100vw - (var(--side-padding) * 2));
    margin-inline:calc(var(--item-side-padding) * -1);
}

@media(max-width: 768px) {
    .store__events {
        --item-side-padding: 4px;
        --items-per-view: 1.05;
    }
}

.store__event {
    width: calc(100% / var(--items-per-view));
    display: inline-block;
    padding-inline:var(--item-side-padding);
}

.store-event-item {
    display: grid;
    gap: 16px;
    text-align: left;
}

.store-event-item__image {
    aspect-ratio: 420/320;
    width: 100%;
    height: auto;
    display: block;
}

.store-event-item__store {
    color: $primary-color;
}

.store-event-item__copy {
    text-wrap: wrap;
    display: grid;
    gap: 8px;
}

.store-event-item__title {
    text-transform: uppercase;
    letter-spacing: 2.4px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.store-event-item__date {
    color: #797a7e;
}

.store__form {
    margin-inline:auto;max-width: 50rem;
    padding: 80px 60px;
    text-align: center;
    display: grid;
    gap: 24px;
}

@media(max-width: 768px) {
    .store__form {
        padding:48px 20px;
    }
}

.store__form-header {
    display: grid;
    gap: 16px;
}

.store__map-info-schedule-item {
    margin: 0;
}
