// ----------------------------------------------------------------------------
// Content Cards
// ----------------------------------------------------------------------------
//
// This is a section that displays a default card consisting of image,
//title, content and button. It displays as either a 1, 2 or 3 column grid.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Content Cards -- Default
// -------------------------------------
.page-section--content-card--wrapper {
    grid-column: main-start / full-end;

    @include bp(tablet) {
        grid-column: main;
    }
}

.page-section--content-cards {
    overflow-x: hidden;
    overflow-x: clip;
}
.content-cards {
    position: relative;
    .content-cards__header-wrapper {

        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: gap(3);
        }
    }

    .content-cards__header {
        margin: 0;
    }

    .card--default {
        display: inline-flex;
        flex-direction: column;
    }
    .card--default .card__image-wrapper {
        height: unset;
        aspect-ratio: 1;
    }

    .card__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex-grow: 1;
        padding: gap(2.7);
        background: $white;

        .card__title {
            margin-top: 0;
            margin-bottom: gap(1.5);
        }

        .card__copy {
            flex-grow: 1;
        }
    }

    .card__link {
        margin-top: 15px;
        padding: 0;
    }

    .swiper-wrapper {
        padding: 0;
        margin: 0;

        @include bp(tablet) {
            &.css-grid--medium-5, &.css-grid--medium-6 {
                flex-wrap: wrap;

                .card {
                    width: calc((100% / 3) - 10px);
                }

            }
        }

        .swiper-slide {
            list-style-type: none;
            height: auto;
        }
    }

    .swiper-scrollbar {
        position: relative;
        bottom: auto;
        width: 100%;
        background-color: #6E6E6E;
        height: 1px;
        border-radius: 0;
        left: 0;
        margin-top: 0;
    }

    .swiper-scrollbar-drag {
        height: 2px;
        top: -1px;
        background-color: $primary-text-color;
    }

    .carousel {
        margin-bottom: gap(2);
    }

    .carousel.swiper-initialized {
        overflow: visible;
        .swiper-wrapper.css-grid {
            grid-gap: 0;
        }
    }
}



// -------------------------------------
// Content Cards -- Icon
// -------------------------------------

.content-cards--icons {
    .content-cards__content {
        @include bp(laptop) {
            @include container--wysiwyg;
            margin-left: 0;
        }
    }
}
