// ----------------------------------------------------------------------------
// Product Guarantees
// ----------------------------------------------------------------------------
//
// A list of icon and title that triggers a modal
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';

.page-section--guarantee {
    display: flex;
    background-color: $background-color;
    padding: gap(3.2) 0;
    margin: 0;
    grid-column: full;

    @include bp(laptop) {
        padding: gap(.8) 0;
        margin: 0;
    }
}

// -------------------------------------
// Guarantee List
// -------------------------------------
.guarantees {
    @include container;
    background-color: transparent;
    border-radius: 0;
    white-space: normal;
    text-align: left;
}

.guarantee__list {
    padding: 0;
    margin: 0;
    max-width: 100%;

    @include bp(laptop) {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        grid-gap: gap(2);
    }
}

.guarantee__item {
    display: grid;
    grid-template-columns: 25px auto;
    grid-gap: 15px;
    align-items: center;
    max-width: 100%;
    color: $primary-text-color;
    margin-bottom: gap(2);

    @include bp(laptop) {
        margin-bottom: 0;
    }

    &:last-of-type {
        margin-bottom: 0;
    }
}

.guarantee__icon {
    width: 25px;
    min-width: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

// -------------------------------------
// Guarantee Modal
// -------------------------------------
.modal--guarantees {
    .modal__content {
        flex-direction: column;
        padding: gap(2);
        max-height: 90vh;
        overflow: scroll;
    }

    .button__icon {
        background-color: transparent;
        padding: 10px;
        top: 5px;
        right: 5px;
    }

    .guarantees-modal__header {
        margin: 0 gap(2) gap(2) 0;
    }
}
