// ----------------------------------------------------------------------------
// Footer
// ----------------------------------------------------------------------------
//
// Lives at the bottom of every page to provide secondary navigation options.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------
$footer-background-color: $background-color !default;
$footer-background-padding: gap(8) 0 !default;
$footer-content-margin: 0 0 $grid-gutter !default;
$footer-text-color: $primary-text-color;

// -------------------------------------
// Footer Defaults
// -------------------------------------

.footer__content {
    display: inline-block;
    width: 100%;

    @include bp(laptop) {
        width: auto;
    }
}

// -------------------------------------
// Footer -- Default
// -------------------------------------
.footer--default {
    background-color: $footer-background-color;

    // Main footer
    .footer__container {
        @include bp(laptop) {

            @include container;
            display: grid;
            max-width: 1820px;
            grid-template-columns: 435px 10% 1fr;
            grid-template-rows: auto;
            padding: gap(6) 0 gap(8);
        }
    }

    .footer__content--newsletter {
        grid-column: 1;
        grid-row: 1;
    }

    .footer__content--menu {
        grid-column: 3;
        grid-row: 1;
        padding: gap(4) gap(1.5) 0;

        @include bp(laptop) {
            padding: 0;
            gap: 25px;
            margin-right: 68px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
        }

        .menu__heading {
            @include font-header;
            @include font-size(20);
            @include line-height(24);
            padding: 0 0 gap(1);

            @include bp(laptop) {
                @include font-default;
                @include font-size(24);
                @include line-height(30);
                padding: 0 0 gap(1.5);
            }
        }
    }

    .menu__item {
        .menu__link {
            @include font-size(14);
            @include line-height(22.5);
            font-weight: 300;
            color: $footer-text-color;
            padding: 0;
            margin: 0;
            display: inline-block;

            @include bp(laptop) {
                @include font-size(16);
                @include line-height(24);
                margin: 0;
                padding: 0;
                padding-bottom: 15px;


            }

            &:hover {
                background-color: transparent;
                text-decoration: underline;
            }
        }

        &:last-child {
            .menu__link {
                padding-bottom: 0;
            }
        }
    }



    .footer__icon {
        display:none;
        @include bp(laptop) {
            display: block;
            .icon {
                width: 27.275px;
                height: 50px;
            }
        }
    }


    .footer__heading {
        margin-bottom: gap(2);
        .footer__title {
            @include font-navigation;
            @include responsive-fontsize(18, 22.5, 24, 30);
            color: $footer-text-color;
            margin: 0 0 gap(0.5);

            @include bp(laptop) {
                font-weight: 300;
            }
        }

        .footer__description {
            @include font-size(14);
            @include line-height(22.5);
        }
    }

    .footer-menu--mobile {
        display: block;
        margin-bottom: gap(2);
        padding-bottom: gap(2);
        border-bottom: 1px solid $border-color--light;

        @include bp(laptop) {
            display: none;
        }

        &:last-child {
            margin-bottom: 0;
            padding-bottom: 0;
            border-bottom: 0;
        }

        .menu__list {
            margin-top: 10px;
            gap: 10px;
        }
    }
}





// -------------------------------------
// Footer -- Bottom
// -------------------------------------
.footer__bottom {
    @include container;
    padding: gap(5) 0 gap(4);

    @include bp(laptop) {
        padding: gap(8) 0 gap(4);
        border-top: 1px solid $border-color--light;
        max-width: 1820px;
    }

    .footer__logo {
        margin-bottom: gap(4);
        .svg {
            width: 100%;
        }

        @include bp(laptop) {
            margin-bottom: gap(6);
        }
    }

    .footer__utility {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 12px;
        align-items: center;
    }

    .legal__container {
        padding: 0;
        margin: 0;

        @include bp(tablet) {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }

        @include bp(laptop) {
            align-items: center;
            flex-flow: row nowrap;
            padding: 0;
            margin: 0;
        }



        .legal__content {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;
            gap: 10px 0;

            @include bp(tablet) {
                display: flex;
                align-items: center;
            }

            .menu--legal {
                margin-bottom: 0;

                @include bp(laptop) {
                    margin-left: 15px;
                }


                .menu__list {
                    display: flex;
                    position: relative;
                    padding-right: 15px;
                    align-items: center;

                    @include bp(laptop) {
                        padding-right: 0;
                    }
                }

                .menu__item {
                    display: flex;
                    padding-left: 15px;
                    align-items: center;

                    &:after {
                        content: "|";
                        margin-left: 15px;
                    }

                    &:first-child {
                        padding-left: 0;
                    }

                    @include bp(laptop) {
                        &:first-child {
                            &:before {
                                content: "|";
                                margin-right: 15px;
                            }
                        }
                    }
                }

                .menu__link {
                    margin: 0;
                    @include font-size(12);
                    @include line-height(22);
                    padding: 0;


                    &:hover {
                        text-decoration: underline;
                    }
                    @include bp(tablet) {
                        padding: 0;
                        margin: 0;
                    }



                    .menu__title {
                        @include font-size(12);
                        @include line-height(22);

                    }
                }
            }
        }



        .footer__copyright, .footer__copyright--secondary {
            margin: 0;
            padding-right: 15px;
            @include font-size(12);
            @include line-height(22);
            color: $footer-text-color;
            font-weight: 400;
            @include bp(laptop) {
                padding-left: 0;
                padding-right: 0;
            }
        }

        .footer__copyright {
            width: 100%;

            @include bp(laptop) {
                width: auto;
            }
        }

        .footer__copyright--secondary {
            display: flex;
            @include bp(laptop) {
                padding-left: 15px;
            }
        }

        .footer__copyright {
            display: flex;
        }
    }







    .footer__social.footer__social--mobile {
        display: block;
        margin-bottom: gap(5);
        width: 100%;
        max-width: 320px;

        @include bp(laptop) {
            display: none;
        }
    }

    .footer__social.footer__social--desktop {
        display: none;

        @include bp(laptop) {
            display: block;
            width: 100%;
            max-width: 320px;
        }
    }

    .menu--social {
        margin-bottom: gap(0);

        .menu__list {
            flex-direction: row;
            justify-content: space-between;
        }

        .menu__item {
            width: 20px;
            height: 20px;
        }
        .menu__link {
            margin: 0;
            padding:0;
            font-size: 20px;
            line-height: 0;
        }
    }
}

// -------------------------------------
// Footer Accordion
// -------------------------------------
// ------------------------------------------------------
// Accordion - On mobile only (No accordion on Desktop)
// ------------------------------------------------------

.accordion--footer.accordion--mobile {
    border: 0;

    &.accordion--footer {

        .accordion__button,
        .menu__heading {
            @include font-header;
            @include font-size(18);
            @include line-height(22.5);
            padding: 0;
        }
    }

    .accordion__item {

        @include bp(laptop) {

            &:hover {

                .accordion__button {
                }
            }
        }
    }

    .accordion__button {
        display: flex;
        align-items: center;

        @include bp(laptop) {
            pointer-events: none;
            padding: 0 0 gap(1);

            .accordion__icon {
                display: none;
            }
        }

        .accordion__icon {

            .icon {
                width: 25px;
                height: 25px;
                margin-right: gap(1);
            }
        }
    }

    .accordion__item {
        box-shadow: none;
        border: 0;
    }

    .accordion__content {
        margin: 0 0 gap(1.5);
        height: auto !important;

        @include bp(laptop) {
            display: flex;
            height: auto;
            overflow: initial;
            margin: 0;
        }

        &[hidden] {
            display: none;
        }
    }
}

// -------------------------------------
// Footer -- Newsletter
// -------------------------------------

// Newsletter footer
$newsletter-footer-text-color: $primary-text-color;

.footer__content--newsletter {
    padding: gap(4) gap(1.5);
    background: $background-color--light;

    @include bp(laptop) {
        padding: 0;
        background: none;
        display: flex;
        gap: 28.72px;
    }
    .form--newsletter {


        .form__error {
            color: $white;
            background-color: $error-color;
            padding: 0 15px;
        }
    }

    .newsletter__wrapper {
        display: flex;
        background: $white;
        padding-left: gap(0.954);
        align-items: center;
    }

    .form__field--email, .form__field--button {
        margin-bottom: 0;
        float: unset;
        width: unset;
    }

    .form__field--email {
        width: 100%;
    }

    .newsletter__input {
        color: $newsletter-footer-text-color;
        padding: 0;
        height: 22px;

        &:focus {
            outline: $focus-state;
        }

        &::placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &:-moz-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &:-ms-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &::-ms-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }

        &::-webkit-input-placeholder {
            color: rgba($newsletter-footer-text-color, .7);
        }
    }

    .form__field--button {
        .newsletter__submit {
            @include font-size(12);
            width: 45px;
            height: 45px;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            color: $newsletter-footer-text-color;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
                color: $newsletter-footer-text-color;
            }
        }
    }
}

// -------------------------------------
// Footer -- Navigation
// -------------------------------------
.footer-menu.footer-menu--desktop {
    display: none;

    @include bp(laptop) {
        display: flex;
        flex-direction: column;
    }


    .menu__list {
        display: flex;
        flex-direction: column;
    }
}
