// ----------------------------------------------------------------------------
// Model Card Grid
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.model-product-grid {
    margin: gap(3) 0;
}

.model-product-grid__title {
    @include font-size(18);
    @include line-height(28);
    font-weight: 400;
    margin: 0 0 gap(2);
}

.model-product-grid__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
}

// Model Product Card
.card--model-product {
    text-decoration: none;

    .wishlist-button {
        position: absolute;
        top: 5px;
        right: 5px;
    }

    &:hover {
        color: $primary-text-color;
    }

    .card__title {
        @include font-size(14);
        @include line-height(18);
        font-weight: 400;
        margin: gap(0.8) 0 0;
    }

    .card__price {
        @include font-size(14);
        @include line-height(18);
        font-weight: 300;
        margin: gap(0.8) 0 0;
    }
}
