// ----------------------------------------------------------------------------
// Typography
// ----------------------------------------------------------------------------
//
// Contains all site-wide typography styles. All base level HTML typographical
// elements are styled here first, with mixins and variables to help with
// granular includes at the component level.
//
// ----------------------------------------------------------------------------

@font-face {
    font-family: "RiformaLLWeb";
    src:  url("https://cdn.shopify.com/s/files/1/0604/9120/2756/files/RiformaLLWeb-Regular.woff2?v=1709754028") format("woff2"), /* Super Modern Browsers */
        url("https://cdn.shopify.com/s/files/1/0604/9120/2756/files/RiformaLLWeb-Regular.woff?v=1709754028") format("woff");
    font-style: normal;
    font-stretch: normal;
    font-display: swap;
    font-weight: 400;
}

