// -------------------------------------
// Font Family Variables
// -------------------------------------
$font--primary: 'RiformaLLWeb', Helvetica, Arial, sans-serif;

// -------------------------------------
// Typography Mixins
// -------------------------------------
@mixin responsive-fontsize($m-fontsize, $m-lineheight, $fontsize, $lineheight) {
    @include font-size($m-fontsize);
    @include line-height($m-lineheight);

    @include bp(tablet) {
      @include font-size($fontsize);
      @include line-height($lineheight);
    }
}

// Default font mixin
// This makes no assumption about sizes
@mixin font-default {
    font-family: $font--primary;
    font-style: normal;
    font-weight: 300;
    line-height: 1.625;
}

// Header font mixin
// This makes no assumption about sizes
@mixin font-header {
    font-family: $font--primary;
    font-style: normal;
    line-height: 1.2;
}

// Default bold font
@mixin font-medium {
    font-family: $font--primary;
    font-style: normal;
    font-weight: 700;
}


// Paragraphs/Default text mixin
// This is used for main element styles; includes sizes and colour
@mixin font-primary {
    @include font-default;
    @include font-size(16);
    @include line-height(24);
    color: $primary-text-color;
}

// Caption text mixin
// This is used for caption text styles; includes sizes and colour
@mixin font-caption {
    @include font-default;
    @include font-size(14);
    @include line-height(22);
    font-weight: 300;
}

// Font size for tags
@mixin font-tags {
    @include font-default;
    @include font-size(14);
    @include line-height(24);
    font-weight: 400;
}

// Font Navigation Menu
@mixin font-navigation {
    @include font-header;
    @include font-size(15);
    @include line-height(24);
    font-weight: 400;

    @include bp(laptop) {
        @include font-size(15);
    }

    @include bp(desktop) {
        @include font-size(15);
    }
}

// Jumbo Font text mixin
// This is used for homepage hero element styles; includes sizes
@mixin font-jumbo {
    @include font-header;
    @include responsive-fontsize(32, 36, 75, 89);
}
