// ----------------------------------------------------------------------------
// Article
// ----------------------------------------------------------------------------
//
// A page template which displays blog article content.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


.page-section--article {
    .container {
        width: 100%;

        @include bp(tablet) {
            @include container--wysiwyg;
        }
    }

    .article__feature-image {
        height: 400px;
        overflow: hidden;

        img {
            object-fit: cover;
            object-position: center center;
            height: 100%;
            width: 100%;


        }
    }
}

