// ----------------------------------------------------------------------------
// Product Driver - Custom Styles
// ----------------------------------------------------------------------------
//
// A section with a flexible grid layout based on the number of product cards.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------
@import 'init';

// -------------------------------------
// Variables
// -------------------------------------

.product-driver__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: row;
    gap: gap(1.5);

    @include bp(laptop) {
        grid-template-columns: repeat(12, 1fr);
    }
}

.product-driver__grid--2 {
    .product-driver__grid-item {
        @include bp(laptop) {
            grid-column: span 6;
        }
    }
}

.product-driver__grid--3 {
    .product-driver__grid-item {
        @include bp(laptop) {
            grid-column: span 4;
        }
    }

    .product-driver__grid-item--3 {
        grid-column: span 2;

        @include bp(laptop) {
            grid-column: span 4;
        }
    }
}

.product-driver__grid--4 {
    .product-driver__grid-item {

        @include bp(laptop) {
            grid-column: span 3;
        }
    }
}

.product-driver__grid--5 {
    grid-template-rows: auto auto;

    .product-driver__grid-item {
        @include bp(laptop) {
            grid-column: span 3;
        }
    }

    .product-driver__grid-item--1 {
        grid-column: span 2;

        @include bp(laptop) {
            grid-column: span 6;
            grid-row: span 2;

            .card--product {
                height: 100%;
                display: grid;
                grid-template-rows: auto 1fr auto;
            }
        }
    }
}
