// ----------------------------------------------------------------------------
// Banner Block
// ----------------------------------------------------------------------------
//
// A component that allows for text over an image, with optional content and
// link behaviour. A background image overlay is also optionally included.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$banner-block-hover-transition: $anim !default;
$button-hover-transition: color .2s ease, background-color .2s ease;
$banner-block-overlay-color: rgba($black, .3) !default;
$banner-block-overlay-color-hover: rgba($black, .4) !default;


// -------------------------------------
// Banner Block -- Default
// -------------------------------------
.banner-block {
    width: 100%;

    .background-media {
        display: block;

        @include bp(laptop) {
            display: grid;
            grid-template-columns: repeat(24, 1fr);
            grid-template-rows: [image-start] 50px [content] auto 50px [image-end];
        }

        .background-media__background {
            grid-column: 1 / span 24;

            @include bp(laptop) {
                position: relative;
                width: 100%;
            }
        }
    }

    .background-media__content {
        grid-column: content;
        grid-row: content;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: gap(2) $grid-gutter--mobile gap(4);

        @include bp(laptop) {
            grid-column-start: 1;
            grid-column-end: 12;
            grid-row: content;
            justify-content: flex-end;
            padding: 0 0 0 $grid-gutter;
        }
    }

    &.already-visible.is-animated {
        .banner-block__link {
            .button {
                transition: $button-hover-transition;
            }
        }
    }
}

.banner-block__title {
    margin: 0;
    color: $black;
    font-size: 2.2rem;

    @include bp(laptop) {
        font-size: 3.2rem;
    }
}

.banner-block__content {
    @include font-default;
    color: $black;

    ul li{
       color: $black;
    }
}

.banner-block__content + .button {
    margin-top: gap(2);
    padding: 0;

    @include bp(laptop) {
        margin-top: gap(1);
    }
}


// -------------------------------------
// Banner Block -- Default
// -------------------------------------
.page-section--banner-block {
    .banner-block__title {
        margin-bottom: gap(2);

        @include bp(laptop) {
            margin-bottom: gap(1);
        }
    }
}


// -------------------------------------
// Banner Block -- Split
// -------------------------------------
.page-section--split-block {
    grid-column: main;

    .banner-block__split-container {
        background-color: $tertiary-color;

        @include bp(tablet) {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            grid-template-rows: 60px minmax(380px, 1fr) 60px;
        }
    }

    .background-media {
        display: unset;
        grid-row: 1 / 4;

        &--left {
            grid-column: 4 / span 2;
        }

        &--right {
            grid-column: 1 / span 2;
        }

        .background-media__overlay { z-index: 3;}

        .background-media__background {
            z-index: 2;
            position: unset;
        }

        .background-image {
            background-color: transparent;
            object-position: top center;
        }
    }

    .background-media__content {
        grid-row: 2 / 3;
        justify-items: center;
        align-self: center;
        padding: gap(6) gap(2);

        @include bp(tablet) {
            padding: gap(2) gap(6);
        }

        &--left {
            grid-column: 1 / span 3;
        }

        &--right {
            grid-column: 3 / span 3;
        }
    }

    .banner-block__title {
        margin-bottom: 10px;
    }
}
