// ----------------------------------------------------------------------------
// Shopify Review Styles
// ----------------------------------------------------------------------------
//
// Override styles for shopify review app
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Review Page Section
// -------------------------------------

.page-section--shopify-reviews {
    .spr-header-title {
        //@extend %h2;
    }

    .spr-container {
        padding: 0;
        border: 0;
    }

    .spr-form-input-text,
    .spr-form-input-email,
    .spr-form-input-textarea {
        @include font-default;
        @include font-size(14);
        @include line-height(16);
        color: $primary-color;
        padding: gap(1.7) gap(1.5);

        &:focus-visible {
            border-color: $primary-color;
        }
    }

    .spr-form-label {
        @include font-size(16);
        @include line-height(19);
        display: block;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .spr-form-review-rating a {
        color: $primary-color;
    }

    .spr-button {
        @include font-medium;
        @include font-size(16);
        @include line-height(20);

        letter-spacing: 0.050em;
        padding: 14px 44px 13px;
        border-radius: 25px;
        background-color: $primary-color;
        color: $white;
    }

    .spr-starrating,
    .spr-starratings {
        color: $primary-color;
    }

    .spr-summary-caption {
        @include font-size(16);
        @include line-height(26);
    }

    .spr-review-header-title {
        @include responsive-fontsize(18, 28, 20, 34);
        @include font-default;
    }

    .spr-form-title {
        @include font-header;
        @include responsive-fontsize(18, 20, 24, 29);
    }

    .spr-review-content-body {
        @include font-primary;
    }

    .spr-summary-actions-newreview {
        margin-top: 10px;
        display: block;

        @include bp(tablet) {
            display: inline-block;
            margin-top: 0;
        }
    }
}

// -------------------------------------
// Review Badge Link
// -------------------------------------
.spr-badge {
    .spr-starrating {
        font-size: 16px;
    }

    .spr-badge-caption {
        font-size: 16px;
        margin-left: 10px;
    }
}

.shopify-product-reviews-badge__link {
    color: $primary-color;
    display: inline-block;

    &:hover {
        color: rgba($primary-color, .7);
    }
}


