// ----------------------------------------------------------------------------
// Header
// ----------------------------------------------------------------------------
//
// Basic site navigation, including a logo, main menu, and search.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

@import 'init';


// -------------------------------------
// Variables
// -------------------------------------

$header-background-color: transparent !default;
$header-logo-height-mobile: 17px !default;
$header-logo-height-desktop: 20px !default;
//$z-index--header: 10000;
$header-overlay-color: rgba(0, 0, 0, .7);

:root {
    --header-text-color: $black;
}

// -------------------------------------
// Header
// -------------------------------------

.shopify-section--header {
    position: sticky;
    top: 0;
    z-index: $z-index--header;

    & + main {
        margin-top: -80px;
    }
}

.header {
    background-color: $header-background-color;
    position: relative;
    z-index: $z-index--header;

    .localization-forms {
        display: flex;
    }

    .disclosure__button {
        //color: black;
        display: flex;
        line-height: normal;
    }

    &.header--background {
        background-color: $background-color--base;
    }
}

.template-index {
    .header--white:not(.header--background):not(.js--is-open) {
        --header-text-color: #fff;

        .header__logo--white {
            display: block;
        }

        .header__logo {
            display: none;
        }
    }
}


.header__container {
    @include container;
    max-width: 1820px;

}

.header__link--logo {
    height: $header-logo-height-mobile;
    vertical-align: baseline;
    width: auto;
    margin-right: gap(1.5);

    @include bp(laptop) {
        height: $header-logo-height-desktop;
    }

    img {
        height: 100%;
        width: auto;
        aspect-ratio: attr(width) / attr(height);
    }


}

.header__logo--white {
    display: none;
}

.header__link--menu {
    @include hamburger(22px, 22px, 2px);
    color: var(--header-text-color);
    background: transparent;
    padding: 0;

    @include bp(laptop) {
        display: none;
    }
}


// -------------------------------------
// Header -- Default
// -------------------------------------

.header--default {
    .header__container {
        align-items: center;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        padding: 28px 0;

        @include bp(laptop) {
            display: grid;
            grid-template-columns: minmax(180px, auto) 1fr;
            padding: 0;
        }
    }

    .header__link--logo {
        flex: none;
    }

    .header__link--menu {
        flex: none;
    }

    .header__mobile-links {
        display: flex;
        align-items: center;
        gap: 22px;

        @include bp(laptop) {
            display: none;
        }
    }

    .menu__item--customer {
        display: flex;
        align-items: center;

        .menu__link {
            display: flex;
            align-items: center;
        }

        .menu__title {
            display: inline-block;
            padding-left: 10px;

            @include bp(laptop) {
                display: none;
            }
        }
    }

    .menu__item--wishlist {
        .menu__link {
            display: flex;
            align-items: center;
            color: var(--header-text-color);
        }

        .menu__title {
            display: inline-block;
            padding-left: 10px;

            @include bp(laptop) {
                display: none;
            }
        }
    }

    .header__link--cart {
        @include font-default;
        text-decoration: none;

        .menu__title {
            display: flex;
            align-items: center;
            @include font-default;
            color: var(--header-text-color);

            position: relative;
            display: flex;
            width: 20px;
            align-items: center;
            justify-content: center;
        }

        .icon__cart {
            position: absolute;
            width: 22px;
            height: 22px;
            font-size: 20px;
            top: -6px;
        }

        .cart__item-count {
            font-size: 8px;
        }
    }

    .menu {
        &.js--is-open {
            display: block;
            flex: 0 1 100%;
        }

        @include bp(laptop) {
            display: flex;
            flex: 0 1 auto;
        }
    }

    &.js--is-open {
        &::after {
            background-color: $header-overlay-color;
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100vh;
            z-index: 100;
        }
    }
}

.header__nav-wrapper {
    display: none;
    width: 100vw;
    height: calc(100vh - 70px);
    margin: 0 gap(-1.5);
    padding: 0;
    background-color: $header-background-color;
    overflow-x: hidden;
    overflow-y: hidden;

    .js--is-open & {
        display: block;
        z-index: $z-index--header - 1;
    }

    @include bpgd(tablet) {
        height: calc(100vh - 80px);
        overflow-x: hidden;
        margin: 30px gap(-1.5) 0;

        .js--is-open & {
            background-color: $background-color--base;
            overflow-y: auto;
        }
    }

    @include bp(laptop) {
        display: grid;
        grid-template-columns: 1fr auto;
        align-items: center;
        justify-content: space-between;
        position: initial;
        top: 0;
        width: auto;
        height: initial;
        max-height: calc(100vh - 70px);
        margin: 0;
        padding: 0;
        border: 0;
        overflow-x: visible;
        overflow-y: visible;
    }
}

#search-overlay {
    background: black;
    opacity: 0.6;
    position: fixed;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
}
