// ----------------------------------------------------------------------------
// Base Variables
// ----------------------------------------------------------------------------
//
// Stores all global variables for reference into individual components.
//
// ----------------------------------------------------------------------------

// -------------------------------------
// Dependencies
// -------------------------------------

// SCSS Helpers
@use "sass:math";


// -------------------------------------
//  Colours
// -------------------------------------
// Universal Colors
$black:                    #000;
$white:                    #fff;
$light-grey:               #ECECED;
$grey:                     #D1D3D4;
$dark-grey:                #6E6E6E;


// Theme Colors
// If you are having trouble coming up with names, check out
// http://www.color-blindness.com/color-name-hue/

// Shopimota Buzz
$navy:                      #1C2646;
$shoreline:                 #F1F2EF;
$sand:                      #ECEAE4;
$stone:                     #ECECED;
$sky-blue:                  #1F62B5;
$milk-white:                #E0DDD1;


// Alert Styles
$red:                      #C31818;
$green:                    #448A10;
$yellow:                   #ffcc44;


// Theme Mapping
// - Try to avoid declaring colours directly in your Sass !default;
//   instead, use one of these variables
$primary-color:            $navy;
$secondary-color:          $sky-blue;
$tertiary-color:           $shoreline;
$disabled-color:           $grey;

$primary-text-color:       $black;
$secondary-text-color:     $primary-color;
$highlight-text-color:     $secondary-color;

$background-color:         $sand;
$background-color--base:   $shoreline;
$background-color--white:  $white;
$background-color--light:  $milk-white;

$border-color:             $black;
$border-color--light:      $grey;

$caption-text-color:       $primary-color;
$caption-background-color: $light-grey;

$sale-color:               $red;


// For Alerts and Validations
$success-color:            $green;
$error-color:              $red;
$warning-color:            $yellow;

// Focus State Colors
$input-focus:              $border-color;
$focus-color:              $navy;

// -------------------------------------
//  Gradients
// -------------------------------------
$gradient:                 linear-gradient(to right, rgba($black, .1) 0%, rgba($black, .3) 100%) !default;

//--------------------------------------
// Standard Colour Palette
// -------------------------------------
$color-palette: (
    primary: (
        color:     $primary-color,
        contrast:  $white
    ),
    secondary: (
        color:     $secondary-color,
        contrast:  $white
    ),
    white: (
        color:     $white,
        contrast:  $primary-text-color
    )
);


// Button Colour Palette
// -------------------------------------
$button-color--disabled: (
    color:     $disabled-color,
    contrast:  rgba($navy, .25)
);


// Button Sizes
// -------------------------------------
$button-sizes: (
    small: (
        font-size: 14,
        padding:   5px
    ),
    medium:         (
        font-size: 16,
        padding:   15px 44px
    ),
    large:          (
        font-size: 18,
        padding:   10px 20px
    ),
) !default;


// -------------------------------------
//  Media Query Breakpoints
// -------------------------------------
$container--tablet:            768px !default;
$container--laptop:            1024px !default;
$container--desktop:           1280px !default;
$container--desktop-xl:        1920px !default;

// -------------------------------------
//  Base Margin & Padding Gap
// -------------------------------------
$gap:                          10px !default;

$grid-gap:                     15px !default;

$grid-gutter:                  50px !default;
$grid-gutter--mobile:          15px !default;


// -------------------------------------
//  Default Transitions
//  property name | duration | timing function | delay
// -------------------------------------
$anim:                         all .2s ease !default;
$anim--slow:                   all .3s ease !default;


// -------------------------------------
//  Borders and Text Effects
// -------------------------------------
$border-radius:                3px !default;
$border:                       1px solid $border-color !default;
$border--light:                1px solid $border-color--light !default;

$background-overlay-color:     rgba($black, .2) !default;

$box-shadow:                   5px 5px 15px 0 rgba(0, 60, 121, .1), 5px 5px 15px 0 rgba($black, .1) !default;

$text-shadow:                  0 1px 3px rgba($black, .3) !default;

$focus-width:                  2px;
$focus-style:                  solid;
$focus-state:                  $focus-width $focus-style $focus-color !default;


// -------------------------------------
//  Z-index
// -------------------------------------
$z-index--base:                100 !default;
$z-index--base-overlay:        300 !default;
$z-index--base-controls:       500 !default;
$z-index--base-content:        700 !default;
$z-index--header:              900000000 !default;
$z-index--nav:                 10000000000 !default;
$z-index--tooltip:             16000000000 !default;
$z-index--lightbox:            50000000000 !default;


//--------------------------------------
// Shopify Theme Variables (to integrate)
// -------------------------------------

// Text colors
$color-body-text:              var(--color-body-text);

// Background colors
$color-body:                   var(--color-main-background);

// Placeholders
$placeholder-color-foreground: #161d25;
$placeholder-color-background: #f9fafb;

// Other colors
$color-accent:                 var(--color-accent);
$color-border:                 var(--color-border);

// Helper colors for form error states
$color-disabled:               #000000;
$color-disabled-border:        #000000;
$color-error:                  $error-color;
$color-error-bg:               #ffffff;
$color-success:                #000000;
$color-success-bg:             #000000;

// Typography
$font-heading:                 var(--font-heading);
$font-body:                    var(--font-body);
$font-body-weight:             var(--font-body-weight);
$font-body-style:              var(--font-body-style);
$font-body-bold-weight:        var(--font-body-bold-weight);

// Transitions
$transition-duration:          250ms;
$transition-timing:            ease-in-out;

// Grid
$grid-medium:                  46.85em;
$grid-large:                   61.85em;
$grid-widescreen:              87.5em;
$grid-max-width:               73.75em;
//$grid-gutter:                  30px;

// Breakpoints used with the media-query mixin
// $small:                        'small';
// $medium:                       'medium';
// $medium-down:                  'medium-down';
// $medium-up:                    'medium-up';
// $large:                        'large';
// $large-down:                   'large-down';
// $large-up:                     'large-up';
// $widescreen:                   'widescreen';

// $breakpoints: (
//     $small       '(max-width: #{$grid-medium - 1})',
//     $medium      '(min-width: #{$grid-medium}) and (max-width: #{$grid-large - 1})',
//     $medium-down '(max-width: #{$grid-large - 1})',
//     $medium-up   '(min-width: #{$grid-medium})',
//     $large       '(min-width: #{$grid-large}) and (max-width: #{$grid-widescreen - 1})',
//     $large-down  '(max-width: #{$grid-widescreen - 1})',
//     $large-up    '(min-width: #{$grid-large})',
//     $widescreen  '(min-width: #{$grid-widescreen})',
// );

$max-width:                  $container--desktop-xl !default;
$max-width__content:         calc((#{$container--desktop} * .75) - (math.div($grid-gutter, 2))) !default;
$max-width__aside:           calc((#{$container--desktop} * .25) - (math.div($grid-gutter, 2))) !default;
$max-width__content--legacy: calc(75% - math.div($grid-gutter, 2)) !default;
$max-width__aside--legacy:   calc(25% - math.div($grid-gutter, 2)) !default;
