// ----------------------------------------------------------------------------
// Background Image -- Overlay
// ----------------------------------------------------------------------------
//
// Container component that allows an overlay and image to be placed as the background of
// many components in our library.
//
// ----------------------------------------------------------------------------


// -------------------------------------
// Dependencies
// -------------------------------------

// Styles
@import 'init';


// -------------------------------------
// Variables
// -------------------------------------
$background-image-content-color-with-overlay: $white !default;
$background-image-overlay: $background-overlay-color !default;

// -------------------------------------
// Background Image
// -------------------------------------
.background-image {
    &.background-image--with-overlay::after {
        background: $background-image-overlay;
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: $z-index--base-overlay;
        transition: $anim;
    }
}

.background-image__content {
    .background-image--with-overlay & {
        color: $background-image-content-color-with-overlay;
    }
}
