// Styles
@import 'init';


.modal--size-guide {
    .modal__close-button {
        @include font-size(16);
        padding: 15px;
    }
}

.modal__content--size-guide {
    display: block;
    padding: 20px 15px;
    width: 550px;
    max-width: 90%;

    @include bp(laptop) {
        padding: 40px;
    }
}

.size-guide {
    width: 100%;
}

.size-guide__title {
    margin-top: 0;
    text-align: center;
}

.tabs--size-guide {
    width: 100%;
    max-width: 1020px;
    margin: 0 auto;

    .tab__list--horizontal {
        .tab__button--horizontal {
            @include font-size(16);
            text-transform: none;
            padding: 5px;
            text-align: center;
            transition: $anim;
            color: $primary-color;
            border-bottom: 1px solid $primary-color;
            align-items: flex-start;
            cursor: pointer;
            position: relative;
            font-weight: 300;

            &:hover {
                color: $border-color;
            }
        }

        .tab__button--horizontal.is-open {
            border-width: 2px;
        }
    }

    .tab-content__wrapper {
        max-width: 100%;
        overflow-x: auto;
    }

    table {
        text-align: center;
        min-width: 400px;

        th, td {
            @include font-size(16);
            padding: 5px;
            border: none;
        }

        th {
            font-weight: 400;
        }

        tr:nth-of-type(even) {
            background: $background-color;
        }
    }
}

